<template>
  <ejs-dialog ref="dialogObj" id="dialog" :isModal='isModal' v-bind:visible="isShowing" :header='header'
              :animationSettings='animationSettings' :allowDragging='true' :showCloseIcon='showCloseIcon'
              :target='target' :width='width' :open="dialogOpen" :close="dialogClose">
    <hr style="margin-top: 0px;">
    <form id="formId" class="dlg-form" novalidate="">
      <div v-if="loadingCreate == true" class="sk-fading-circle" style="margin-top: -15px;">
        <div class="sk-circle1 sk-circle"></div>
        <div class="sk-circle2 sk-circle"></div>
        <div class="sk-circle3 sk-circle"></div>
        <div class="sk-circle4 sk-circle"></div>
        <div class="sk-circle5 sk-circle"></div>
        <div class="sk-circle6 sk-circle"></div>
        <div class="sk-circle7 sk-circle"></div>
        <div class="sk-circle8 sk-circle"></div>
        <div class="sk-circle9 sk-circle"></div>
        <div class="sk-circle10 sk-circle"></div>
        <div class="sk-circle11 sk-circle"></div>
        <div class="sk-circle12 sk-circle"></div>
      </div>
      <div :id="blockedForm" class="col-lg-12 e-input-section" style="margin-top: -13px;">
        <div class="content-wrapper">

          <hr style="margin-top: 8px;">
          <div class="row" style="margin-top: -20px;">
            <div class="col-xs-6 col-sm-6 col-lg-5 col-md-12">
              <label class="e-text">Cantidad máxima de controles a realizar</label> <br>
              <ejs-numerictextbox id="cantMaxControl" name="CantMax" class="e-input"
                                  ref="numeric_instance" format="###.###" :value="valueExcepcion" :change="changeValueExcepcion" :step="1" :min="0" :max="maxControlesRecibidos">
              </ejs-numerictextbox>
            </div>
          </div>

          <div class="row">
          </div>
          <div class="row" v-show=false style="margin-top: -20px;">
            <div class="col-xs-6 col-sm-6 col-lg-12 col-md-12">
              <!-- <div class="e-float-input"> -->
              <label class="e-text">Descripcion</label> <br>
              <input id="test" name="Testing" type="text" :value="Yuniet" required>
              <!-- <span class="e-float-line"></span> -->
              <!-- </div> -->
            </div>
          </div>
          <div class="row pull-right">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-6">
              <ejs-button id="primarybtn" :isPrimary="true" @click.native="addRecord">{{btnName}}</ejs-button>
            </div>
          </div>
        </div>
      </div>
    </form>
  </ejs-dialog>
</template>
<script>
  import Vue from 'vue'
  import axios from '../../axios/axios'
  import {ButtonPlugin} from '@syncfusion/ej2-vue-buttons'
  import {DropDownListPlugin} from '@syncfusion/ej2-vue-dropdowns'
  import {DialogPlugin} from '@syncfusion/ej2-vue-popups'
  import {UploaderPlugin, FormValidator} from '@syncfusion/ej2-vue-inputs'
  import {ToastPlugin} from '@syncfusion/ej2-vue-notifications'
  import {employeeData} from '../../datasource.js'
  import {DataManager, Query, Predicate} from '@syncfusion/ej2-data'
  import {ColorPickerPlugin} from "@syncfusion/ej2-vue-inputs";
  import { MultiSelectPlugin } from "@syncfusion/ej2-vue-dropdowns";
  import { RecurrenceEditorPlugin, RecurrenceEditorChangeEventArgs } from "@syncfusion/ej2-vue-schedule";
  import { NumericTextBoxPlugin } from "@syncfusion/ej2-vue-inputs";

  Vue.use(NumericTextBoxPlugin);
  Vue.use(RecurrenceEditorPlugin);
  Vue.use(ColorPickerPlugin);
  Vue.use(MultiSelectPlugin);
  Vue.use(ToastPlugin)
  Vue.use(DialogPlugin)
  Vue.use(DropDownListPlugin)
  Vue.use(ButtonPlugin)
  Vue.use(UploaderPlugin)
  export default Vue.extend({
    data: function () {
      return {
        Yuniet:'',
        valueExcepcion:1,
        maxControlesRecibidos:10,
        disabledCant:false,
        listControladores: [],
        selectedControlador:'',
        urlMeettingType : '/meeting_type/meeting_type/', //url del servidor para los servicios del Tipo de reunión
        urlMeetting : '/meeting/meeting/',
        urlMeettingTypeGroups : '/meeting_type/meeting_type_group/',
        meetingTypeSelect:'',// Tipo de reunión que se seleccionó
//  grupos en los que esta implicado el usuario
        pointFields: {text: 'nombre', value: 'grupoId'},
        queryPoint: new Query().select(['nombre', 'grupoId']),
        labelPoint: 'Seleccione un grupo',
        selectedGroups:'',
//////////// Configuraciones del dilaog
        selectedType: 1,
        header: 'Añadir excepción para un controlador',
        target: document.body,
        showCloseIcon: true,
        width: '600px',
        btnName: 'Guardar',
        animationSettings: {effect: 'Zoom'},
        isModal: true,
        fields: {value: 'Employee'},
        controladorFields: {text: 'name', value: 'id_person'},
        controladorQuery: new Query().select(['name', 'id_person']),
        options: {
          // Initialize the CustomPlacement.
          customPlacement: function (inputElement, errorElement) {
            inputElement.parentNode.appendChild(errorElement)
          },
// ///////////Validaciones del formulario
        },
        frmObj: undefined,
////////////////////////////
        loadingCreate: false,
        blockedForm: '',
////////////////////////////// rango de fecha del editor de recurrencia
        minDate: new Date(new Date().getFullYear(),0,1).toLocaleDateString(),
        maxDate: new Date(new Date().getFullYear(),11,28,).toLocaleDateString(),
      }
    },
    mounted: function () {
      this.frmObj = new FormValidator('#formId', this.options)
      this.frmObj.defaultMessages={
        required: 'Este campo es obligatorio'
      }

    },

    methods: {
      changeControlador: function(args){
        this.selectedControlador= args.value
        this.maxControlesRecibidos=10
        if(args.value!=null && args.value!=undefined && args.value!="") {
          this.disabledCant=true
        }
        else{
          this.disabledCant=false
        }
      },
      changeValueExcepcion: function(args){
        this.valueExcepcion = args.value
      },
      onChange: function (args) {///función del editor de recurrencias
        let outputElement = document.getElementById('rule-output');
        let recObject = document.getElementById('editor');
        var rule = recObject.ej2_instances[0].getRecurrenceRule();
//        (startDate, rule, excludeDate, maximumCount, viewDate)
        var listMeeting= {
          "meeting": []
        }
        var dates= recObject.ej2_instances[0].getRecurrenceDates(new Date(2019,0,1),rule,'',365)
        var formatDate=[]
        for(var i=0 ; i<dates.length; i++ ){
          listMeeting.meeting.push({'id_meeting_type':1 , 'date_planned_completion':new Intl.DateTimeFormat('en-GB').format( new Date(dates[i]))})
          formatDate[i]= new Intl.DateTimeFormat('en-US').format( new Date(dates[i]))
        }
      },
      dialogClose: function () {
        this.listControladores = []
        this.selectedControlador = ''
        this.disabledCant=false
        this.valueExcepcion = 1
        this.$emit('close')
      },

      findControladorEnExcepcion: function(Controlador){
        var listaExcepciones = document.querySelector('#TablaControladores')['ej2_instances'][0].dataSource
        var find = false
        for(var i=0;i<listaExcepciones.length && !find;i++){
          if(!this.isEdit){
            if(Controlador.id_person==listaExcepciones[i].person.id_person){
              find=true
            }
          }
          else{
            var row = this.rowData
            if(Controlador.id_person==listaExcepciones[i].person.id_person && listaExcepciones[i].person.id_person!=row.person.id_person){
              find=true
            }
          }
        }
        return find
      },

      dialogOpen: function () {
        this.frmObj.reset()
        if (!this.isEdit) {
          this.Yuniet = "Esto nada mas sirve para que el form no de el berro"
          this.header = 'Añadir excepción para un controlador'
          this.btnName = 'Guardar'
          this.selectedControlador = ''
          this.disabledCant=false
          var find = false
          for(var i=0;i<this.Controladores.length;i++){
            find = this.findControladorEnExcepcion(this.Controladores[i])
            if(find==false){
              this.listControladores.push(this.Controladores[i])
            }
          }
        } else {
          this.header = 'Editar excepción de un controlador'
          this.btnName = 'Modificar'
          this.valueExcepcion = 1
        }
        this.$refs.dialogObj.show()
      },
      addRecord: function () {
        var grid = document.querySelector('#TablaControladores')['ej2_instances'][0]
        this.Yuniet = "Esto nada mas sirve para que el form no de el berro"
        this.valueExcepcion = this.$refs.numeric_instance.ej2Instances.value
          if (!this.isEdit) {

           if(document.getElementById('cantMaxControl').value==""){
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Advertencia!',
                content: 'Debe seleccionar una cantidad',
                cssClass: 'e-toast-warning',
                icon: 'e-warning toast-icons'
              })
              this.frmObj.reset()
              return
            }

            var jsonTabla =
              {
                "person": {
                  "id_person": this.selectedControlador,
                  "name": document.getElementById('profcontrolador').value
                },
                "cant": document.getElementById('cantMaxControl').value
              }

            grid.dataSource.push(jsonTabla)
            grid.refresh()

            this.frmObj.reset()
            this.blockedForm = ''
            this.loadingCreate = false
            this.$emit('close')
//          this.$refs.dialogObj.hide()
//          this.$emit('close')
            // } else {
          }
          else{
            if(document.getElementById('cantMaxControl').value==""){
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Advertencia!',
                content: 'Debe seleccionar una cantidad',
                cssClass: 'e-toast-warning',
                icon: 'e-warning toast-icons'
              })
              this.frmObj.reset()
              return
            }

            var indexes = []

            for(var i=0;i<grid.getSelectedRecords().length;i++){
              for(var j=0;j<this.Controladores.length;j++){
                if(grid.getSelectedRecords()[i].person.id_person == this.Controladores[j].id_person){
                  indexes.push(j)
                }
              }
            }
            // var jsonTabla =
            //   {
            //     "person": {
            //       "id_person": this.selectedControlador,
            //       "name": document.getElementById('profcontrolador').value
            //     },
            //     "cant": document.getElementById('cantMaxControl').value
            //   }
            for(var i=0;i<indexes.length;i++){
              //grid.dataSource[indexes[i]].person = this.rowData[i].person
              grid.dataSource[indexes[i]].cant = this.valueExcepcion
            }
            //grid.editModule.updateRow(index, jsonTabla)
            // grid.dataSource[index].person = jsonTabla.person
            // grid.dataSource[index].cant = jsonTabla.cant
            grid.refresh()

            var gridReglas = document.querySelector('#TablaReglas')['ej2_instances'][0]
            var findControlador = false
            for(var i=0;i<grid.getSelectedRecords().length;i++){
              for(var j=0;j<gridReglas.dataSource.length;j++){
                if(gridReglas.dataSource[j]!=null){
                  if(grid.getSelectedRecords()[i].person.id_person == gridReglas.dataSource[j].profesorcontrolador.id_person){
                    // findControlador = true
                    //gridReglas.deleteRecord("",gridReglas.dataSource[j])
                    gridReglas.dataSource[j]=null
                  }
                }
              }
            }
            var jsonReglasNuevas = []
            for(var i=0;i<gridReglas.dataSource.length;i++){
              if(gridReglas.dataSource[i]!=null){
                jsonReglasNuevas.push(gridReglas.dataSource[i])
              }
            }
            gridReglas.dataSource = jsonReglasNuevas
            gridReglas.refresh()

            this.frmObj.reset()
            this.blockedForm = ''
            this.loadingCreate = false
            this.$emit('AnadirExcepcion', this.valueExcepcion)
            this.$emit('close')
//          this.$refs.dialogObj.hide()
//          this.$emit('close')
            // } else {
          }


      }
    },
    props: {
      /** Whether the dialog is currently showing */
      showing: {
        type: Boolean,
        required: true,
        default: false
      },
      isEdit: {
        type: Boolean,
        required: true,
        default: false
      },
      rowData: {
        type: Object,
        required: false,
        default: null
      },
      Controladores:{
        type: [],
        required: false,
        default:[]
      }
    },
    computed: {
      isShowing() {
        return this.showing
      },
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }

    }
  })
</script>

<style scoped>
  .content-wrapper {
    width: 100%;
    margin: 0 auto;
    min-width: 185px;
  }

  .content-wrapper div.row {
    padding: 5px 0px;
  }

  .control-label {
    padding: 24px 0px 0px 0px;
    font-size: 12px;
    opacity: 0.54;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }

    .e-input-section .col-xs-6,
    .e-input-section .col-xs-4,
    .e-input-section .col-xs-12 {
      padding: 10px 5px;
      width: 100%;
    }

    .content-wrapper div.row {
      padding: 0px;
    }

    #dialog {
      top: 10px;
      /* height: 100%; */
    }
  }

  .upload-box {
    padding-top: 15px;
  }
</style>



