              }
              if(findReglaEspecifica==false){
                for(var i=0;i<listControladoresTabla.length && !findControlador;i++){
                  if(listControladoresTabla[i].person.id_person==jsonTabla.profesorcontrolador.id_person){
                    findControlador=true
                    cantMaxControlador=listControladoresTabla[i].cant
                  }
                }
                for(var i=0;i<listControladosTabla.length && !findControlado;i++){
                  if(listControladosTabla[i].person.id_person==jsonTabla.profesorcontrolado.id_person){
                    findControlado=true
                    cantMaxControlado=listControladosTabla[i].cant
                  }
                }
                if(findControlado==false){
                  var findAuxiliar = false
                  var ControladoAuxiliar = null
                  for(var i=0;i<this.Controlados.length && !findAuxiliar;i++){
                    if(jsonTabla.profesorcontrolado.id_person==this.Controlados[i].id_person){
                      findAuxiliar = true
                      ControladoAuxiliar = this.Controlados[i]
                    }
                  }
                  if(ControladoAuxiliar.total_hours_plan<parseInt(document.getElementById("taskID2").value.toString())){
                    cantMaxControlado = ControladoAuxiliar.total_hours_plan
                  }
                }
                if((cantControlador+1)>cantMaxControlador){
                  todoOk=false
                  this.$root.$children[0].$refs.toastRef.show({
                    title: 'Advertencia!',
                    content: 'El profesor controlador seleccionado excede la cantidad introducida',
                    cssClass: 'e-toast-warning',
                    icon: 'e-warning toast-icons'
                  })
                }
                if((cantControlado+1)>cantMaxControlado){
                  todoOk=false
                  this.$root.$children[0].$refs.toastRef.show({
                    title: 'Advertencia!',
                    content: 'El profesor controlado seleccionado excede la cantidad introducida',
                    cssClass: 'e-toast-warning',
                    icon: 'e-warning toast-icons'
                  })
                }
              }
            }

            if(todoOk==true){
              grid.dataSource.push(jsonTabla)
              grid.refresh()

              this.selectedControlador=''
              this.selectedControlado=''
              this.disabledCant=false
              this.disableTodo=false
              this.primeraVez=true
              this.frmObj.reset()
              this.blockedForm = ''
              this.loadingCreate = false
              this.$emit('close')
//          this.$refs.dialogObj.hide()
//          this.$emit('close')
              // } else {
            }
            else{
              //this.frmObj.reset()
            }
          }
          else{
             var index = grid.selectedRowIndex

            var gridSinTuplaSeleccionada = {
               "dataSource":[]
            }
            for(var i=0;i<grid.dataSource.length;i++){
              if(i!=index){
                gridSinTuplaSeleccionada.dataSource.push(grid.dataSource[i])
              }
            }

            if(document.getElementById('cantControl2').value=="0"){
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Advertencia!',
                content: 'La cantidad seleccionada tiene que ser mayor que 0',
                cssClass: 'e-toast-warning',
                icon: 'e-warning toast-icons'
              })
              //this.frmObj.reset()
              return
            }

            var ControladorAux = this.findControlador(this.selectedControlador)
            var ControladoAux = this.findControladoCompleto(this.selectedControlado)
            if(ControladorAux.teaching_category<ControladoAux.teaching_category || ControladorAux.id_person==ControladoAux.id_person){
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Advertencia!',
                content: 'El profesor seleccionado no puede realizarle un control al otro',
                cssClass: 'e-toast-warning',
                icon: 'e-warning toast-icons'
              })
              //this.frmObj.reset()
              return
            }

            var todoOk = true
            var cantMaxControlador = document.getElementById("taskID").value
            var cantMaxControlado = document.getElementById("taskID2").value
            var listControladoresTabla = this.listExcepcionesControladores
            var listControladosTabla = this.listExcepcionesControlados
            var findControlador = false
            var findControlado = false
            var findReglaEspecifica = false
            var jsonTabla =
              // {
              //   "person": {
              //     "id_person": this.selectedControlador,
              //     "name": document.getElementById('profcontrolador').value
              //   },
              //   "cant": document.getElementById('cantMaxControl').value
              // }
              {
                "profesorcontrolador":{
                  "id_person": this.selectedControlador,
                  "name": document.getElementById('profcontrolador2').value
                },
                "profesorcontrolado":{
                  "id_person": this.selectedControlado,
                  "name": document.getElementById('profcontrolado2').value
                },
                "cantcontroles": document.getElementById('cantControl2').value,
                "mes": this.selectedMes
              }

            if(jsonTabla.cantcontroles!="" && jsonTabla.mes!=null){
              if(parseInt(jsonTabla.cantcontroles.toString())>1){
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Advertencia!',
                  content: 'Solo es admisible un control por mes',
                  cssClass: 'e-toast-warning',
                  icon: 'e-warning toast-icons'
                })
                //this.frmObj.reset()
                return
              }
              else{
                jsonTabla.cantcontroles = ""
              }
            }

            var cantControlador = 0 //document.querySelector('#TablaReglas')['ej2_instances'][0].dataSource
            var cantControlado = 0
            for(var i=0;i<gridSinTuplaSeleccionada.dataSource.length;i++){
              if(gridSinTuplaSeleccionada.dataSource[i].profesorcontrolador.id_person==this.selectedControlador){
                if(gridSinTuplaSeleccionada.dataSource[i].cantcontroles!=""){
                  cantControlador+=parseInt(gridSinTuplaSeleccionada.dataSource[i].cantcontroles.toString())
                }
                else{
                  cantControlador++
                }
              }
            }
            for(var i=0;i<gridSinTuplaSeleccionada.dataSource.length;i++){
              if(gridSinTuplaSeleccionada.dataSource[i].profesorcontrolado.id_person==this.selectedControlado){
                if(gridSinTuplaSeleccionada.dataSource[i].cantcontroles!=""){
                  cantControlado+=parseInt(gridSinTuplaSeleccionada.dataSource[i].cantcontroles.toString())
                }
                else{
                  cantControlado++
                }
              }
            }

            if(jsonTabla.profesorcontrolador.id_person!="" && jsonTabla.profesorcontrolado.id_person!="" && jsonTabla.cantcontroles!="" && jsonTabla.mes==null){
              for(var i=0;i<listControladoresTabla.length && !findControlador;i++){
                if(listControladoresTabla[i].person.id_person==jsonTabla.profesorcontrolador.id_person){
                  findControlador=true
                  cantMaxControlador=listControladoresTabla[i].cant
                }
              }
              for(var i=0;i<listControladosTabla.length && !findControlado;i++){
                if(listControladosTabla[i].person.id_person==jsonTabla.profesorcontrolado.id_person){
                  findControlado=true
                  cantMaxControlado=listControladosTabla[i].cant
                }
              }
              if(findControlado==false){
                var findAuxiliar = false
                var ControladoAuxiliar = null
                for(var i=0;i<this.Controlados.length && !findAuxiliar;i++){
                  if(jsonTabla.profesorcontrolado.id_person==this.Controlados[i].id_person){
                    findAuxiliar = true
                    ControladoAuxiliar = this.Controlados[i]
                  }
                }
                if(ControladoAuxiliar.total_hours_plan<parseInt(document.getElementById("taskID2").value.toString())){
                  cantMaxControlado = ControladoAuxiliar.total_hours_plan
                }
              }
              if((cantControlador+parseInt(jsonTabla.cantcontroles.toString()))>cantMaxControlador){
                todoOk=false
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Advertencia!',
                  content: 'El profesor controlador seleccionado excede la cantidad introducida',
                  cssClass: 'e-toast-warning',
                  icon: 'e-warning toast-icons'
                })
              }
              if((cantControlado+parseInt(jsonTabla.cantcontroles.toString()))>cantMaxControlado){
                todoOk=false
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Advertencia!',
                  content: 'El profesor controlado seleccionado excede la cantidad introducida',
                  cssClass: 'e-toast-warning',
                  icon: 'e-warning toast-icons'
                })
              }

            }
            else if(jsonTabla.profesorcontrolador.id_person!="" && jsonTabla.profesorcontrolado.id_person!="" && jsonTabla.cantcontroles=="" && jsonTabla.mes==null){
              for(var i=0;i<listControladoresTabla.length && !findControlador;i++){
                if(listControladoresTabla[i].person.id_person==jsonTabla.profesorcontrolador.id_person){
                  findControlador=true
                  cantMaxControlador=listControladoresTabla[i].cant
                }
              }
              for(var i=0;i<listControladosTabla.length && !findControlado;i++){
                if(listControladosTabla[i].person.id_person==jsonTabla.profesorcontrolado.id_person){
                  findControlado=true
                  cantMaxControlado=listControladosTabla[i].cant
                }
              }
              if(findControlado==false){
                var findAuxiliar = false
                var ControladoAuxiliar = null
                for(var i=0;i<this.Controlados.length && !findAuxiliar;i++){
                  if(jsonTabla.profesorcontrolado.id_person==this.Controlados[i].id_person){
                    findAuxiliar = true
                    ControladoAuxiliar = this.Controlados[i]
                  }
                }
                if(ControladoAuxiliar.total_hours_plan<parseInt(document.getElementById("taskID2").value.toString())){
                  cantMaxControlado = ControladoAuxiliar.total_hours_plan
                }
              }
              if((cantControlador+1)>cantMaxControlador){
                todoOk=false
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Advertencia!',
                  content: 'El profesor controlador seleccionado excede la cantidad introducida',
                  cssClass: 'e-toast-warning',
                  icon: 'e-warning toast-icons'
                })
              }
              if((cantControlado+1)>cantMaxControlado){
                todoOk=false
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Advertencia!',
                  content: 'El profesor controlado seleccionado excede la cantidad introducida',
                  cssClass: 'e-toast-warning',
                  icon: 'e-warning toast-icons'
                })
              }
            }
            else if(jsonTabla.profesorcontrolador.id_person!="" && jsonTabla.profesorcontrolado.id_person!="" && jsonTabla.cantcontroles=="" && jsonTabla.mes!=null){
              for(var i=0;i<gridSinTuplaSeleccionada.dataSource.length && !findReglaEspecifica;i++){
                if(gridSinTuplaSeleccionada.dataSource[i].profesorcontrolado.id_person==this.selectedControlado &&
                  gridSinTuplaSeleccionada.dataSource[i].mes==this.selectedMes){
                  findReglaEspecifica=true
                  todoOk=false
                  this.$root.$children[0].$refs.toastRef.show({
                    title: 'Advertencia!',
                    content: 'El profesor controlado seleccionado ya posee un control en el mes seleccionado',
                    cssClass: 'e-toast-warning',
                    icon: 'e-warning toast-icons'
                  })
                }
              }
              if(findReglaEspecifica==false){
                for(var i=0;i<listControladoresTabla.length && !findControlador;i++){
                  if(listControladoresTabla[i].person.id_person==jsonTabla.profesorcontrolador.id_person){
                    findControlador=true
                    cantMaxControlador=listControladoresTabla[i].cant
                  }
                }
                for(var i=0;i<listControladosTabla.length && !findControlado;i++){
                  if(listControladosTabla[i].person.id_person==jsonTabla.profesorcontrolado.id_person){
                    findControlado=true
                    cantMaxControlado=listControladosTabla[i].cant
                  }
                }
                if(findControlado==false){
                  var findAuxiliar = false
                  var ControladoAuxiliar = null
                  for(var i=0;i<this.Controlados.length && !findAuxiliar;i++){
                    if(jsonTabla.profesorcontrolado.id_person==this.Controlados[i].id_person){
                      findAuxiliar = true
                      ControladoAuxiliar = this.Controlados[i]
                    }
                  }
                  if(ControladoAuxiliar.total_hours_plan<parseInt(document.getElementById("taskID2").value.toString())){
                    cantMaxControlado = ControladoAuxiliar.total_hours_plan
                  }
                }
                if((cantControlador+1)>cantMaxControlador){
                  todoOk=false
                  this.$root.$children[0].$refs.toastRef.show({
                    title: 'Advertencia!',
                    content: 'El profesor controlador seleccionado excede la cantidad introducida',
                    cssClass: 'e-toast-warning',
                    icon: 'e-warning toast-icons'
                  })
                }
                if((cantControlado+1)>cantMaxControlado){
                  todoOk=false
                  this.$root.$children[0].$refs.toastRef.show({
                    title: 'Advertencia!',
                    content: 'El profesor controlado seleccionado excede la cantidad introducida',
                    cssClass: 'e-toast-warning',
                    icon: 'e-warning toast-icons'
                  })
                }
              }
            }

            if(todoOk==true){
              // grid.dataSource.push(jsonTabla)
              // grid.refresh()
              grid.dataSource[index].profesorcontrolador = jsonTabla.profesorcontrolador
              grid.dataSource[index].profesorcontrolado = jsonTabla.profesorcontrolado
              grid.dataSource[index].cantcontroles = jsonTabla.cantcontroles
              grid.dataSource[index].mes = jsonTabla.mes
              grid.refresh()

              this.selectedControlador=''
              this.selectedControlado=''
              this.disabledCant=false
              this.disableTodo=false
              this.primeraVez=true
              this.frmObj.reset()
              this.blockedForm = ''
              this.loadingCreate = false
              this.$emit('close')
//          this.$refs.dialogObj.hide()
//          this.$emit('close')
              // } else {
            }
            else{
              //this.frmObj.reset()
            }

          }
        }

      }
    },
    props: {
      /** Whether the dialog is currently showing */
      showing: {
        type: Boolean,
        required: true,
        default: false
      },
      isEdit: {
        type: Boolean,
        required: true,
        default: false
      },
      rowData: {
        type: Object,
        required: false,
        default: null
      },
      Controladores:{
        type: [],
        required: false,
        default:[]
      },
      Controlados:{
        type: [],
        required: false,
        default:[]
      },
      listExcepcionesControladores:{
        type: [],
        required: false,
        default:[]
      },
      listExcepcionesControlados:{
        type: [],
        required: false,
        default:[]
      },

    },
    computed: {
      isShowing() {
        return this.showing
      },
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }

    }
  })
</script>

<style scoped>
  .content-wrapper {
    width: 100%;
    margin: 0 auto;
    min-width: 185px;
  }

  .content-wrapper div.row {
    padding: 5px 0px;
  }

  .control-label {
    padding: 24px 0px 0px 0px;
    font-size: 12px;
    opacity: 0.54;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }

    .e-input-section .col-xs-6,
    .e-input-section .col-xs-4,
    .e-input-section .col-xs-12 {
      padding: 10px 5px;
      width: 100%;
    }

    .content-wrapper div.row {
      padding: 0px;
    }

    #dialog3 {
      top: 10px;
      /* height: 100%; */
    }
  }

  .upload-box {
    padding-top: 15px;
  }
</style>



