<template>
  <ejs-dialog ref="dialogObj" id="dialog" :isModal='isModal' v-bind:visible="isShowing" :header='header'
              :animationSettings='animationSettings' :allowDragging='true' :showCloseIcon='showCloseIcon'
              :target='target' :width='width' :open="dialogOpen" :close="dialogClose">
    <hr style="margin-top: 0px;">
    <form id="formId" class="dlg-form" novalidate="">
      <div v-if="loadingCreate == true" class="sk-fading-circle" style="margin-top: -15px;">
        <div class="sk-circle1 sk-circle"></div>
        <div class="sk-circle2 sk-circle"></div>
        <div class="sk-circle3 sk-circle"></div>
        <div class="sk-circle4 sk-circle"></div>
        <div class="sk-circle5 sk-circle"></div>
        <div class="sk-circle6 sk-circle"></div>
        <div class="sk-circle7 sk-circle"></div>
        <div class="sk-circle8 sk-circle"></div>
        <div class="sk-circle9 sk-circle"></div>
        <div class="sk-circle10 sk-circle"></div>
        <div class="sk-circle11 sk-circle"></div>
        <div class="sk-circle12 sk-circle"></div>
      </div>
      <div :id="blockedForm" class="col-lg-12 e-input-section" style="margin-top: -13px;">
        <div class="content-wrapper">
          <div class="row" v-show="isEdit">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-12">
              <label class="e-text">No.</label>
              <input id="taskID" class="e-input" type="text" disabled>
            </div>
          </div>
          <div class="row" style="    margin-top: -7px;">
            <div class="col-xs-12 col-sm-12 col-lg-8 col-md-12">
              <!-- <div class="e-float-input"> -->
              <!-- <span class="e-float-line"></span> -->
              <label class="e-text">Nombre</label>
              <input id="name" name="Name" class="e-input" type="text" required>
              <!-- </div> -->
            </div>
            <label class="e-text" style="margin-left: 18px;">Color</label>
            <div class="col-xs-12 col-sm-12 col-lg-4 col-md-12">
              <ejs-colorpicker id="color-picker"></ejs-colorpicker>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-12 col-lg-7 col-md-12" style="margin-top: -12px;">
              <label class="e-text">Grupo</label>
              <ejs-multiselect id="group" name="Group" class="e-input" :allowFiltering="true"
                               :dataSource="user.grupos" :fields='pointFields'
                               :change='changeGroup' :query='queryPoint' data-msg-containerid="errorIDPoint"
                               :placeholder='labelPoint' sortOrder='Ascending' required
                               :value="selectedGroups" :ignoreAccent="true"></ejs-multiselect>

              <div id="errorIDPoint"></div>
            </div>
          </div>
          <hr style="margin-top: 8px;">
          <div class="row" style="margin-top: -18px;">
            <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12">
              <ejs-dropdownlist id="profcontrolador" class="e-input" name="Controlador" :allowFiltering="true" :dataSource="this.Controladores"
                                :fields='controladorFields' :query='controladorQuery' data-msg-containerid="errorIDPoint"
                                :ignoreAccent="true" placeholder="Seleccione un controlador" sortOrder='Ascending' multiple
                                filterBarPlaceholder="Ej. Yuniet Artiles" required
                                :change='changeControlador' :value="selectedControlador">
              </ejs-dropdownlist>
            </div>
          </div>

          <div class="row">
          </div>
          <div class="row" style="margin-top: -20px;">
            <div class="col-xs-6 col-sm-6 col-lg-12 col-md-12">
              <!-- <div class="e-float-input"> -->
              <label class="e-text">Descripcion</label> <br>
              <textarea id="description" class="e-input" rows="4"></textarea>
              <!-- <span class="e-float-line"></span> -->
              <!-- </div> -->
            </div>
          </div>
          <div class="row pull-right">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-6">
              <ejs-button id="primarybtn" :isPrimary="true" @click.native="addRecord">{{btnName}}</ejs-button>
            </div>
          </div>
        </div>
      </div>
    </form>
  </ejs-dialog>
</template>
<script>
  import Vue from 'vue'
  import axios from '../../axios/axios'
  import {ButtonPlugin} from '@syncfusion/ej2-vue-buttons'
  import {DropDownListPlugin} from '@syncfusion/ej2-vue-dropdowns'
  import {DialogPlugin} from '@syncfusion/ej2-vue-popups'
  import {UploaderPlugin, FormValidator} from '@syncfusion/ej2-vue-inputs'
  import {ToastPlugin} from '@syncfusion/ej2-vue-notifications'
  import {employeeData} from '../../datasource.js'
  import {DataManager, Query, Predicate} from '@syncfusion/ej2-data'
  import {ColorPickerPlugin} from "@syncfusion/ej2-vue-inputs";
  import { MultiSelectPlugin } from "@syncfusion/ej2-vue-dropdowns"; 
  import { RecurrenceEditorPlugin, RecurrenceEditorChangeEventArgs } from "@syncfusion/ej2-vue-schedule";

  Vue.use(RecurrenceEditorPlugin);
  Vue.use(ColorPickerPlugin);
  Vue.use(MultiSelectPlugin);
  Vue.use(ToastPlugin)
  Vue.use(DialogPlugin)
  Vue.use(DropDownListPlugin)
  Vue.use(ButtonPlugin)
  Vue.use(UploaderPlugin)
  export default Vue.extend({
    data: function () {
      return {
        selectedControlador:'',
        urlMeettingType : '/meeting_type/meeting_type/', //url del servidor para los servicios del Tipo de reunión
        urlMeetting : '/meeting/meeting/',
        urlMeettingTypeGroups : '/meeting_type/meeting_type_group/',
        meetingTypeSelect:'',// Tipo de reunión que se seleccionó
//  grupos en los que esta implicado el usuario
        pointFields: {text: 'nombre', value: 'grupoId'},
        queryPoint: new Query().select(['nombre', 'grupoId']),
        labelPoint: 'Seleccione un grupo',
        selectedGroups:'',
//////////// Configuraciones del dilaog
        selectedType: 1,
        header: 'Añadir nuevo Tipo de reunión',
        target: document.body,
        showCloseIcon: true,
        width: '600px',
        btnName: 'Guardar',
        animationSettings: {effect: 'Zoom'},
        isModal: true,
        fields: {value: 'Employee'},
        controladorFields: {text: 'name', value: 'id_person'},
        controladorQuery: new Query().select(['name', 'id_person']),
        options: {
          // Initialize the CustomPlacement.
          customPlacement: function (inputElement, errorElement) {
            inputElement.parentNode.appendChild(errorElement)
          },
// ///////////Validaciones del formulario
          rules: {
            Name: {required: true},
            Group: {required: true}
          }
        },
        frmObj: undefined,
////////////////////////////
        loadingCreate: false,
        blockedForm: '',
////////////////////////////// rango de fecha del editor de recurrencia
        minDate: new Date(new Date().getFullYear(),0,1).toLocaleDateString(),
        maxDate: new Date(new Date().getFullYear(),11,28,).toLocaleDateString(), 
      }
    },
    mounted: function () {
      this.frmObj = new FormValidator('#formId', this.options)
      this.frmObj.defaultMessages={
        required: 'Este campo es obligatorio'
      }
      let recObject = document.getElementById('editor');
      recObject.ej2_instances[0].selectedType = 1;

    },
   
    methods: {     
      changeGroup: function (args) {
        this.selectedGroups = args.value
      },
      changeControlador: function(args){
        this.selectedControlador= args.value
      },
      onChange: function (args) {///función del editor de recurrencias
        let outputElement = document.getElementById('rule-output');
        let recObject = document.getElementById('editor');
        var rule = recObject.ej2_instances[0].getRecurrenceRule();
//        (startDate, rule, excludeDate, maximumCount, viewDate)
       var listMeeting= {
             "meeting": []
            } 
        var dates= recObject.ej2_instances[0].getRecurrenceDates(new Date(2019,0,1),rule,'',365)
        var formatDate=[]
        for(var i=0 ; i<dates.length; i++ ){
          listMeeting.meeting.push({'id_meeting_type':1 , 'date_planned_completion':new Intl.DateTimeFormat('en-GB').format( new Date(dates[i]))})
          formatDate[i]= new Intl.DateTimeFormat('en-US').format( new Date(dates[i]))
        }
      },
      dialogClose: function () {
        this.$emit('close')
      },
      dialogOpen: function () {
        console.log(this.user)
        let recObject = document.getElementById('editor');
        this.frmObj.reset()
        if (!this.isEdit) {
          this.header = 'Añadir nuevo Tipo de reunión'
          this.btnName = 'Guardar'
          document.getElementById('name').value = ''
          recObject.ej2_instances[0].setRecurrenceRule('')
          document.getElementById('color-picker').value = ''
          document.getElementById('description').value = ''
        } else {
          this.header = 'Editar Tipo de reunión'
          this.btnName = 'Modificar'
          var row = this.rowData
          var meeting=this.meetingTypeSelected[0]
          document.getElementById('taskID').value = meeting.id_meeting_type
          document.getElementById('name').value = meeting.name
          document.getElementById('color-picker').value = meeting.color
          document.getElementById('description').value = meeting.description
          for(var i=0;i<this.user.grupos;i++)
          {
            document.getElementById('group').value=this.user.grupos[i].nombre
          }
          recObject.ej2_instances[0].setRecurrenceRule(meeting.periodicity)

        }
        this.$refs.dialogObj.show()
      },
      addRecord: function () {
        if (this.frmObj.validate()) {
          if (!this.isEdit) {

            this.frmObj.reset()
            this.blockedForm = ''
            this.loadingCreate = false
            this.$emit('close')
//          this.$refs.dialogObj.hide()
//          this.$emit('close')
            // } else {
          }
        }

      },
      notifySuccessCreateMeetingType: function(jsonBodyRequest){
           var userImg = new DataManager(employeeData).executeLocal(new Query().where(new Predicate('id', 'equal', this.$store.state.store_notification.currentUserID)))[0]['ImgSrc']
           var activity = {
                'id': 9,
                'Employee': 'Humberto Díaz Pando',
                'ImgSrc': userImg,
                'TimeStamp': new Date().toLocaleDateString(),
                'Message': 'El Tipo de reunión ' + jsonBodyRequest.name + ' ha sido añadido'
              }
              this.$store.dispatch('addActivity', Object.assign({}, activity)).then(() => {
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Éxito!',
                  content: 'El Tipo de Reunión ' + jsonBodyRequest.name + ' fue añadido correctamente',
                  cssClass: 'e-toast-success',
                  icon: 'e-success toast-icons'
                })
              }).catch((reason) => {
                alert(reason)
              })
              this.blockedForm = ''
              this.loadingCreate = false
              this.$emit('listMeetingType')
              this.$emit('close')
      },
      notifySuccessUpdateMeetingType: function(){
        var userImg = new DataManager(employeeData).executeLocal(new Query().where(new Predicate('id', 'equal', this.$store.state.store_notification.currentUserID)))[0]['ImgSrc']
        var activity = {
                'id': 9,
                'Employee': 'Humberto Díaz Pando',
                'ImgSrc': userImg,
                'TimeStamp': new Date().toLocaleDateString(),
                'Message': 'Ha modificado un Tipo de reunión '
              }
              this.$store.dispatch('addActivity', Object.assign({}, activity)).then(() => {
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Éxito!',
                  content: 'El Tipo de Reunión fue modificado correctamente',
                  cssClass: 'e-toast-success',
                  icon: 'e-success toast-icons'
                })
              }).catch((reason) => {
                alert(reason)
              })
              this.blockedForm = ''
              this.loadingCreate = false
              this.$emit('listMeetingType')
              this.$emit('close')
      },
      notifyErrorMeetingType: function(){
         this.$root.$children[0].$refs.toastRef.show({
                title: 'Error!',
                content: 'Ha ocurrido un error en el servidor, verifique sus datos o espere un momento',
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
              this.blockedForm = ''
              this.loadingCreate = false
              this.$emit('close')
      }
    },
    props: {
      /** Whether the dialog is currently showing */
      showing: {
        type: Boolean,
        required: true,
        default: false
      },
      isEdit: {
        type: Boolean,
        required: true,
        default: false
      },
      rowData: {
        type: Object,
        required: false,
        default: null
      },
      meetingTypeSelected: {
        type: Object,
        required: false,
        default: null
      },
      Controladores:{
        type: [],
        required: false,
        default:[]
      }
    },
    computed: {
      isShowing() {
        return this.showing
      },
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }

    }
  })
</script>

<style scoped>
  .content-wrapper {
    width: 100%;
    margin: 0 auto;
    min-width: 185px;
  }

  .content-wrapper div.row {
    padding: 5px 0px;
  }

  .control-label {
    padding: 24px 0px 0px 0px;
    font-size: 12px;
    opacity: 0.54;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }

    .e-input-section .col-xs-6,
    .e-input-section .col-xs-4,
    .e-input-section .col-xs-12 {
      padding: 10px 5px;
      width: 100%;
    }

    .content-wrapper div.row {
      padding: 0px;
    }

    #dialog {
      top: 10px;
      /* height: 100%; */
    }
  }

  .upload-box {
    padding-top: 15px;
  }
</style>



