enableRipple(false);
var templateVue = Vue.component("menuTemplate", {
  template: `
    <div class="listWrapper" style="width: inherit;height: inherit;">
        <span :class="[data.icon + ' list_svg']"></span>
        <span class="list_text">{{data.text}} </span>
        <span :class="[data.badge]" v-if="data.messages !== ''" style="float: right;margin-top: 16px;font-size: 12px;margin-right: 5px;">{{data.messages}} New</span>
    </div>
  `,
  data() {
    return {
      data: {}
    };
  }
});
export default Vue.extend({
  data: function() {
    return {
      accountList: [
        { text: "Salir", id: "002", icon: "logout" }
      ],
      dataList: "",
      fields: { iconCss: "icon", tooltip: "text" },
      template: function() {
        return { template: templateVue };
      },
      type: Browser.isDevice ? "Over" : "Push",
      width: "170px",
      name: "App",
      dockSize: "170px",
      enableDock: !Browser.isDevice,
      closeOnDocumentClick: true,
      position: {
        X: "Right"
      },
      showNotifyDlg: false,
      logoutPopup: false,
      logoutPopupClicked: false,
      notifyBellClicked: false,
      sidebarClicked: false,
      contextLeft: "1000px",
      isDevice: Browser.isDevice,
      showBackdrop: false,
      sidebarTarget: Browser.isDevice ? ".content" : document.body,
      sidebarPosition: Browser.isDevice ? "absolute" : "fixed",
      marginSize: Browser.isDevice ? "0px" : "0px",
      backColor: Browser.isDevice
        ? "linear-gradient(-138deg, #3D8EC4 0%, #276AAB 100%)"
        : "#fff",
      bellColor: Browser.isDevice ? "#fff" : "#999999"
      // loader
    };
  },
  created() {
    document.getElementById("mojonera").setAttribute("style","background-color: #e4e8f1")
    // Lista de notificaciones
    this.$store.dispatch("listActivity");
    // Permitir Acciones en dependencia del rol
    if (this.userData.is_jefe_departamento) {
      this.dataList = [
        {
          text: "Seguimiento",
          id: "31",
          messages: "",
          badge: "",
          icon: "sf-icon-Hardware"
        }
      ];
    } else if(this.userData.is_jefe_disciplina) {
      this.dataList = [
        {
          text: "Planificar",
          id: "22",
          messages: "",
          badge: "",
          icon: "sf-icon-Dashboard"
        },
        {
          text: "Planificacion",
          id: "15",
          messages: "",
          badge: "",
          icon: "sf-icon-Hardware"
        },
        {
          text: "Seguimiento",
          id: "31",
          messages: "",
          badge: "",
          icon: "sf-icon-Hardware"
        }
      ];
    }
    else{
      this.dataList = [
        {
          text: "Seguimiento",
          id: "31",
          messages: "",
          badge: "",
          icon: "sf-icon-Hardware"
        }
      ];
    }
  },
  computed: {
    userData() {
      if (this.$store.getters.user) return this.$store.getters.user;
      return false;
    }
  },
  methods: {
    onComplete: function(args) {
      var name = this.$router.currentRoute.name;
      var menuId;
      for (let i = 0; i < args.data.length; i++) {
        if (args.data[i].text === name) {
          menuId = args.data[i].id;
        }
      }
      this.$refs.sidebarListObj.selectItem({ id: menuId });
    },
    updatePendingRequests: function() {
      for (let data of this.dataList) {
        if (data["badge"] !== "") {
          data["messages"] = this.$store.getters.pendingTask;
        }
      }
    },
    // Listview select event handler
    onSelect: function(args) {
      var vuePath = "/";
      if (args.text !== "") {
        vuePath += args.text
          .replace(" ", "")
          .replace("ó", "o")
          .replace(" ", "");
      }
      this.$router.push({ path: vuePath });
      this.$refs.notifybarObj.hide();
    },
    openClick: function() {
      this.$refs.sidebarInstance.toggle();
      this.sidebarClicked = true;
    },
    closeClick: function() {
      this.$refs.sidebarInstance.hide();
    },
    closeMenus: function() {
      if (!this.logoutPopupClicked) {
        this.logoutPopup = false;
      }
      if (!this.notifyBellClicked) {
        this.$refs.notifybarObj.hide();
      }
      if (this.isDevice) {
        if (!this.sidebarClicked) {
          this.$refs.sidebarInstance.hide();
        }
      }
      this.logoutPopupClicked = false;
      this.notifyBellClicked = false;
      this.sidebarClicked = false;
    },
    AccountMenu: function(event) {
      if (event != null) {
        if (event.text == "Salir") {
          // var yy = '<h4 id="yyy" style="" class="ml5">\n' +
          //   '  <span class="text-wrapper">\n' +
          //   '    <span class="line line1"></span>\n' +
          //   '    <span class="letters letters-left">Reuniones</span>\n' +
          //   '    <span class="letters ampersand">&amp;</span>\n' +
          //   '    <span class="letters letters-right" style="color: #ffc502;">Acuerdos</span>\n' +
          //   '    <span class="line line2"></span>\n' +
          //   '  </span>\n' +
          //   '        </h4>'
          // var script = document.createElement('script');
          // script.setAttribute("src","static/anime.min.js")
          this.$store.dispatch("logout", false);
          document.getElementById("sidebar-menu").remove();
          document.getElementById("appId").setAttribute("class", "#app");
          document.getElementById("appId").setAttribute("style" , "");
          document.getElementById("yyy").remove();
          document.getElementById("yy").innerHTML(yy);

        }
      }
      this.logoutPopup = !this.logoutPopup;
      this.logoutPopupClicked = true;
    },
    toggleNotify: function() {
      this.$refs.notifybarObj.toggle();
      this.notifyBellClicked = true;
    }
  },
  components: {
    Notification
  }
});

anime.timeline({loop: true})
  .add({
    targets: '.ml5 .line',
    opacity: [0.5,1],
    scaleX: [0, 1],
    easing: "easeInOutExpo",
    duration: 700
  }).add({
  targets: '.ml5 .line',
  duration: 600,
  easing: "easeOutExpo",
  translateY: function(e, i, l) {
    var offset = -0.625 + 0.625*2*i;
    return offset + "em";
  }
}).add({
  targets: '.ml5 .ampersand',
  opacity: [0,1],
  scaleY: [0.5, 1],
  easing: "easeOutExpo",
  duration: 600,
  offset: '-=600'
}).add({
  targets: '.ml5 .letters-left',
  opacity: [0,1],
  translateX: ["0.5em", 0],
  easing: "easeOutExpo",
  duration: 600,
  offset: '-=300'
}).add({
  targets: '.ml5 .letters-right',
  opacity: [0,1],
  translateX: ["-0.5em", 0],
  easing: "easeOutExpo",
  duration: 600,
  offset: '-=600'
}).add({
  targets: '.ml5',
  opacity: 0,
  duration: 1000,
  easing: "easeOutExpo",
  delay: 1000
});

