<template>
  <ejs-dialog ref="dialogObj" id="dialog" :isModal='isModal' v-bind:visible="isShowing" :header='header'
              :animationSettings='animationSettings' :allowDragging='true' :showCloseIcon='showCloseIcon'
              :target='target' :width='width' :open="dialogOpen" :close="dialogClose">
    <hr style="margin-top: 0px;">
    <form id="formId" class="dlg-form" novalidate="">
      <div class="col-lg-12 e-input-section" style="margin-top: -13px;">
        <div class="content-wrapper">
          <div class="row" v-show="isEdit">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-12">
              <label class="e-text">No.</label>
              <input id="number" class="e-input" type="text" disabled>
            </div>
          </div>
          <div class="row" style="    margin-top: -7px;">
            <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12">
              <label class="e-text">Punto</label>
              <textarea id="point" name="Point" class="e-input" rows="4" required></textarea>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-12" style="margin-top: -12px;">
              <label class="e-text">Responsable</label>
              <ejs-dropdownlist id="responsible" name="Responsible" class="e-input" :allowFiltering="true"
                                :dataSource="membersPoint"  :fields='membersFields' :query='membersQuery'
                                :ignoreAccent="true" placeholder="Seleccione un responsable" filterBarPlaceholder="Ej. Juan Pérez"
                                :beforeOpen="changeMembersPoint" :change='changeResponsible' :value="selectedResponsible">
              </ejs-dropdownlist>
            </div>
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-12" style="margin-top: -12px;">
              <label class="e-text">Faclitador</label> <br>
              <ejs-dropdownlist id="manager" class="e-input" :allowFiltering="true" :dataSource="membersPoint"
                                :fields='membersFields' :query='membersQuery' data-msg-containerid="errorIDPoint"
                                :ignoreAccent="true" placeholder="Seleccione un facilitador" sortOrder='Ascending' multiple
                                filterBarPlaceholder="Ej. Juan Pérez"
                                :beforeOpen="changeMembersPoint"
                                :change='changeFacilitator' :value="selectedFacilitator">


              </ejs-dropdownlist>

            </div>
          </div>
          <hr style="margin-top: 8px;">
          <div class="row" style="margin-top: -18px;">
            <div class="col-xs-12 col-sm-12 col-lg-9 col-md-12">
              <label class="e-text">Invitados</label>
              <ejs-multiselect id="guests" class="e-input" :allowFiltering="true"
                               :dataSource="guestsPoint"  :fields='guestsFields' :ignoreAccent="true"
                               :query='guestsQuery' :change='changeGuest'
                               placeholder="Seleccione invitados"></ejs-multiselect>


            </div>
          </div>
          <hr style="margin-top: 8px;">
          <div class="row" style="margin-top: -20px;">
            <div class="col-xs-6 col-sm-6 col-lg-5 col-md-12">
              <label class="e-text">Duración</label> <br>
              <ejs-numerictextbox id="duration" name="Duration" class="e-input"
                                  ref="numeric_instance" format="###.### min" :value="10" :step="5" :min="5" :max="90">
              </ejs-numerictextbox>
            </div>
          </div>

          <div class="row pull-right">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-6">
              <ejs-button id="primarybtn" :isPrimary="true" @click.native="addRecord">{{btnName}}</ejs-button>
            </div>
          </div>
        </div>
      </div>
    </form>
  </ejs-dialog>
</template>
<script>
  import Vue from 'vue'
  import axios from '../../axios/axios'
  import {ButtonPlugin} from '@syncfusion/ej2-vue-buttons'
  import {DropDownListPlugin} from '@syncfusion/ej2-vue-dropdowns'
  import {DialogPlugin} from '@syncfusion/ej2-vue-popups'
  import {UploaderPlugin, FormValidator} from '@syncfusion/ej2-vue-inputs'
  import {ToastPlugin} from '@syncfusion/ej2-vue-notifications'
  import {employeeData} from '../../datasource.js'
  import {DataManager, Query, Predicate,ODataAdaptor} from '@syncfusion/ej2-data'
  import { MultiSelectPlugin } from "@syncfusion/ej2-vue-dropdowns";
  import { NumericTextBoxPlugin } from "@syncfusion/ej2-vue-inputs";


  Vue.use(NumericTextBoxPlugin);
  Vue.use(MultiSelectPlugin);
  Vue.use(ToastPlugin)
  Vue.use(DialogPlugin)
  Vue.use(DropDownListPlugin)
  Vue.use(ButtonPlugin)
  Vue.use(UploaderPlugin)
  var serverDataMeetingType = new DataManager({
    url: 'http://localhost:81/meeting_system/meeting/web/api/users_ldap',
    adaptor: new ODataAdaptor,
    crossDomain: true
  });
  export default Vue.extend({
    data: function () {
      return {
        /////////////// URLs
        urlAgenda: 'annual_plan/agenda/',
        urlAgenda_Responsible: 'annual_plan/responsible_agenda/',
        urlAgenda_guests: 'annual_plan/agenda_guest/',
///////////////Miembros del grupo seleccionado       
        membersPoint:'',
        membersFields: {text: 'nombre', value: 'userId'},
        membersQuery: new Query().select(['nombre', 'userId']),
        selectedResponsible:'',
        selectedFacilitator:'',
////////////////////////////Invitados: usuarios del LDAP
        guestsPoint: serverDataMeetingType,
        guestsFields: {text: 'nombre', value: 'userId'},
        guestsQuery: new Query().select(['nombre', 'userId']),
        selectedGuests:'',
///////////////////////////// 
        selectedType: 1,
        header: 'Añadir punto al Plan',
        target: document.body,
        showCloseIcon: true,
        width: '500px',
        btnName: 'Guardar',
        animationSettings: {effect: 'None'},
        isModal: true,
        options: {
          // Initialize the CustomPlacement.
          customPlacement: function (inputElement, errorElement) {
            inputElement.parentNode.appendChild(errorElement)
          },
          rules: {
            Point: {required: true},
            Responsible: {required: true},
            Duration: {required: true}
          }
        },
        frmObj: undefined,
      }
    },
    mounted: function () {
      this.frmObj = new FormValidator('#formId', this.options)
    },

    methods: {
      changeResponsible: function(args){
        this.selectedResponsible= args.value
      },
      changeFacilitator: function(args){
        this.selectedFacilitator= args.value
      },
      changeGuest: function (args) {
        this.selectedGuests = args.value
      },
      dialogClose: function () {
        this.$emit('close')
      },
      dialogOpen: function () {
        let recObject = document.getElementById('editor');
        this.frmObj.reset()
        if (!this.isEdit) {
          this.header = 'Añadir punto al Plan'
          this.btnName = 'Añadir'
          document.getElementById('point').value = ''
          document.getElementById('responsible').value = ''
          document.getElementById('guests').value = ''
          document.getElementById('manager').value = ''
          document.getElementById('duration').value = ''
        } else {
          this.header = 'Modificar punto del Plan '
          this.btnName = 'Modificar'
          var row = this.rowData
          document.getElementById('number').value = row['number']
          document.getElementById('point').value = row['title']
          document.getElementById('responsible').value = row['Responsable']
          document.getElementById('guests').value = row['Invitados']
          document.getElementById('manager').value = row['id_facilitator']
          document.getElementById('duration').value = row['duration']

        }
        this.$refs.dialogObj.show()
      },
      addRecord: function () {
        var grid = document.querySelector('.e-grid')['ej2_instances'][0]
        if (this.frmObj.validate()) {
          var id = this.$store.state.store_notification.activityData.length
          var guests= this.selectedGuests
          var listGuests= { "Agenda_guest": [] }
          var jsonBodyRequest = {
            "id_meeting": this.meetingSelected.id_meeting,
            "number": this.lengthPoints + 1,
            "title": document.getElementById('point').value,
            "duration": document.getElementById('duration').value,
            "observation": 'pending',
            "id_facilitator": this.selectedFacilitator
            //Falta el responsable del punto
            //Falta el facilitador del punto
          }
          if (!this.isEdit) {
            var id_responsible= this.selectedResponsible
            this.$emit('loadingBlocked')
            axios.post(this.urlAgenda +'create', jsonBodyRequest).then(response => {
              var id_agenda=response.data.model.id_agenda
              axios.post(this.urlAgenda_Responsible +'create', {
                "id_agenda": id_agenda,
                "id_user_ldap": id_responsible
              }).then(response => {
              })
              for(var k= 0 ; k < guests.length; k++ ){
                listGuests.Agenda_guest.push({'id_agenda':id_agenda , 'id_user_ldap':guests[k] })
              }
              axios.post(this.urlAgenda_guests +'create', listGuests).then(response => {
              })
              this.MessagePoint("Success","creado",response.data.model )
              this.$emit('listAgendaByMeeting')
            }).catch(response => {
              this.MessagePoint("Error","",response.data.model )
            })

          } else {
            var id_agenda= this.rowData['id_agenda']
            this.$emit('loadingBlocked')
            axios.post(this.urlAgenda +'update/'+id_agenda, jsonBodyRequest).then(response => {
              this.MessagePoint("Success","modificado",response.data.model )
              this.$emit('listAgendaByMeeting')
            }).catch(response => {
              this.MessagePoint("Error","",response.data.model)
            })
          }
          this.frmObj.reset()
          this.$refs.dialogObj.hide()
          this.$emit('close')
          // } else {
        }
      },
      MessagePoint: function(type, message, data){
        if(type== "Success"){
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Éxito!',
            content: 'El punto número ' + data.number + ' fue '+message+' correctamente',
            cssClass: 'e-toast-success',
            icon: 'e-success toast-icons'
          })
        }
        if(type== "Error"){
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Advertencia!',
            content: 'Error en el servidor',
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons',
            position:  { X: 'Right'}
          })
        }

      },
      changeMembersPoint: function(){
        this.$store.dispatch('listMembersByUser').then((listPoint) => {
          this.membersPoint=this.$store.getters.getListMembersByUser
        }).catch((reason) => {
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: reason,
            cssClass: 'e-toast-success',
            icon: 'e-success toast-icons'
          })
        });
      },
    },
    props: {
      /** Whether the dialog is currently showing */
      showing: {
        type: Boolean,
        required: true,
        default: false
      },
      meetingSelected: {
        type: Object,
        required: false,
        default: null
      },
      meetingTypeSelected: {
        type: Object,
        required: false,
        default: null
      },
      lengthPoints: {
        type: Object,
        required: false,
        default: null
      },
      isEdit: {
        type: Boolean,
        required: true,
        default: false
      },
      rowData: {
        type: Object,
        required: false,
        default: null
      }
    },
    computed: {
      isShowing() {
        return this.showing
      },
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }
    }
  })
</script>

<style scoped>
  .content-wrapper {
    width: 100%;
    margin: 0 auto;
    min-width: 185px;
  }

  .content-wrapper div.row {
    padding: 5px 0px;
  }

  .control-label {
    padding: 24px 0px 0px 0px;
    font-size: 12px;
    opacity: 0.54;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }

    .e-input-section .col-xs-6,
    .e-input-section .col-xs-4,
    .e-input-section .col-xs-12 {
      padding: 10px 5px;
      width: 100%;
    }

    .content-wrapper div.row {
      padding: 0px;
    }

    #dialog {
      top: 10px;
      /* height: 100%; */
    }
  }

  .upload-box {
    padding-top: 15px;
  }
</style>



