<template>
  <ejs-dialog ref="dialogObj" id="dialog" :isModal='isModal' v-bind:visible="isShowing" :header='header'
              :animationSettings='animationSettings' :allowDragging='true' :showCloseIcon='showCloseIcon'
              :target='target' :width='width' :open="dialogOpen" :close="dialogClose">
    <hr style="margin-top: 0px;">
    <form id="formId" class="dlg-form" novalidate="">
      <div class="col-lg-12 e-input-section" style="margin-top: -13px;">
        <div class="content-wrapper">
          <div class="row" v-show="isEdit">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-12">
              <label class="e-text">No.</label>
              <input id="number" class="e-input" type="text" disabled>
            </div>
          </div>
          <div class="row" style="    margin-top: -7px;">
            <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12">
              <label class="e-text">Punto</label>
              <ejs-dropdownlist id='point' name="Point" :dataSource='point_data' :fields='pointFields'
                                :change='changePiont'    :query='queryPoint' :placeholder='labelPoint'
                                data-msg-containerid="errorIDPoint" sortOrder='Ascending'>
              </ejs-dropdownlist>
              <div id="errorIDPoint"></div>

            </div>
          </div>
          <div class="row" style="    margin-top: -7px;">
            <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12">
              <label class="e-text">Acuerdo</label>
              <textarea id="task" name="Task" class="e-input" rows="4" required></textarea>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-12" style="margin-top: -12px;">
              <label class="e-text">Responsable</label>
              <ejs-dropdownlist id="responsible" name="Responsible" class="e-input" :allowFiltering="true" :dataSource="participansPoint"
                                data-msg-containerid="errorIDResponsible" :ignoreAccent="true" placeholder="Seleccione un responsable" filterBarPlaceholder="Ej. Juan Pérez">
              </ejs-dropdownlist>
              <div id="errorIDResponsible"></div>
            </div>
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-12" style="margin-top: -12px;">
              <label class="e-text">Fecha de cumplimiento</label> <br>
              <ejs-datepicker class="e-input" data-msg-containerid="errorID" :max="maxDate" id="dataCompliance" name="DataCompliance" ></ejs-datepicker>
              <div id="errorID"></div>
            </div>
          </div>
          <hr style="margin-top: 8px;">
          <div class="row" style="margin-top: -18px;">
            <div class="col-xs-12 col-sm-12 col-lg-9 col-md-12">
              <label class="e-text">Participantes</label>
              <ejs-multiselect id="participans" class="e-input" :allowFiltering="true"
                               :dataSource="participansPoint" ::ignoreAccent="true" placeholder="Seleccione participantes"></ejs-multiselect>
            </div>
          </div>
          <hr style="margin-top: 8px;">
          <div class="row" style="margin-top: -20px;">
            <div class="col-xs-6 col-sm-6 col-lg-12 col-md-12">
              <label class="e-text">Marcar como punto</label> <br>
              <ejs-switch id="checked" :checked="false"></ejs-switch>
            </div>
          </div>
          <div class="row" style="margin-top: -7px;">
            <div class="col-xs-6 col-sm-6 col-lg-12 col-md-12">
              <label class="e-text">Observaciones</label> <br>
              <textarea id="obesrvations" class="e-input" rows="4"></textarea>
            </div>
          </div>

          <div class="row pull-right">
            <div class="col-xs-12 col-sm-12 col-lg-6 col-md-6">
              <ejs-button id="primarybtn" :isPrimary="true" @click.native="addRecord">{{btnName}}</ejs-button>
            </div>
          </div>
        </div>
      </div>
    </form>
  </ejs-dialog>
</template>
<script>
  import Vue from 'vue'
  import {ButtonPlugin} from '@syncfusion/ej2-vue-buttons'
  import {DropDownListPlugin} from '@syncfusion/ej2-vue-dropdowns'
  import {DialogPlugin} from '@syncfusion/ej2-vue-popups'
  import {UploaderPlugin, FormValidator} from '@syncfusion/ej2-vue-inputs'
  import {ToastPlugin} from '@syncfusion/ej2-vue-notifications'
  import {employeeData} from '../../datasource.js'
  import {AgendasData} from '../../datasource.js'
  import {DataManager, Query, Predicate} from '@syncfusion/ej2-data'
  import { MultiSelectPlugin } from "@syncfusion/ej2-vue-dropdowns";
  import { DatePickerPlugin } from '@syncfusion/ej2-vue-calendars'
  import {DropDownListComponent} from '@syncfusion/ej2-vue-dropdowns'
  import { SwitchPlugin } from "@syncfusion/ej2-vue-buttons";
  import { rippleMouseHandler } from "@syncfusion/ej2-buttons";


  Vue.use(SwitchPlugin);
  Vue.component(DropDownListComponent)
  Vue.use(DatePickerPlugin)
  Vue.use(MultiSelectPlugin);
  Vue.use(ToastPlugin)
  Vue.use(DialogPlugin)
  Vue.use(DropDownListPlugin)
  Vue.use(ButtonPlugin)
  Vue.use(UploaderPlugin)
  export default Vue.extend({
    data: function () {
      return {
//      DropDownList de los puntos de la reunión
        point_data: AgendasData,
        pointFields: {text: 'Punto', value: 'No'},
        queryPoint: new Query().select(['Punto', 'No']),
        labelPoint: 'Seleccione un punto de la agenda',

//
        participansPoint: [
          {id: '1', text: 'Oscar Leyva Sánchez'},
          {id: '2', text: 'Humberto Díaz Pando'},
          {id: '3', text: 'Roselio Pérez Trujillo'},
          {id: '4', text: 'Yuniet Artiles García'},
          {id: '5', text: 'Dayana Rodriguez Romeu'},
          {id: '5', text: 'Gabriel Suárez Matos'},
          {id: '5', text: 'Carlos el Trenza'},
          {id: '5', text: 'Carlos Brito Neira'},
          {id: '5', text: 'Maylin Moreno Mendez'},
        ],
        selectedType: 1,
        header: 'Añadir acuerdo',
        target: document.body,
        showCloseIcon: true,
        width: '500px',
        btnName: 'Guardar',
        animationSettings: {effect: 'Zoom'},
        isModal: true,
        maxDate: new Date(),
        fields: {value: 'Employee'},
        options: {
          // Initialize the CustomPlacement.
          customPlacement: function (inputElement, errorElement) {
            inputElement.parentNode.appendChild(errorElement)
          },
          rules: {
            Task: {required: true},
            Point: {required: true},
            Responsible: {required: true},
            DataCompliance: { required: true, date: true }
          }
        },
        frmObj: undefined,
        showUploader: false,
        showAssignee: false,
        path: {
          saveUrl: 'https://aspnetmvc.syncfusion.com/services/api/uploadbox/Save',
          removeUrl: 'https://aspnetmvc.syncfusion.com/services/api/uploadbox/Remove'
        },
        dropElement: '.content-wrapper'
      }
    },
    mounted: function () {
      this.frmObj = new FormValidator('#formId', this.options)
    },
    methods: {
      changePiont: function () {
        console.log(document.getElementById('point').item)
      },
      dialogClose: function () {
        this.$emit('close')
      },
      dialogOpen: function () {
        let recObject = document.getElementById('editor');
        this.frmObj.reset()
        if (!this.isEdit) {
          this.header = 'Añadir acuerdo'
          this.btnName = 'Añadir'
          document.getElementById('point').value = ''
          document.getElementById('task').value = ''
          document.getElementById('responsible').value = ''
          document.getElementById('participans').value = ''
          document.getElementById('dataCompliance').value = new Date().toLocaleDateString()
          document.getElementById('obesrvations').value = ''
          document.getElementById('obesrvations').value = ''
        } else {
          this.header = 'Modificar acuerdo'
          this.btnName = 'Modificar'
          var row = this.rowData
          document.getElementById('number').value = row['No']
          document.getElementById('point').value = row['Punto']
          document.getElementById('task').value = row['Acuerdo']
          document.getElementById('responsible').value = row['Responsable']
          document.getElementById('participans').value = row['Invitados']
          document.getElementById('dataCompliance').value = row['Facilitador']
          document.getElementById('obesrvations').value = row['Observaciones']
        }
        this.$refs.dialogObj.show()
      },
      addRecord: function () {
        var grid = document.querySelector('.e-grid')['ej2_instances'][0]
        if (this.frmObj.validate()) {
          var id = this.$store.state.store_notification.activityData.length
          if (!this.isEdit) {
            grid.addRecord({
              'No': grid.dataSource.length + 1,
              'Punto': document.getElementsByName('Point')[0].value,
              'Acuerdo': document.getElementById('task').value,
              'Responsable': document.getElementById('responsible').value,
              'Invitados': document.getElementById('participans').value,
              'FechaCumplimiento': document.getElementById('dataCompliance').value,
              'Observaciones': document.getElementById('obesrvations').value,
            })
            this.$root.$children[0].$refs.toastRef.show({
              title: 'Éxito!',
              content: 'El acuerdo número ' + grid.dataSource.length + ' fue añadido correctamente',
              cssClass: 'e-toast-success',
              icon: 'e-success toast-icons'
            })
          } else {
            var data = {
              'No': +document.getElementById('number').value,
              'Punto': document.getElementsByName('Point')[0].value,
              'Acuerdo': document.getElementById('task').value,
              'Responsable': document.getElementById('responsible').value,
              'Invitados': document.getElementById('participans').value,
              'FechaCumplimiento': document.getElementById('dataCompliance').value,
              'Observaciones': document.getElementById('obesrvations').value,
            }
            var index = grid.selectedRowIndex
            grid.editModule.updateRow(index, data)
            this.$root.$children[0].$refs.toastRef.show({
              title: 'Éxito!',
              content: 'El acuerdo número ' + document.getElementById('number').value + ' fue modificado correctamente',
              cssClass: 'e-toast-success',
              icon: 'e-success toast-icons'
            })
          }
          this.frmObj.reset()
          this.$refs.dialogObj.hide()
          this.$emit('close')
          // } else {
        }
      }
    },
    props: {
      /** Whether the dialog is currently showing */
      showing: {
        type: Boolean,
        required: true,
        default: false
      },
      isEdit: {
        type: Boolean,
        required: true,
        default: false
      },
      rowData: {
        type: Object,
        required: false,
        default: null
      }
    },
    computed: {
      isShowing() {
        return this.showing
      }
    }
  })
</script>

<style scoped>
  .content-wrapper {
    width: 100%;
    margin: 0 auto;
    min-width: 185px;
  }

  .content-wrapper div.row {
    padding: 5px 0px;
  }

  .control-label {
    padding: 24px 0px 0px 0px;
    font-size: 12px;
    opacity: 0.54;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }

    .e-input-section .col-xs-6,
    .e-input-section .col-xs-4,
    .e-input-section .col-xs-12 {
      padding: 10px 5px;
      width: 100%;
    }

    .content-wrapper div.row {
      padding: 0px;
    }

    #dialog {
      top: 10px;
      /* height: 100%; */
    }
  }

  .upload-box {
    padding-top: 15px;
  }
</style>



