import axios from '../../../axios/axios-auth'
import { DataManager, Query, Predicate } from '@syncfusion/ej2-data'

const state = {
    meetingTypeListByUser: [],
    urlMeettingType: '/meeting_type/meeting_type/',
    urlMeetting: '/meeting/meeting/',
    urlMeettingTypeGroups: '/meeting_type/meeting_type_group/',

}
const getters = {
    getMeetingTypeListByUser(state) {
        return state.meetingTypeListByUser
    },

}
const mutations = {
    setMeetingTypeList(state, meetingTypeListByUser) {
        state.meetingTypeListByUser = meetingTypeListByUser
    },
}
const actions = {
    getMeetingTypeListByUser(context) {
        return new Promise((resolve, reject) => {
            var groups = []
                // esto no está bien, hay que pedir los grupos donde el usuario es jefe o planificador, no todos,
                // que es los que se está haciendo
            for (var i = 0; i < context.getters.user.grupos.length; i++) {
                if (context.getters.user.grupos[i].Jefe_de_Grupo.userId == context.getters.user.userId)
                    groups[i] = context.getters.user.grupos[i].grupoId;
            }
            let body = {
                "relations": "all",
                "pagination": 1000,
                "attr": {
                    "id_group": groups
                }
            }
            var listMeetingType = []
            axios.post(context.state.urlMeettingTypeGroups, body).then(response => {
                // this.gridData= response.data
                listMeetingType = response.data.items
                var idLisMeetingType = []
                for (var k = 0; k < listMeetingType.length; k++) {
                    idLisMeetingType[k] = listMeetingType[k].id_meeting_type
                }
                var bodyMeetingType = {
                    "attr": {
                        "id_meeting_type": idLisMeetingType
                    }
                }
                axios.post(context.state.urlMeettingType, bodyMeetingType).then(response => {
                    context.commit('setMeetingTypeList', response.data)
                    resolve(Object.assign({}, response.data))
                }).catch(response => {
                    reject(new Error('Error del servior!'))
                })
            }).catch(response => {
                reject(new Error('Error del servior!'))
            })
        })
    },
}

export default {
    state,
    mutations,
    actions,
    getters
}



