import {
  requestData,
  listData,
  employeeData,
  TaskData
} from '../../../datasource.js'
import { DataManager, Query, Predicate } from '@syncfusion/ej2-data'


const state={
  taskInput: TaskData,

}
const getters={  //
  pendingTask: state => {
    return new DataManager(state.taskInput).executeLocal(new Query().where(new Predicate('Status', 'equal', 'Pending'))).length
  },
}
const mutations={

}
const actions= {
}

export default {
  state,
  mutations,
  actions,
  getters
}



