<template id="view-hardware">
  <div class="container-fluid">
    <div :class="blockedcomponent">

      <div id="dialog" style="padding:10px;position:absolute;">
        <AddControl :showing="dlgShow" :controlSelected="controlSelected" @loadingPage="loadingPage"
                      :isEdit="isEditing" :Controladores="Controladores" :Controlados="Controlados" :rowData="rowData" @close="onDlgClose"
                      @listControls="listControls">
        </AddControl>
      </div>
      <!------------------Header, btn-Add TR y filtrar por rango de fechas los TR --------------------------------->
      <div class="row top-row">
        <div class="col-xs-12 col-sm-12 col-lg-7 col-md-12" style="margin-top: 6px;padding-left: 28px;"><h3>Controles Planificados</h3></div>

        <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6">
          <ejs-button id="primarybtn" class="asset-btn" :isPrimary="true"
                      style="margin-left: 284px;" v-on:click.native="AddBtnClick">
            <span class="e-add-icon"></span>
            Añadir</ejs-button>
        </div>
      </div>
      <!--Lista de los TR---------------------------------------------------------------------------------->
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12" style="margin-top: -17px;">
          <ejs-grid ref="gridObj" class="r-space" :load="onGridLoad" id="ControlesPlanificados"
                    :toolbarClick="editToolClicked" :actionBegin='onActionBegin' :actionComplete='onload'
                    :toolbar="toolbar" :dataSource='gridData' :recordDoubleClick='false'
                    :query='query' :allowSorting='true' :allowSelection='true'
                    :selectionSettings='selectionSettings' :allowFiltering='true' :allowPaging='true'
                    :allowGrouping='false' :pageSettings='pageSettings' :editSettings='editSettings'
                    :filterSettings='filterSettings' :rowSelected='rowSelected'>
            <e-columns>
              <e-column type='checkbox' width='20'></e-column>
              <e-column :visible='false' field='id_control' headerText='' textAlign='Left' width=10 :isPrimaryKey='true'></e-column>
              <e-column field='id_asignatura.asignatura_name' headerText='Asignatura' :filter='filter' width=70 clipMode='EllipsisWithTooltip' :required='true'></e-column>
              <e-column field='fecha' headerText='Fecha' type='date' editType="datepickeredit"  format='yMd' clipMode='EllipsisWithTooltip' width=140 :required='true'></e-column>
              <e-column field='id_person.name' headerText='Controlador' textAlign='Center' width=130 required=true></e-column>
              <e-column field='controlado.name' headerText='Profesor a Controlar' :filter='filter' width=100 required=true></e-column>
            </e-columns>
          </ejs-grid>
        </div>
      </div>
    </div>
    <!--loaders de spinkit-->
    <div v-if="loadingGrid == true" class="sk-fading-circle" style="margin-top: 54px;">
      <div class="sk-circle1 sk-circle"></div>
      <div class="sk-circle2 sk-circle"></div>
      <div class="sk-circle3 sk-circle"></div>
      <div class="sk-circle4 sk-circle"></div>
      <div class="sk-circle5 sk-circle"></div>
      <div class="sk-circle6 sk-circle"></div>
      <div class="sk-circle7 sk-circle"></div>
      <div class="sk-circle8 sk-circle"></div>
      <div class="sk-circle9 sk-circle"></div>
      <div class="sk-circle10 sk-circle"></div>
      <div class="sk-circle11 sk-circle"></div>
      <div class="sk-circle12 sk-circle"></div>
    </div>
    <div v-if="loadingEdit == true" class="sk-fading-circle" style="margin-top: -200px;">
      <div class="sk-circle1 sk-circle"></div>
      <div class="sk-circle2 sk-circle"></div>
      <div class="sk-circle3 sk-circle"></div>
      <div class="sk-circle4 sk-circle"></div>
      <div class="sk-circle5 sk-circle"></div>
      <div class="sk-circle6 sk-circle"></div>
      <div class="sk-circle7 sk-circle"></div>
      <div class="sk-circle8 sk-circle"></div>
      <div class="sk-circle9 sk-circle"></div>
      <div class="sk-circle10 sk-circle"></div>
      <div class="sk-circle11 sk-circle"></div>
      <div class="sk-circle12 sk-circle"></div>
    </div>

    <!---->
  </div>
</template>
<script>
  import Vue from 'vue'
  import axios from '../../axios/axios'
  import { GridPlugin, Edit, Filter, Group, Page, Selection, CommandColumn, Toolbar } from '@syncfusion/ej2-vue-grids'
  import { ButtonPlugin } from '@syncfusion/ej2-vue-buttons'
  import { DatePickerPlugin, DateRangePickerPlugin } from '@syncfusion/ej2-vue-calendars'
  import { DropDownListComponent } from '@syncfusion/ej2-vue-dropdowns'
  import AddControl from '../planificacion/add_control.vue'
  import { meetingData } from '../../datasource.js'
  import { TooltipPlugin, Tooltip } from '@syncfusion/ej2-vue-popups'
  import { Predicate } from '@syncfusion/ej2-data'
  import { Browser, L10n } from '@syncfusion/ej2-base'

  Vue.use(DateRangePickerPlugin)
  Vue.use(GridPlugin)
  Vue.component(DropDownListComponent)
  Vue.use(DatePickerPlugin)
  Vue.use(ButtonPlugin)
  Vue.use(TooltipPlugin)


  export default Vue.extend({
    data: function () {
      return {
        yuniey:'yy/MM/dd',
        urlControls : '/controls/web/planificacion/control/',
        controlSelected : '',
        Controladores: [],
        Controlados: [],
        dlgShow: false,
        isEditing: false,
        loadingEdit: false,
        loadingGrid: false,
        blockedcomponent:'',
        rowData: null,
        gridData: '',
        tooltip: '',
        editSettings: {showDeleteConfirmDialog: true,allowEditing: true, allowAdding: true, allowDeleting: true, mode: 'Dialog' },
        toolbar: ['Delete','Search','Edit'],
        editparams: { params: { popupHeight: '300px' } },
        pageSettings: { pageCount: 5, pageSize: 10 },
        filterSettings: { type: 'Menu', columns: [] },
        filter: { type: 'CheckBox' },
        selectionSettings: { type: 'Multiple', mode: 'Row', persistSelection: true },
        query: '',
        commands: [{ type: 'edit', buttonOption: { iconCss: 'e-icons e-edit', cssClass: 'e-flat e-noback', click: this.editBtnClick } },
        ]
      }
    },
    mounted(){
      this.listControls();
    },
    methods: {
      rowSelected: function(args) {
        console.log(this.$refs.gridObj.ej2Instances.localeObj.localeStrings)
//      let selectedrowindex = this.$refs.gridObj.getSelectedRowIndexes();  // Get the selected row indexes.
        var selectedrecords = this.$refs.gridObj.getSelectedRecords();  // Get the selected records.
        if(selectedrecords.length>1){
          this.editSettings.allowEditing=false
        }
        else{
          this.editSettings.allowEditing=true
        }

      },
//////////////Logica del servidor
      listControls: function () {
        if(this.dlgShow == true)
          this.loadingEdit= true;
        else
          this.loadingGrid = true
        this.blockedcomponent='blockedcomponent'
        this.$store.dispatch('getProfesoresFacultadCompleto').then((successMessage) => {

          var ListaControladores = this.$store.getters.getProfesoresFacultadCompleto
          var ListaControlados = this.$store.getters.getProfesoresFacultadSemestre
          //Esto es pq no se como coger la cant de meses de clases y estoy haciendo una modificacion del json de los profesores para trabajar la cant de meses
          var jsonControladores= null
          var jsonControlados= null
          for(var i=0;i<ListaControladores.length;i++){
            jsonControladores={
              "id_person": ListaControladores[i].id_person,
              "name" : ListaControladores[i].name,
              "teaching_category" : null,
              "total_hours_plan" : ListaControladores[i].total_hours_plan,
              "teaching_category_ponderada" : ListaControladores[i].teaching_category
            }
            this.Controladores.push(jsonControladores)
          }

          for(var i=0;i<ListaControlados.length;i++){
            jsonControlados={
              "id_person": ListaControlados[i].id_person,
              "name" : ListaControlados[i].name,
              "teaching_category" : null,
              "total_hours_plan" : ListaControlados[i].total_hours_plan,
              "teaching_category_ponderada" : ListaControlados[i].teaching_category
            }
            this.Controlados.push(jsonControlados)
          }

          for(var i=0;i<this.Controladores.length;i++){
            if(this.Controladores[i].teaching_category_ponderada=="Alumno Ayudante"){
              this.Controladores[i].teaching_category=1
            }
            else if(this.Controladores[i].teaching_category_ponderada=="Instructor Recién Graduado"){
              this.Controladores[i].teaching_category=2
            }
            else if(this.Controladores[i].teaching_category_ponderada=="Auxiliar Técnico de la  Docencia" || this.Controladores[i].teaching_category_ponderada=="Auxiliar Técnico de la  Docencia Adjunto"){
              this.Controladores[i].teaching_category=3
            }
            else if(this.Controladores[i].teaching_category_ponderada=="Instructor" || this.Controladores[i].teaching_category_ponderada=="Instructor Adjunto"){
              this.Controladores[i].teaching_category=4
            }
            else if(this.Controladores[i].teaching_category_ponderada=="Asistente" || this.Controladores[i].teaching_category_ponderada=="Asistente Adjunto"){
              this.Controladores[i].teaching_category=5
            }
            else if(this.Controladores[i].teaching_category_ponderada=="Profesor Auxiliar" || this.Controladores[i].teaching_category_ponderada=="Profesor Auxiliar Adjunto"){
              this.Controladores[i].teaching_category=6
            }
            else if(this.Controladores[i].teaching_category_ponderada=="Profesor Titular" || this.Controladores[i].teaching_category_ponderada=="Profesor Titular Adjunto"){
              this.Controladores[i].teaching_category=7
            }
            else if(this.Controladores[i].teaching_category_ponderada=="Colaborador" || this.Controladores[i].teaching_category_ponderada==""){
              this.Controladores[i].teaching_category=0
            }
          }
          for(var i=0;i<this.Controlados.length;i++){
            if(this.Controlados[i].teaching_category_ponderada=="Alumno Ayudante"){
              this.Controlados[i].teaching_category=1
            }
            else if(this.Controlados[i].teaching_category_ponderada=="Instructor Recién Graduado"){
              this.Controlados[i].teaching_category=2
            }
            else if(this.Controlados[i].teaching_category_ponderada=="Auxiliar Técnico de la  Docencia" || this.Controlados[i].teaching_category_ponderada=="Auxiliar Técnico de la  Docencia Adjunto"){
              this.Controlados[i].teaching_category=3
            }
            else if(this.Controlados[i].teaching_category_ponderada=="Instructor" || this.Controlados[i].teaching_category_ponderada=="Instructor Adjunto"){
              this.Controlados[i].teaching_category=4
            }
            else if(this.Controlados[i].teaching_category_ponderada=="Asistente" || this.Controlados[i].teaching_category_ponderada=="Asistente Adjunto"){
              this.Controlados[i].teaching_category=5
            }
            else if(this.Controlados[i].teaching_category_ponderada=="Profesor Auxiliar" || this.Controlados[i].teaching_category_ponderada=="Profesor Auxiliar Adjunto"){
              this.Controlados[i].teaching_category=6
            }
            else if(this.Controlados[i].teaching_category_ponderada=="Profesor Titular" || this.Controlados[i].teaching_category_ponderada=="Profesor Titular Adjunto"){
              this.Controlados[i].teaching_category=7
            }
            else if(this.Controlados[i].teaching_category_ponderada=="Colaborador" || this.Controlados[i].teaching_category_ponderada==""){
              this.Controlados[i].teaching_category=0
            }
          }

          this.$store.dispatch('getControlList').then((successMessage) => {
            this.gridData= this.$store.getters.getControlList
            var yuniii = this.$store.getters.getControlList
            for(var i =0; i<this.gridData.length;i++){
              this.gridData[i].fecha = new Date(this.gridData[i].fecha)
            }
            for(var i=0;i<this.gridData.length;i++){
              this.gridData[i].id_asignatura = JSON.parse(this.gridData[i].id_asignatura)
              this.gridData[i].id_person = JSON.parse(this.gridData[i].id_person)
              this.gridData[i].controlado = JSON.parse(this.gridData[i].controlado)
            }
            this.loadingGrid = false
            this.blockedcomponent=''
            this.loadingEdit = false;
          }).catch((reason) => {
            this.loadingGrid = false
            this.blockedcomponent=''
            this.loadingEdit = false;
            this.$root.$children[0].$refs.toastRef.show({
              title: 'Error!',
              content: reason,
              cssClass: 'e-toast-danger',
              icon: 'e-error toast-icons'
            })
          });
        }).catch((reason) => {
          this.loadingGrid = false
          this.blockedcomponent=''
          this.loadingEdit = false;
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: reason,
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons'
          })
        });
      },
////////////////////////////
      onGridLoad: function () {
        if (Browser.isDevice) {
          this.toolbar = [{ text: 'Edit', tooltipText: 'Edit', prefixIcon: 'e-edit', id: 'editTool' }, 'Delete']
          this.$refs.gridObj.ej2Instances.columns[10].visible = false
        }
      },
      onload: function () {
      },
      loadingPage: function(loading,blocked){
        this.loadingGrid = loading
        this.blockedcomponent=blocked
      },
      onActionBegin: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick(args)
          args.cancel = true
        }
        if (args.requestType === 'delete') {
          this.loadingGrid=true;
          this.blockedcomponent='blockedcomponent'
          var records =  this.$refs.gridObj.getSelectedRecords();
          var cantSeleccionada = records.length
          var listControls=[]
          for (var index = 0; index < records.length; index++) {
            listControls[index]=records[index].id_control
          }
          // Eliminar Tipo de reunión
          axios.post(this.urlControls+'delete_by_id',listControls).then(response => {
            if(cantSeleccionada == 1){
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Éxito!',
                content: 'El Control se ha eliminado correctamente',
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
              this.$refs.gridObj.clearSelection();
            }
            else{
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Éxito!',
                content: 'Los Controles se han eliminado correctamente',
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
              this.$refs.gridObj.clearSelection();
            }
            this.loadingGrid=false;
            this.blockedcomponent=''
          })
        }
      },
      AddBtnClick: function () {
        console.log(this.$refs.gridObj.ej2Instances)
        this.dlgShow = !this.dlgShow
        this.isEditing = false
      },

      //No se para que es (ME PARECE QUE NO HACE NADA)
      editToolClicked: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing = true
          var rows = this.$refs.gridObj.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData = rowObj.data
          console.log(rowObj)
          this.dlgShow = !this.dlgShow
        }
      },
      editBtnClick: function (args) {
        if (this.$refs.gridObj.getSelectedRecords().length === 1){
          this.loadingEdit = true;
        this.blockedcomponent = 'blockedcomponent'
        if (args.type === 'actionBegin') {
          this.rowData = args.rowData
        }
//        var target = args.target
//        if (target.classList.contains('e-edit')) {
//          target = target.parentElement
//        }
//        let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(target.parentElement.parentElement.parentElement.getAttribute('data-uid'))
//        this.rowData = rowObj.data
//         let body={
//           "attr":{
//             "id_control": this.rowData['id_control']
//           }
//         }
//         axios.post(this.urlControls+'find_parameters',body).then(response => {
        this.controlSelected = this.rowData
        this.isEditing = true
        this.dlgShow = !this.dlgShow
        this.loadingEdit = false;
        this.blockedcomponent = ''
        // })
      }
        else{
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: 'Debe seleccionar solo un control para modificar',
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons'
          })
        }
      },
      onDlgClose () {
        this.$refs.gridObj.clearSelection();
        this.controlSelected = ''
        this.dlgShow = false
      },
      showTooltip: function (args) {
        this.tooltip = new Tooltip({
          beforeOpen: (e) => {
            if (e.target.classList.contains('e-unboundcell') || e.target.lastChild.classList.contains('e-unboundcell')) {
              // e.cancel = true
            }
          },
          content: args.data['Note'].toString() }, args.row)
      }
    },
    provide: {
      grid: [Edit, Group, Filter, Page, Selection, CommandColumn, Toolbar]
    },
    components: {
      DropDownListComponent,
      AddControl
    },
    computed: {
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }

    }
  })
</script>
<style scoped>
  .datepicker-panel{
    float: right;
    margin-right:20px;
  }
  .datepicker-panel-group{
    display: flex;
  }
  .content-wrapper {
    width: 100%;
    margin: 15px auto;
    min-width: 185px;
  }
  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }
  }
  .e-add-icon {
    background-repeat: no-repeat;
    /* background-image: url('../assets/Plus.png'); */
  }
</style>
<style>
  @import "@syncfusion/ej2-base/styles/material.css";
  @import "@syncfusion/ej2-inputs/styles/material.css";
</style>



