<template>
  <div>
    <div class="col-md-9 control-section">
      <div class="content-wrapper">
        <ejs-schedule height="650px" :selectedDate='selectedDate' :eventSettings='eventSettings'></ejs-schedule>
      </div>
    </div>
    <div class="col-lg-3 property-section">
      <div id="property" class="property-panel-table" title="Properties">
        <h4>Tipos de reunión a visualizar</h4>
        <hr style="border-color: #d4d3d1;margin-top: 10px;">
        <table id="property" title="Properties" style="width: 100%;margin-top: -10px;">
          <tbody>
          <tr style="height: 50px">
            <td>
              <div>
                <ejs-checkbox label="Consejo de Dirección" cssClass="e-resource e-airways-1" :checked="true" :change="onChange"></ejs-checkbox>
              </div>
            </td>
          </tr>
          <tr style="height: 50px">
            <td>
              <div>
                <ejs-checkbox label="Consejo Universitario" cssClass="e-resource e-airways-2" :checked="true" :change="onChange"></ejs-checkbox>
              </div>
            </td>
          </tr>
          <tr style="height: 50px">
            <td>
              <div>
                <ejs-checkbox label="Reunión de proyecto" cssClass="e-resource e-airways-3" :checked="true" :change="onChange"></ejs-checkbox>
              </div>
            </td>
          </tr>
          <tr style="height: 50px">
            <td>
              <div>
                <ejs-checkbox label="Cosnejo de Sindicato" cssClass="e-resource e-airways-3" :checked="true" :change="onChange"></ejs-checkbox>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

  </div>
</template>
<script>
  import Vue from "vue";
  import { scheduleData } from '../../datasource';
  import { extend } from '@syncfusion/ej2-base';
  import { SchedulePlugin, Day, Week, WorkWeek, Month, Agenda, Resize, DragAndDrop } from "@syncfusion/ej2-vue-schedule";
  import { DatePickerPlugin, ChangeEventArgs } from '@syncfusion/ej2-vue-calendars';
  Vue.use(SchedulePlugin);
  Vue.use(DatePickerPlugin);
  export default Vue.extend({
    data: function () {
      return {
        eventSettings: {
        dataSource: extend([], scheduleData, null, true) },
        selectedDate: new Date(2019, 1, 15)
      }
    },
    provide: {
      schedule: [Day, Week, WorkWeek, Month, Agenda, Resize, DragAndDrop]
    },
    methods: {
      onDateChange: function (args) {
        this.selectedDate = args.value;
      }
    }
  });

</script>



