<template>
    <table class="detailtable" width="100%">
        <colgroup>
            <col width="0%">
            <col width="50%">
            <col width="50%">
        </colgroup>
        <tbody>
            <tr>
                <td rowspan="4" style="text-align: center;">
                    <!-- <img class='photo' :src="image" :alt="data.id" /> -->
                </td>
                 <td>
                    <span style="font-weight: 500;">Punto número: </span> {{data.number}}
                </td>    
                 
                  <td>
                    <span style="font-weight: 500;">Responsable: </span> Humberto Diaz Pando 
                </td>                                                 
            </tr>
            <tr>  
                 <td>
                    <span style="font-weight: 500;">Titulo del punto: </span> {{data.title}}
                </td>     
                <td>
                    <span style="font-weight: 500;">Facilitador: </span> Oscar Leyva Sanchez
                </td>               
            </tr>
            <tr>
                 <td>
                    <span style="font-weight: 500;">Duración </span> {{data.duration}}
                </td>
                              
                <td>
                    <span style="font-weight: 500;">Invitados: </span> Yuniet Artiles, Dayana Rodriguez
                </td>               
            </tr>           
        </tbody>
    </table>
</template>
 
<script>

import { Internationalization } from '@syncfusion/ej2-base';

let instance = new Internationalization();

export default {
  data () {
    return {
        data: {}
    }
  },
  methods: {
    format: function(value){
        return instance.formatDate(value, { skeleton: 'yMd', type: 'date' });
    }
  },
  computed: {
    image: function() {
      return './assets/Avatar_Male.svg';
    }
  }
}
</script>



