import axios from '../../../axios/axios-auth'

const state = {
    editAnnualPlan: false,
    pointListByMeeting: [],
    urlAgenda: "annual_plan/agenda/",
    urlAgenda_Responsible: 'annual_plan/responsible_agenda/',
    listPointUserByState: [],
    // state points
    pointsObserved: [],
    pointsDeveloped: [],
    pointRejected: [],
    pointClosed: []
        //////////////////// 
}
const getters = {
    // state points
    getPointsObserved(state) {
        return state.pointsObserved
    },
    getPointsDeveloped(state) {
        return state.pointsDeveloped
    },
    getPointRejected(state) {
        return state.pointRejected
    },
    getPointClosed(state) {
        return state.pointClosed
    },
    getTotalStatePoint(state) {
        return state.pointsObserved.length + state.pointsDeveloped.length + state.pointRejected.length + state.pointClosed.length
    },
    // 
    isEditPlan(state) {
        return state.editAnnualPlan
    },
    getUrlAgenda(state) {
        return state.urlAgenda
    },
    getPointListByMeeting(state) {
        return state.pointListByMeeting
    },
    geturlAgenda_Responsible(state) {
        return state.urlAgenda_Responsible
    },
    geturlAgenda(state) {
        return state.urlAgenda
    },
    getListPointUserByState(state) {
        return state.listPointUserByState
    },
}
const mutations = {
    setEditableAnnualPlan(state, value) {
        state.editAnnualPlan = value;
    },
    setPointListByMeeting(state, pointList) {
        state.pointListByMeeting = pointList;
    },
    setListPointUserByState(state, listPointUserByState) {
        state.listPointUserByState = listPointUserByState;
    },
    // state points
    setPointsObserved(state, value) {
        state.pointsObserved = value;
    },
    setPointsDeveloped(state, value) {
        state.pointsDeveloped = value;
    },
    setpPintRejected(state, value) {
        state.pointRejected = value;
    },
    setPointClosed(state, value) {
        state.pointClosed = value;
    },
    ///////////// 
}
const actions = {
    setAnnualPlan(context, value) {
        context.commit('setEditableAnnualPlan', value)
    },
    listPointByMeeting(context, idMeeting) {
        return new Promise((resolve, reject) => {
            var jsonBody = {
                "relations": ["meeting.meeting_type"],
                "attr": {
                    "id_meeting": idMeeting
                }
            }
            axios.post(context.getters.getUrlAgenda, jsonBody).then(response => {
                context.commit('setPointListByMeeting', response.data)
                resolve(Object.assign({}, response.data))
            }).catch(response => {
                reject(new Error('No se pudo cargar el orden del día de esta reunión. Error en el servior!'))
            })
        })
    },
    deleteAgenda(context, idAgenda) {
        return new Promise((resolve, reject) => {
            axios.post(context.getters.geturlAgenda + 'delete_by_id', [idAgenda]).then(response => {
                resolve(Object.assign({}, response.data))
            }).catch(response => {
                reject(new Error('No se pudo eliminar el punto. Error en el servior!'))
            })
        })
    },
    listPointByState(context, request) {
        return new Promise((resolve, reject) => {
            let listPoint = [];
            var listObser = [];
            var listDev = [];
            var listReject = [];
            var listClosed = []
            context.commit('setListPointUserByState', [])
            axios.post(context.getters.geturlAgenda_Responsible, request.body).then(response => {

                for (let i = 0; i < response.data.length; i++) {
                    // Esto es para ponerle el nombre del facilitador a la agenda
                    for (let j = 0; j < context.getters.getUserListLDAP.length; j++)
                        if (response.data[i].agenda.id_facilitator == context.getters.getUserListLDAP[j].userId)
                            response.data[i].agenda.id_facilitator = context.getters.getUserListLDAP[j].nombre
                            // Esto es para ponerle el nombre a los invitados al punto 
                    for (let k = 0; k < response.data[i].agenda.arrayagenda_guest.length; k++) {
                        for (let n = 0; n < context.getters.getUserListLDAP.length; n++) {
                            if (response.data[i].agenda.arrayagenda_guest[k].id_user_ldap == context.getters.getUserListLDAP[n].userId) {
                                response.data[i].agenda.arrayagenda_guest[k].id_user_ldap = context.getters.getUserListLDAP[n].nombre
                            }

                        }
                    }
                    if (response.data[i].agenda.observation == request.state)
                        listPoint.push(response.data[i])
                    if (response.data[i].agenda.observation == 'observed')
                        listObser.push(response.data[i])
                    else if (response.data[i].agenda.observation == 'developed')
                        listDev.push(response.data[i])
                    else if (response.data[i].agenda.observation == 'rejected')
                        listReject.push(response.data[i])
                    else if (response.data[i].agenda.observation == 'closed')
                        listClosed.push(response.data[i])
                }
                context.commit('setListPointUserByState', listPoint)
                    // states
                context.commit('setPointsObserved', listObser)
                context.commit('setPointsDeveloped', listDev)
                context.commit('setpPintRejected', listReject)
                context.commit('setPointClosed', listClosed)

                resolve(Object.assign({}, listPoint))
            }).catch(response => {
                reject(new Error('No se pudo obtener la agenda de este usuario. Error en el servior!'))
            })
        })
    },
    statePoints(context, request) {
        return new Promise((resolve, reject) => {
            var listObser = [];
            var listDev = [];
            var listReject = [];
            var listClosed = []
            axios.post(context.getters.geturlAgenda_Responsible, request).then(response => {
                for (let i = 0; i < response.data.length; i++) {
                    if (response.data[i].agenda.observation == 'observed')
                        listObser.push(response.data[i].agenda)
                    if (response.data[i].agenda.observation == 'developed')
                        listDev.push(response.data[i].agenda)
                    if (response.data[i].agenda.observation == 'rejected')
                        listReject.push(response.data[i].agenda)
                    if (response.data[i].agenda.observation == 'closed')
                        listClosed.push(response.data[i].agenda)
                }
                // states
                context.commit('setPointsObserved', listObser)
                context.commit('setPointsDeveloped', listDev)
                context.commit('setpPintRejected', listReject)
                context.commit('setPointClosed', listClosed)
                resolve(Object.assign({}, response))
            }).catch(response => {
                reject(new Error('No se pudo obtener la agenda de este usuario. Error en el servior!'))
            })
        })
    }

}

export default {
    state,
    mutations,
    actions,
    getters
}


