<template>
  <div id="signin">

    <div style="margin-top: 60px;" class="signin-form">
      <!--<div class="logo" style="margin-left: 89px;margin-top: -24px;">-->
        <!--<a href="#">-->
          <!--<img src="../../assets/(Azul)meeting-logo.png" style="height: 20px;" alt=""/>-->
        <!--</a>-->
      <!--</div>-->
      <div style="text-align: center">
        <img id="fotoCujae" style="width: 140px" src="../../assets/IMG_2427.PNG">
      </div>

      <div id="yy">
        <h4 id="yyy" style="margin-left: 90px;margin-top: 70px; font-size: 36px">
  <span class="text-wrapper">
    <span class="line line1"></span>
    <span class="letters letters-left">Controles</span>
    <span class="letters ampersand">a</span>
    <span class="letters letters-right" style="color: rgb(5, 109, 84);">clases</span>
    <span class="line line2"></span>
  </span>
        </h4>
      </div>
      <form @submit.prevent="onSubmit" style="margin-left: 63px;margin-top: 40px;">
        <div class="input">
          <label for="email" style="margin-top: 30px;">Correo</label>
          <input
            type="email"
            id="email"
            v-model="email"
            style="border-radius: 34px;width: 400px;">
        </div>
        <div class="input">
          <label for="password">Contraseña</label>
          <input
            type="password"
            id="password"
            v-model="password"
            style="border-radius: 34px;width: 400px;">
        </div>
        <div class="row pull-right">
          <div class="col-xs-12 col-sm-12 col-lg-6 col-md-6" style="margin-top: 8px;margin-right: 70px;">
            <ejs-button style="background-color: #005ba3; border-color: #005ba3; box-shadow: none; color: #fff; outline: #eaeaea 0 solid; font-size: 14px; font-weight: 600;" @click.native="loginUser">Entrar</ejs-button>
          </div>
        </div>
      </form>
    </div>
    <!---->
    <div class="e-sample-resize-container">
      <ejs-toast ref='toastRef' id='toast_type' :position='position' :created='created' :close='onclose' :beforeOpen='onbeforeOpen'>
      </ejs-toast>
    </div>
    <div class="hidden" id="toast_types">
      <div>
        <ejs-button ref='infoRef' class="e-btn e-control e-info" id="info_Toast" v-on:click.native="infoClick"> Info Message </ejs-button>
        <ejs-button ref='warningRef' class="e-btn e-control e-warning" id="warning_Toast" v-on:click.native="warningClick"> Warning Message </ejs-button>
        <ejs-button ref='successRef' class="e-btn e-control e-success" id="success_Toast" v-on:click.native='successClick'> Success Message </ejs-button>
        <ejs-button ref='errorRef' class="e-btn e-control e-danger" id="error_Toast" v-on:click.native="errorClick">Danger Message</ejs-button>
      </div>
      <div style="padding-top: 15px">
        <ejs-button ref='hideButtonRef' class="e-btn e-control" id="hideToast" v-on:click.native="hideClick"> Hide All </ejs-button>
      </div>
    </div>
    <!--loader-->
    <div v-if="this.$store.getters.getLoader" class="sk-fading-circle">
      <div class="sk-circle1 sk-circle"></div>
      <div class="sk-circle2 sk-circle"></div>
      <div class="sk-circle3 sk-circle"></div>
      <div class="sk-circle4 sk-circle"></div>
      <div class="sk-circle5 sk-circle"></div>
      <div class="sk-circle6 sk-circle"></div>
      <div class="sk-circle7 sk-circle"></div>
      <div class="sk-circle8 sk-circle"></div>
      <div class="sk-circle9 sk-circle"></div>
      <div class="sk-circle10 sk-circle"></div>
      <div class="sk-circle11 sk-circle"></div>
      <div class="sk-circle12 sk-circle"></div>
    </div>
    <!---->
  </div>
</template>


<script>
  //  <ejs-button id="primarybtn" :isPrimary="true" @click.native="loginUser">
  import Vue from 'vue'
  import {ButtonPlugin} from '@syncfusion/ej2-vue-buttons'
  import {ToastPlugin} from '@syncfusion/ej2-vue-notifications'
  import {isNullOrUndefined} from '@syncfusion/ej2-base';


  Vue.use(ToastPlugin)
  Vue.use(ButtonPlugin)
  export default Vue.extend({
    data: function () {
      return {
        email: '',
        password: '',
        position: { X: 'Right' }
      }
    },
    methods: {
      loginUser: function (args) {
        this.loading= true;
        const formData = {
          email: this.email,
          password: this.password,
        }
        this.$store.dispatch('loginSystem', formData).then((successMessage) => {
        //  informacion del usuario
         }).catch((reason) => {
             this.errorClick();
         });
      },

        hideClick: function(args){
          this.$refs.toastRef.hide('All');
        },
        infoClick: function(args){
          this.$refs.toastRef.show(this.toasts[3]);
        },
        warningClick: function(args){
          this.$refs.toastRef.show(this.toasts[0]);
        },
        successClick: function(args){
          this.$refs.toastRef.show(this.toasts[1]);
        },
        errorClick: function(args){
          this.$refs.toastRef.show(this.toasts[2]);
        },
        created: function(args){
          document.addEventListener('click', function() {
            if (!isNullOrUndefined(this.$refs.toastRef) && !isNullOrUndefined(this.$refs.successRef) && !isNullOrUndefined(this.$refs.errorRef) && !isNullOrUndefined(this.$refs.infoRef) && !isNullOrUndefined(this.$refs.warningRef) && event.target !== this.$refs.infoRef.$el && event.target !== this.$refs.warningRef.$el && event.target !== this.$refs.successRef.$el && event.target !== this.$refs.errorRef.$el) {
              this.$refs.toastRef.hide('All');
            }
          }.bind(this));
        },
        onclose: function(e){
          if (e.toastContainer.childElementCount === 0 ) {
            document.getElementById('hideToast').style.display = 'none';
          }
        },
        onbeforeOpen: function(e){
          document.getElementById('hideToast').style.display = 'inline-block';
        }
    },
    mounted: function () {
      document.getElementById("mojonera").setAttribute("style","background-color: #fff")
      this.toasts = [
        {
          title: 'Warning!',
          content: 'There was a problem with your network connection.',
          cssClass: 'e-toast-warning',
          icon: 'e-warning toast-icons'
        },
        {
          title: 'Success!',
          content: 'Your message has been sent successfully.',
          cssClass: 'e-toast-success',
          icon: 'e-success toast-icons'
        },
        {
          title: 'Error de Autenticación!',
          content: 'Verifique sus datos por favor',
          cssClass: 'e-toast-danger',
          icon: 'e-error toast-icons'
        },
        {
          title: 'Information!',
          content: 'Please read the comments carefully.',
          cssClass: 'e-toast-info',
          icon: 'e-info toast-icons'
        }];

    },
  })



</script>

<style scoped>
  #primarybtn {
    /* display: block !important; */
    margin-right: 20px;
  }

  .signin-form {
    width: 567px;
    margin: 30px auto;
    border: 1px solid #ffffff;
    padding: 20px;

  }

  .input {
    margin: 10px auto;
  }

  .input label {
    display: block;
    color: #4e4e4e;
    margin-bottom: 6px;
  }

  .input input {
    font: inherit;
    width: 100%;
    padding: 6px 12px;
    box-sizing: border-box;
    border: 1px solid #ccc;
  }

  .input input:focus {
    outline: none;
    border: 1px solid #521751;
    background-color: #eee;
  }

  .submit button {
    border: 1px solid #521751;
    color: #521751;
    padding: 10px 20px;
    font: inherit;
    cursor: pointer;
  }

  .submit button:hover,
  .submit button:active {
    background-color: #521751;
    color: white;
  }

  .submit button[disabled],
  .submit button[disabled]:hover,
  .submit button[disabled]:active {
    border: 1px solid #ccc;
    background-color: transparent;
    color: #ccc;
    cursor: not-allowed;
  }

  @font-face {
    font-family: 'Toast_icons';
    src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype');
    font-weight: normal;
    font-style: normal;
  }

  #toast_types button {
    margin: 5px;
    min-width: 160px;
    max-width: 160px;
  }

  #toast_types {
    text-align: center;
  }

  .toast-icons {
    font-family: 'Toast_icons' !important;
    speak: none;
    font-size: 55px;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #toast_type .e-toast-icon.e-icons {
    height: auto;
    font-size: 30px;
  }

  .toast-icons.e-success::before {
    content: "\e701";
  }

  .toast-icons.e-error::before {
    content: "\e700";
  }

  .toast-icons.e-info::before {
    content: "\e704";
  }

  .toast-icons.e-warning::before {
    content: "\e703";
  }
  /*loader*/
  .sk-fading-circle {
    margin: 40px auto;
    width: 40px;
    height: 40px;
    position: relative; }
  .sk-fading-circle .sk-circle {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0; }
  .sk-fading-circle .sk-circle:before {
    content: '';
    display: block;
    margin: 0 auto;
    width: 15%;
    height: 15%;
    background-color: #597de8;
    border-radius: 100%;
    -webkit-animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
    animation: sk-circleFadeDelay 1.2s infinite ease-in-out both; }
  .sk-fading-circle .sk-circle2 {
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    transform: rotate(30deg); }
  .sk-fading-circle .sk-circle3 {
    -webkit-transform: rotate(60deg);
    -ms-transform: rotate(60deg);
    transform: rotate(60deg); }
  .sk-fading-circle .sk-circle4 {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg); }
  .sk-fading-circle .sk-circle5 {
    -webkit-transform: rotate(120deg);
    -ms-transform: rotate(120deg);
    transform: rotate(120deg); }
  .sk-fading-circle .sk-circle6 {
    -webkit-transform: rotate(150deg);
    -ms-transform: rotate(150deg);
    transform: rotate(150deg); }
  .sk-fading-circle .sk-circle7 {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg); }
  .sk-fading-circle .sk-circle8 {
    -webkit-transform: rotate(210deg);
    -ms-transform: rotate(210deg);
    transform: rotate(210deg); }
  .sk-fading-circle .sk-circle9 {
    -webkit-transform: rotate(240deg);
    -ms-transform: rotate(240deg);
    transform: rotate(240deg); }
  .sk-fading-circle .sk-circle10 {
    -webkit-transform: rotate(270deg);
    -ms-transform: rotate(270deg);
    transform: rotate(270deg); }
  .sk-fading-circle .sk-circle11 {
    -webkit-transform: rotate(300deg);
    -ms-transform: rotate(300deg);
    transform: rotate(300deg); }
  .sk-fading-circle .sk-circle12 {
    -webkit-transform: rotate(330deg);
    -ms-transform: rotate(330deg);
    transform: rotate(330deg); }
  .sk-fading-circle .sk-circle2:before {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s; }
  .sk-fading-circle .sk-circle3:before {
    -webkit-animation-delay: -1s;
    animation-delay: -1s; }
  .sk-fading-circle .sk-circle4:before {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s; }
  .sk-fading-circle .sk-circle5:before {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s; }
  .sk-fading-circle .sk-circle6:before {
    -webkit-animation-delay: -0.7s;
    animation-delay: -0.7s; }
  .sk-fading-circle .sk-circle7:before {
    -webkit-animation-delay: -0.6s;
    animation-delay: -0.6s; }
  .sk-fading-circle .sk-circle8:before {
    -webkit-animation-delay: -0.5s;
    animation-delay: -0.5s; }
  .sk-fading-circle .sk-circle9:before {
    -webkit-animation-delay: -0.4s;
    animation-delay: -0.4s; }
  .sk-fading-circle .sk-circle10:before {
    -webkit-animation-delay: -0.3s;
    animation-delay: -0.3s; }
  .sk-fading-circle .sk-circle11:before {
    -webkit-animation-delay: -0.2s;
    animation-delay: -0.2s; }
  .sk-fading-circle .sk-circle12:before {
    -webkit-animation-delay: -0.1s;
    animation-delay: -0.1s; }

  @-webkit-keyframes sk-circleFadeDelay {
    0%, 39%, 100% {
      opacity: 0; }
    40% {
      opacity: 1; } }

  @keyframes sk-circleFadeDelay {
    0%, 39%, 100% {
      opacity: 0; }
    40% {
      opacity: 1; } }

</style>

<style>
  .ml5 {
    position: relative;
    font-weight: 300;
    font-size: 3.6em;
    color: #402d2d;
  }

  .ml5 .text-wrapper {
    position: relative;
    display: inline-block;
    padding-top: 0.1em;
    padding-right: 0.05em;
    padding-bottom: 0.15em;
    line-height: 1em;
  }

  .ml5 .line {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    height: 3px;
    width: 100%;
    background-color: #402d2d;
    transform-origin: 0.5 0;
  }

  .ml5 .ampersand {
    font-family: Baskerville, "EB Garamond", serif;
    font-style: italic;
    font-weight: 400;
    width: 1em;
    margin-right: -0.1em;
    margin-left: -0.1em;
  }

  .ml5 .letters {
    display: inline-block;
    opacity: 0;
  }
</style>



