<template>
  <div class="col-lg-12 control-section">
    <div>
      <ejs-grid ref='grid' :dataSource="data" :allowPaging='true' :pageSettings='pageSettings' :toolbar='toolbar' :toolbarClick='toolbarClick'
                :allowExcelExport='true' :allowPdfExport='true' :allowGrouping="false">
        <e-columns>
          <e-column field='id_asignatura.asignatura_name' headerText='Asignatura' :filter='filter' width=70 clipMode='EllipsisWithTooltip' :required='true'></e-column>
          <e-column field='fecha' headerText='Fecha' type='date' editType="datepickeredit"  format='yMd' clipMode='EllipsisWithTooltip' width=140 :required='true'></e-column>
          <e-column field='id_person.name' headerText='Controlador' textAlign='Center' width=130 required=true></e-column>
          <e-column field='controlado.name' headerText='Profesor a Controlar' :filter='filter' width=100 required=true></e-column>
        </e-columns>
      </ejs-grid>
    </div>

  </div>
</template>
<script>
  import Vue from "vue";
  import { GridPlugin, GridComponent, PdfExport, ExcelExport, Page, Group, Toolbar } from "@syncfusion/ej2-vue-grids";
  import { ClickEventArgs } from '@syncfusion/ej2-vue-navigations';

  Vue.use(GridPlugin);

  export default Vue.extend({
    data: () => {
      return {
        data: '',
        groupOptions: { showDropArea: false, columns: ['ShipCountry'] },
        toolbar: ['ExcelExport', 'PdfExport', 'CsvExport'],
        pageSettings: { pageCount: 5, pageSize: 10 }
      };
    },
    mounted(){
      this.listControls();
    },
    methods:{
      toolbarClick: function (ClickEventArgs) {
        switch (ClickEventArgs.item.text) {
          case 'PDF Export':
            this.$refs.grid.pdfExport();
            break;
          case 'Excel Export':
            this.$refs.grid.excelExport();
            break;
          case 'CSV Export':
            this.$refs.grid.csvExport();
            break;
        }
      },
      listControls: function () {
        var ControlesPrevia = this.$store.getters.getListaControlesPrevia
        // var Controladores = this.$store.getters.getProfesoresFacultad
        // var Controlados = this.$store.getters.getProfesoresFacultad
        for(var i=0;i<ControlesPrevia.length;i++){
          ControlesPrevia[i].id_asignatura = JSON.parse(ControlesPrevia[i].id_asignatura)
          ControlesPrevia[i].id_person = JSON.parse(ControlesPrevia[i].id_person)
          ControlesPrevia[i].controlado = JSON.parse(ControlesPrevia[i].controlado)
        }

        this.data = ControlesPrevia
      },
    },
    provide: {
      grid: [PdfExport, ExcelExport, Page, Group, Toolbar]
    }
  });
</script>



