<template id="view-hardware">
  <div class="container-fluid">
    <div class="row top-row">
      <div class="col-xs-12 col-sm-12 col-lg-7 col-md-12"><h3>Acuerdos</h3></div>

      <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6">
        <!--<ejs-button id="primarybtn" class="asset-btn" :isPrimary="true" v-on:click.native="AddBtnClick"> <span class="e-add-icon"></span>Añadir Plan</ejs-button>-->
      </div>
      <div class="col-xs-12 col-sm-6 col-lg-3 col-md-6 daterange overview-range-picker">
        <ejs-daterangepicker ref='dateRangePickerObject' class="e-input-group e-input-custom-tag e-control-wrapper e-date-range-wrapper" id='date-wrapper' :placeholder="waterMark" :change="dateRangeChanged">
        </ejs-daterangepicker>
      </div>

    </div>
    <div class="row">
      <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12">
        <ejs-grid class="r-space" ref="gridObj" :rowDataBound="setStyle" :load="onGridLoad" 
        :actionBegin='onActionBegin' :toolbarClick="editToolClicked" :toolbar="toolbar" 
        :actionComplete='onload' :dataSource='gridData' :allowSorting='true' :allowSelection='true' 
        :selectionSettings='selectionSettings' :allowFiltering='true' :allowPaging='true' 
        :allowGrouping='false' :pageSettings='pageSettings' :editSettings='editSettings' 
        :filterSettings='filterSettings'>
          <e-columns>
            <e-column field='No' headerText='' textAlign='Left' width=20 :isPrimaryKey='true'></e-column>
            <e-column field='Acuerdo' headerText='Acuerdo' clipMode='EllipsisWithTooltip' width=150></e-column>
            <e-column field='FechaCumplimiento' headerText='Fecha Cump.' type='date' editType='datepickeredit' format='yMd'  :filter='filter' clipMode='EllipsisWithTooltip'
                      width=60></e-column>
            <e-column field='Responsable' headerText='Responsable' :filter='filter' width=90></e-column>
            <e-column field='Participantes' headerText='Participantes' :filter='filter' width=90></e-column>
            <e-column field='Status' headerText='Estado' width=70 :visible='false'></e-column>
            <e-column  headerText='Acción' width='60' :commands='commands' :visible='false' textAlign='left'></e-column>
            <e-column  headerText='Cumplimiento' width='150' :commands='labelCommands' textAlign='left' ></e-column>
          </e-columns>
        </ejs-grid>
      </div>
    </div>
    <ejs-dialog :buttons='promptDlgButtons' ref="promptDialog" isModal='true' :allowDragging='true'
                v-bind:visible="false" :header='promptHeader' :animationSettings='animationSettings'
                :showCloseIcon='showCloseIcon' :target='target' :open="dialogOpen" :close="dialogClose"
                :width='promptWidth'>
      <hr style="margin-top: 0px;">
      <br>
      <div class="control-section">
        <div class="row" style="margin-top: -7px;">
          <div class="col-xs-6 col-sm-6 col-lg-12 col-md-12" style="margin-top: -18px;">
            <label style="font-size: 17px;" class="e-text">Motivos:</label> <br>
            <textarea id="obesrvations" class="e-input" rows="4"></textarea>
          </div>
        </div>      </div>
    </ejs-dialog>

  </div>
</template>
<script>
  import Vue from 'vue'
  import { GridPlugin, Edit, Filter, Group, Page, Selection, CommandColumn, Toolbar } from '@syncfusion/ej2-vue-grids'
  import { ButtonPlugin } from '@syncfusion/ej2-vue-buttons'
  import { DatePickerPlugin, DateRangePickerPlugin } from '@syncfusion/ej2-vue-calendars'
  import { DropDownListComponent } from '@syncfusion/ej2-vue-dropdowns'
  import { TaskData, employeeData } from '../../datasource.js'
  import { DataManager, Query, Predicate } from '@syncfusion/ej2-data'
  import { Browser } from '@syncfusion/ej2-base'

  Vue.use(DateRangePickerPlugin)
  Vue.use(GridPlugin)
  Vue.component(DropDownListComponent)
  Vue.use(DatePickerPlugin)
  Vue.use(ButtonPlugin)

  export default Vue.extend({
    data: function () {
      return {
//
        promptDlgButtons: [{
          click: this.saveDialogVerification,
          buttonModel: {content: 'Confirmar', isPrimary: true}
        }, {click: this.closeDialogDone, buttonModel: {content: 'Cancelar'}}],
        promptHeader: 'Verificación del acuerdo',
        animationSettings: {effect: 'Zoom'},
        showCloseIcon: true,
        target: '.control-section',
        promptWidth: '500px',
//
        dlgShow: false,
        isEditing: false,
        rowData: null,
        waterMark: 'Filtrar por rango de fecha',
        gridData: TaskData,
        editSettings: { showDeleteConfirmDialog: true, allowEditing: true, allowAdding: true, allowDeleting: true, mode: 'Dialog' },
        toolbar: undefined,
        editparams: { params: { popupHeight: '300px' } },
        pageSettings: { pageCount: 5, pageSize: 10 },
        filterSettings: { type: 'Menu', columns: [] },
        filter: { type: 'CheckBox' },
        selectionSettings: { persistSelection: true },
        commands: [{ type: 'edit', buttonOption: { iconCss: ' e-icons e-edit', cssClass: 'e-flat e-noback', click: this.editBtnClick } },
          { type: 'Delete', buttonOption: { iconCss: 'e-icons e-delete', cssClass: 'e-flat e-noback' } }],
        labelCommands: [{ buttonOption: { content: 'Cumplido', iconCss: '', cssClass: 'e-flat e-approve', click: this.showDialogVerification } },
          { buttonOption: { content: 'No cumplido', iconCss: '', cssClass: 'e-flat e-reject', click: this.showDialogVerification } }],
//      argumentos de la accion de los botones cumplido y no cumplido
        argsDialogVerification:'',
      }
    },
    methods: {
      showDialogVerification: function (args) {
        this.argsDialogVerification=args
        this.$refs.promptDialog.show();
      },
      closeDialogDone: function () {
        this.$refs.promptDialog.hide();
      },
      saveDialogVerification: function () {
        var target = this.argsDialogVerification.target
        if (target.classList.contains('e-approve')) {
          this.$refs.promptDialog.hide();
          this.approveClick(this.argsDialogVerification)
        }
        if(target.classList.contains('e-reject')){
          this.$refs.promptDialog.hide();
          this.rejectClick(this.argsDialogVerification)
        }

      },
      onGridLoad: function () {
        if (Browser.isDevice) {
          this.toolbar = [{ text: 'Edit', tooltipText: 'Edit', prefixIcon: 'e-edit', id: 'editTool' }, 'Delete']
          this.$refs.gridObj.ej2Instances.columns[8].visible = false
        }
      },
      onload: function () {
      },
      onActionBegin: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick(args)
          args.cancel = true
        }
      },
      dateRangeChanged: function (args) {
        // check wheather the value is empty or not
        if (args.startDate && args.endDate) {
          var predicatesStart = new Predicate('RequestedOn', 'greaterthanorequal', args.startDate)
          var predicatesEnd = new Predicate('RequestedOn', 'lessthanorequal', args.endDate)
          this.filterSettings = {type: 'Menu', columns: [predicatesStart, predicatesEnd]}
        } else {
          this.filterSettings = {type: 'Menu', columns: []}
        }
      },
      AddBtnClick: function () {
        this.$store.dispatch('setAnnualPlan', false)
        this.$router.push({ path: '/' })

      },
      editToolClicked: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing = true
          var rows = this.$refs.gridObj.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData = rowObj.data
          this.dlgShow = !this.dlgShow
        }
      }, 
       editToolClicked: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing = true
          var rows = this.$refs.gridObj.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData = rowObj.data
          this.dlgShow = !this.dlgShow
        }
      },
      editBtnClick: function (args) {
        this.$store.dispatch('setAnnualPlan', true)
        this.$router.push({ path: '/Plan' })

      },
      setStyle: function (args) {
        var status = args.data['Status'].toString()
        if (status === 'Approved') {
          args.row.querySelectorAll('.e-unboundcell')[1].classList.remove('row-reject')
          args.row.querySelectorAll('.e-unboundcell')[1].classList.add('row-approve')
          args.row.querySelector('.e-approve').classList.add('e-hide')
          args.row.querySelector('.e-reject').classList.add('e-hide')
        } else if (status === 'Rejected') {
          args.row.querySelectorAll('.e-unboundcell')[1].classList.remove('row-approve')
          args.row.querySelectorAll('.e-unboundcell')[1].classList.add('row-reject')
          args.row.querySelector('.e-approve').classList.add('e-hide')
          args.row.querySelector('.e-reject').classList.add('e-hide')
        } else {
          args.row.querySelectorAll('.e-unboundcell')[1].classList.remove('row-approve')
          args.row.querySelectorAll('.e-unboundcell')[1].classList.remove('row-reject')
          args.row.querySelector('.e-approve').classList.remove('e-hide')
          args.row.querySelector('.e-reject').classList.remove('e-hide')
        }
      },
      onDlgClose () {
        this.dlgShow = false
      },
      approveClick: function (args) {
        var userName = new DataManager(employeeData).executeLocal(new Query().where(new Predicate('id', 'equal', this.$store.state.store_notification.currentUserID)))[0]['Employee']
        var userImg = new DataManager(employeeData).executeLocal(new Query().where(new Predicate('id', 'equal', this.$store.state.store_notification.currentUserID)))[0]['ImgSrc']
        var target = args.target
        if (target.classList.contains('e-approve')) {
          target = target.parentElement
        }
        let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(target.parentElement.parentElement.getAttribute('data-uid'))
        var row = rowObj.data

        var grid = document.querySelector('.e-grid')['ej2_instances'][0]
        var index = grid.selectedRowIndex
        grid.editModule.updateRow(index, {'No': row['No'], 'Status': 'Approved'})

        var activity = {
          'id': 9,
          'Employee': 'Humberto Díaz Pando',
          'ImgSrc': userImg,
          'TimeStamp': new Date().toLocaleDateString(),
          'Message': 'El acuerdo #' + row['No'] + ' del responsable ' +row['Responsable'] +' ha sido cumplido'
        }
        this.$store.dispatch('addActivity', Object.assign({}, activity)).then(() => {
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Éxitos!',
            content: 'La solicitud le será enviada al Jefe del grupo y a su planificador',
            cssClass: 'e-toast-success',
            icon: 'e-success toast-icons'
          })
        }).catch((reason) => {
          alert(reason)
        })

        this.$root.$children[0].$children[0].updatePendingRequests()
        console.log(this.$root)
      },
      rejectClick: function (args) {
        var userName = new DataManager(employeeData).executeLocal(new Query().where(new Predicate('id', 'equal', this.$store.state.store_notification.currentUserID)))[0]['Employee']
        var userImg = new DataManager(employeeData).executeLocal(new Query().where(new Predicate('id', 'equal', this.$store.state.store_notification.currentUserID)))[0]['ImgSrc']
        var target = args.target
        if (target.classList.contains('e-reject')) {
          target = target.parentElement
        }
        let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(target.parentElement.parentElement.getAttribute('data-uid'))
        var row = rowObj.data

        var grid = document.querySelector('.e-grid')['ej2_instances'][0]
        var index = grid.selectedRowIndex
        grid.editModule.updateRow(index, {'No': row['No'], 'Status': 'Rejected'})
        var activity = {
          'id': 9,
          'Employee': 'Humberto Díaz Pando',
          'ImgSrc': userImg,
          'TimeStamp': new Date().toLocaleDateString(),
          'Message': 'El acuerdo #' + row['No'] + ' del responsable ' +row['Responsable'] +' no ha sido cumplido'
        }
        this.$store.dispatch('addActivity', Object.assign({}, activity)).then(() => {
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Éxitos!',
            content: 'La solicitud le será enviada al Jefe del grupo y a su planificador',
            cssClass: 'e-toast-success',
            icon: 'e-success toast-icons'
          })
        }).catch((reason) => {
          alert(reason)
        })
        this.$root.$children[0].$children[0].updatePendingRequests()
      }
    },
    provide: {
      grid: [Edit, Group, Filter, Page, Selection, CommandColumn, Toolbar]
    },
    components: {
      DropDownListComponent,
    }
  })
</script>

<style>
  #primarybtn{
    /* display: block !important; */
    margin-right:20px;
  }
  .datepicker-panel{
    float: right;
    margin-right:20px;
  }
  .datepicker-panel-group{
    display: flex;
  }
  .content-wrapper {
    width: 100%;
    margin: 15px auto;
    min-width: 185px;
  }
  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }
  }
  .e-flat .e-approve {
    padding: 2px 10px !important;
  }
  .e-approve, .e-reject {
    background-color: #E2E4E8 !important;
    font-size: 13px !important;
    color: #4C4C4C !important;
  }
  .e-approve:hover, .e-reject:hover {
    background-color: #D1D4DB !important;
  }
  .e-approve:focus, .e-reject:focus {
    background-color: #B4B8C0 !important;
  }

  .e-grid .e-rowcell.e-unboundcell.row-approve {
    background-image: url('../../assets/Approve.svg');
    background-repeat: no-repeat;
    background-position-y: center;
  }
  .row-approve::after {
    padding-left: 20px;
    content: 'Cumplido';
    color: #0BB60E;
    font-weight: 600;
    font-size: 13px;
  }
  .e-grid .e-rowcell.e-unboundcell.row-reject {
    background-image: url('../../assets/Reject.svg');
    background-repeat: no-repeat;
    background-position-y: center;
  }
  .row-reject::after {
    padding-left: 20px;
    content: 'No cumplido';
    color: #F64F4C;
    font-weight: 600;
    font-size: 13px;
  }
</style>



