<template id="view-hardware">
  <div class="container-fluid">
    <!--template para añadir un punto en el plan----------------------------------->
    <div id="dialog" style="padding:10px;position:absolute;">
      <add-point :showing="dlgShow" :isEdit="isEditing" :rowData="rowData" @close="onDlgClose">
      </add-point>
    </div>
    <!--Seleción de los parametros del plan anual--------------------------------------------->
    <div class="row top-row">
      <!--Selección del Tipo de reunión-->
      <div class="col-xs-12 col-sm-6 col-lg-3 col-md-12">
        <ejs-dropdownlist id='meetingtype' :dataSource='meeting_type_data' :fields='meetingTypeFields'
                          :change='ChangeMeetingTypeInstances' :query='queryMeetingType' :placeholder='label'
                          sortOrder='Ascending'>
        </ejs-dropdownlist>
      </div>
      <!--Selección de la instancia-->
      <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6">
        <ejs-dropdownlist id='intances' :dataSource='intancesData'
                          :change='ChangeAnnualPlanData' :enabled='instancesEnabled' :placeholder='intancesLabel'
                          :popupHeight='height' :fields='groupIntancesFields'></ejs-dropdownlist>
      </div>
      <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6" style="">
        <ejs-button :style="styleBtnDelete"
                    :disabled="btnAdd" id="primarybtn" class="asset-btn" v-on:click.native="promptBtnClick"><span
          class="e-add-icon"></span> Eliminar
        </ejs-button>
      </div>

      <!--Añadir un punto en el plan-->
      <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6" style="margin-left: 285px;">
        <ejs-button :disabled="btnAdd" id="primarybtn" class="asset-btn" :isPrimary="isBolcked"
                    v-on:click.native="AddBtnClick"><span class="e-add-icon"></span> Añadir punto
        </ejs-button>
        <!--:disabled="btnAdd"-->
      </div>
    </div>
    <!--Tabla que lista los puntos de una instancia determinada en el plan-->
    <div class="row">
      <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12">
        <ejs-grid disabled class="r-space" ref="gridObj" :dataSource='gridData'
                  :load="onGridLoad" :actionBegin='onActionBegin' :toolbarClick="editToolClicked"
                  :toolbar="toolbar" :actionComplete='onload' :allowSorting='true' :allowSelection='true'
                  :selectionSettings='selectionSettings' :allowFiltering='true' :allowPaging='true'
                  :allowGrouping='false'
                  :pageSettings='pageSettings' :editSettings='editSettings' :filterSettings='filterSettings'>
          <e-columns>
            <e-column field='No' headerText='No' textAlign='Left' width=35  :isPrimaryKey='true'></e-column>
            <e-column field='Punto' headerText='Punto' :filter='filter' clipMode='EllipsisWithTooltip'
                      width=170></e-column>
            <e-column field='Responsable' headerText='Responsable' width=100></e-column>
            <e-column field='Invitados' headerText='Invitados' clipMode='EllipsisWithTooltip' width=100></e-column>
            <e-column field='Facilitador' headerText='Facilitador' clipMode='EllipsisWithTooltip' width=100></e-column>
            <e-column field='Duracion' headerText='Duración' :filter='filter' clipMode='EllipsisWithTooltip'
                      width=60></e-column>
            <e-column headerText='Acción' width='70' :commands='commands'></e-column>
          </e-columns>
        </ejs-grid>
      </div>
      <!--Botones de guardar y cancelar---------------------------------------------------------->

      <div class="row top-row">
        <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6">
          <ejs-button :style="styleBtnSalve" id="primarybtn" class="asset-btn"
                      v-on:click.native="promptBtnClickConfirm"><span class="e-add-icon"></span>Guardar
            <!---->
          </ejs-button>
        </div>
        <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6">
          <ejs-button :style="styleBtnCancel" id="primarybtn" class="asset-btn"
                      v-on:click.native="CancelActions"><span class="e-add-icon"></span>Cancelar
          </ejs-button>

        </div>
      </div>

      <!--Opciones para configurar el criterio de eliminación el plan-->
      <ejs-dialog :buttons='promptDlgButtons' ref="promptDialog" isModal='true' :allowDragging='true'
                  v-bind:visible="false" :header='promptHeader' :animationSettings='animationSettings'
                  :showCloseIcon='showCloseIcon' :target='target' :open="dialogOpen" :close="dialogClose"
                  :width='promptWidth'>
        <hr style="margin-top: 0px;">
        <h4 class="row" style="margin-left: 45px;">Seleccione una opción para eliminar</h4>
        <br>
        <div class="row">
          <div class="col-xs-12 col-sm-6 col-lg-6 col-md-6" style="margin-top: -20px;">
            <ejs-radiobutton label="Eliminar todo el plan" name="delete" value="deleteAll" :checked="true"
                             :change="onChange"></ejs-radiobutton>
          </div>
          <div class="col-xs-12 col-sm-6 col-lg-6 col-md-6" style="margin-top: -20px;">
            <ejs-radiobutton label="Eliminar por intancias" name="delete" value="deleteIntances"
                             :change="onChange"></ejs-radiobutton>
          </div>
          <ejs-listview :style='styleList'
                        :dataSource='MonthData' :fields='fields' showCheckBox='true'></ejs-listview>
        </div>
      </ejs-dialog>
      <ejs-dialog :buttons='promptDlgButtonsConfirm' ref="promptDialogConfirm" isModal='true' :allowDragging='true'
                  v-bind:visible="false" :header='promptHeaderConfirm' :animationSettings='animationSettings'
                  :showCloseIcon='showCloseIcon' :target='target' :open="dialogOpen" :close="dialogClose"
                  :width='promptWidth'>
        <hr style="margin-top: 0px;">
        <h4 class="row" style="margin-left: 97px;">Añada un título al plan</h4>
        <br>
        <div class="row">
          <div class="col-xs-12 col-sm-6 col-lg-10 col-md-6" style="margin-left: 32px;margin-top: -16px;">
            <input id="name" name="Name" class="e-input" type="text">
          </div>
        </div>
      </ejs-dialog>

    </div>
 
  </div>
</template>
<script>
  import Vue from 'vue'
  import {GridPlugin, Edit, Filter, Group, Page, Selection, CommandColumn, Toolbar} from '@syncfusion/ej2-vue-grids'
  import {ButtonPlugin} from '@syncfusion/ej2-vue-buttons'
  import {DatePickerPlugin, DateRangePickerPlugin} from '@syncfusion/ej2-vue-calendars'
  import {DropDownListComponent} from '@syncfusion/ej2-vue-dropdowns'
  import AddPoint from '../annual_plan/add_point_plan.vue'
  import {AgendasData} from '../../datasource.js'
  import {Predicate} from '@syncfusion/ej2-data'
  import {Browser} from '@syncfusion/ej2-base'
  ///////
  import {DropDownListPlugin} from "@syncfusion/ej2-vue-dropdowns";
  import {CheckBoxPlugin} from "@syncfusion/ej2-vue-buttons";
  import {Query, DataManager, ODataAdaptor} from '@syncfusion/ej2-data';
  import {DialogPlugin} from '@syncfusion/ej2-vue-popups';
  import {RadioButtonPlugin} from "@syncfusion/ej2-vue-buttons";
  import {ListViewPlugin} from "@syncfusion/ej2-vue-lists";

  Vue.use(ListViewPlugin);
  Vue.use(RadioButtonPlugin);
  Vue.use(DialogPlugin);
  Vue.use(DropDownListPlugin);
  Vue.use(CheckBoxPlugin);
  /////////
  Vue.use(DateRangePickerPlugin)
  Vue.use(GridPlugin)
  Vue.component(DropDownListComponent)
  Vue.use(DatePickerPlugin)
  Vue.use(ButtonPlugin)

  var serverDataMeetingType = new DataManager({
    url: 'http://localhost:81/meeting_system/meeting/web/meeting_type/meeting_type/',
    adaptor: new ODataAdaptor,
    crossDomain: true
  });

  export default Vue.extend({
    data: function () {
      return {
        dlgShow: false,
        isEditing: false,
        rowData: null,
        waterMark: 'Select a range',
        gridData: null,
        editSettings: {
          showDeleteConfirmDialog: true,
          allowEditing: true,
          allowAdding: true,
          allowDeleting: true,
          mode: 'Dialog'
        },
        toolbar: undefined,
        editparams: {params: {popupHeight: '300px'}},
        pageSettings: {pageCount: 5, pageSize: 10},
        filterSettings: {type: 'Menu', columns: []},
        filter: {type: 'CheckBox'},
        selectionSettings: {persistSelection: true},
        commands: [{
          type: 'edit',
          buttonOption: {iconCss: ' e-icons e-edit', cssClass: 'e-flat e-noback', click: this.editBtnClick}
        },
          {type: 'Delete', buttonOption: {iconCss: 'e-icons e-delete', cssClass: 'e-flat e-noback'}}],
///////// Selección de el Tipo de reunión y sus instancias
        meeting_type_data: serverDataMeetingType,
        meetingTypeFields: {text: 'name', value: 'id_meeting_type'},
        queryMeetingType: new Query().select(['name', 'id_meeting_type']),
        label: 'Seleccione un Tipo de reunión',
//////////////Seleccion de las instancias para gestionar los puntos de la agenda
        instancesEnabled: false,
        btnAdd: true,
        isBolcked: false,
        groupIntancesFields: {groupBy: 'Mes', text: 'Fecha', value: 'Id'},
        intancesLabel: 'Intancias',
        height: '200px',
        intancesData: [
          {Fecha: '1-1-2019', Mes: 'Enero', Id: 'item1'},
          {Fecha: '18-1-2019', Mes: 'Enero', Id: 'item2'},
          {Fecha: '5-2-2019', Mes: 'Febrero', Id: 'item3'},
          {Fecha: '17-2-2019', Mes: 'Febrero', Id: 'item4'},
          {Fecha: '6-3-2019', Mes: 'Marzo', Id: 'item5'},
          {Fecha: '15-3-2019', Mes: 'Marzo', Id: 'item6'},
          {Fecha: '1-4-2019', Mes: 'Abril', Id: 'item7'},
          {Fecha: '12-4-2019', Mes: 'Abril', Id: 'item8'},
          {Fecha: '4-5-2019', Mes: 'Mayo', Id: 'item9'},
          {Fecha: '13-5-2019', Mes: 'Mayo', Id: 'item10'},
          {Fecha: '2-6-2019', Mes: 'Junio', Id: 'item11'},
          {Fecha: '12-6-2019', Mes: 'Junio', Id: 'item12'}
        ],
///////////////////////////Dialog de eliminar instancias
        promptDlgButtons: [{
          click: this.promptDlgBtnClick,
          buttonModel: {content: 'Confirmar', isPrimary: true}
        }, {click: this.promptDlgBtnClick, buttonModel: {content: 'Cancelar'}}],
        promptHeader: 'Eliminar',
        animationSettings: {effect: 'Zoom'},
        showCloseIcon: true,
        target: '.control-section',
        promptWidth: '450px',
////////////////////////////////Dialog confirmar plan
        promptDlgButtonsConfirm: [{
          click: this.SalveAnnualPlan,
          buttonModel: {content: 'Confirmar', isPrimary: true}
        }, {click: this.promptDlgBtnClickConfirm, buttonModel: {content: 'Cancelar'}}],
        promptHeaderConfirm: 'Confirmación',
//////////////////////////////// estilos de los botones del template
        styleBtnDelete: '',
        styleBtnCancel: 'background-color: rgba(220, 216, 216, 0.93) !important;\n' +
        '    border-color: rgb(220, 216, 217) !important;\n' +
        '    color: rgb(60, 57, 57) !important;\n' +
        '    border-radius: 20px;\n' +
        '    height: 33px;\n' +
        '    width: 93px;\n' +
        '    margin-left: 847px;\n' +
        '    margin-top: 25px;',
        styleBtnSalve: '    background-color: rgb(255, 197, 2) !important;\n' +
        '    border-color: rgb(255, 197, 2) !important;\n' +
        '    color: rgb(60, 57, 57) !important;\n' +
        '    border-radius: 20px;\n' +
        '    height: 33px;\n' +
        '    width: 93px;\n' +
        '    margin-left: 945px;\n' +
        '    margin-top: 25px;',
///////////////////////////////////Seleccion de las instancias a eliminar
        MonthData: '',
        fields: {groupBy: 'mes'},
        styleList: '',
      }
    },
    mounted() {
      if (this.isEditable) {
        this.instancesEnabled= true;
        this.ChangeAnnualPlanData();
        document.getElementById('meetingtype').value = 'Consejo universitario'
        document.getElementById('intances').value = '1-1-2019'
      }
    },
    computed: {
      isEditable() {
        return this.$store.getters.isEditPlan
      },
    },
    methods: {
//      Salvar plan anual
      SalveAnnualPlan: function () {
        this.$router.push({path: '/PlanAnual'})

      },
//      Cancelar plan
      CancelActions: function () {
        this.$router.push({path: '/PlanAnual'})

      },
      onChange: function (args) {
        if (args.value == 'deleteIntances') {
          this.MonthData = [
            {
              'text': '5-1-2019',
              'id': '9bdb',
              'mes': 'Enero'
            },
            {
              'text': '24-1-2019',
              'id': '4589',
              'mes': 'Enero'
            },
            {
              'text': '3-2-2019',
              'id': 'e807',
              'mes': 'Febrero'
            },
            {
              'text': '20-2-2019',
              'id': 'a0cc',
              'mes': 'Febrero'
            },
            {
              'text': '7-3-2019',
              'id': '5e26',
              'mes': 'Marzo'
            },
            {
              'text': '28-3-2019',
              'id': 'f849',
              'mes': 'Marzo'
            },
            {
              'text': '2-4-2019',
              'id': '7aff',
              'mes': 'Abril'
            },
            {
              'text': '22-4-2019',
              'id': 'b1da',
              'mes': 'Abril'
            },
            {
              'text': '1-5-2019',
              'id': 'de2f',
              'mes': 'Mayo'
            },
            {
              'text': '18-5-2019',
              'id': 'b2b1',
              'mes': 'Mayo'
            }
          ]
          this.styleList = 'border: 1px solid #dddddd;border-radius: 3px; margin-top: 12px;'
        }
        else {
          this.styleList = ''
          this.MonthData = ''
        }
      },
//      boton del dialog confirmar plan
      promptBtnClickConfirm: function () {
        this.$refs.promptDialogConfirm.show();
      },
//      mostrar dialog para eliminar instancias
      promptBtnClick: function () {
        this.$refs.promptDialog.show();
      },
//      cerrar dialog:  eliminar instancias
      promptDlgBtnClick: function () {
        this.$refs.promptDialog.hide();
      },
//      cerrar dialog: confirmar plan
      promptDlgBtnClickConfirm: function () {
        this.$refs.promptDialogConfirm.hide();
      },
      dialogOpen: function () {
//      cuando se abre sucede algo
      },
      dialogClose: function () {
//        cuando cierra sucede algo
      },
      ChangeMeetingTypeInstances: function (args) {
        // disable the state DropDownList
        this.instancesEnabled = true;

      },
      ChangeAnnualPlanData: function (args) {
        this.btnAdd = false;
        this.isBolcked = true;
        this.gridData = AgendasData;
        this.styleBtnDelete = 'background-color: #ff2b2bed !important;border-color: #f92f2f !important; color: #fff8f8 !important; border-radius: 20px;height: 33px;width: 93px;'
      },
      onGridLoad: function () {
        if (Browser.isDevice) {
          this.toolbar = [{text: 'Edit', tooltipText: 'Edit', prefixIcon: 'e-edit', id: 'editTool'}, 'Delete']
          this.$refs.gridObj.ej2Instances.columns[11].visible = false
        }
      },
      onload: function () {
      },
      onActionBegin: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick(args)
          args.cancel = true
        }
      },
      AddBtnClick: function () {
        this.dlgShow = !this.dlgShow
        this.isEditing = false
      },
      editToolClicked: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing = true
          var rows = this.$refs.gridObj.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData = rowObj.data
          this.dlgShow = !this.dlgShow
        }
      },
      editBtnClick: function (args) {
        this.isEditing = true
        this.dlgShow = !this.dlgShow
        if (args.type === 'actionBegin') {
          this.rowData = args.rowData
          return
        }
        var target = args.target
        if (target.classList.contains('e-edit')) {
          target = target.parentElement
        }
        let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(target.parentElement.parentElement.parentElement.getAttribute('data-uid'))
        this.rowData = rowObj.data
      },
      onDlgClose() {
        this.dlgShow = false
      }
    },
    provide: {
      grid: [Edit, Group, Filter, Page, Selection, CommandColumn, Toolbar]
    },
    components: {
      DropDownListComponent,
      AddPoint
    }
  })
</script>

<style scoped>
  #primarybtn {
    /* display: block !important; */
    margin-right: 20px;
  }

  .datepicker-panel {
    float: right;
    margin-right: 20px;
  }

  .datepicker-panel-group {
    display: flex;
  }

  .content-wrapper {
    width: 100%;
    margin: 15px auto;
    min-width: 185px;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }
  }

  #sample-list {
    max-width: 500px;
  }

  #sample-list, #sample-list-flat, #sample-list-group {
    border: 1px solid #dddddd;
    border-radius: 3px;
    margin: auto;
  }

  .checklist.control-section {
    overflow: auto;
    padding-bottom: 10px;
  }

  #flat-list, #group-list {
    width: 50%;
    padding: 10px;
    margin: auto;
  }

  #flat-list {
    float: left;
  }

  #group-list {
    float: right;
  }

  #flat-list h4, #group-list h4 {
    padding-left: 15px;
  }

  .e-listview .e-list-icon {
    height: 24px;
    width: 30px;
  }

  #listview {
    max-width: 500px;
    margin: auto;
    border: 1px solid #dddddd;
    border-radius: 3px;
  }

  @media (max-width: 590px) {
    #flat-list, #group-list {
      width: 100%;
    }
  }

</style>



