import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index.js'

import Site from '../components/Home/App_Home.vue'
import Login from '../components/Home/Login.vue'
import PageNotFound from '../components/planificacion/PageNotFound.vue'
import Planificar from '../components/planificacion/planificar.vue'
import Planificacion from '../components/planificacion/planificacion.vue'
import Verificar from '../components/planificacion/Verificar.vue'
import PlanificacionVizualizada from '../components/planificacion/planificacionVizualizada.vue'
import Verificacion from '../components/planificacion/verification.vue'
import Unauthorized from '../components/planificacion/unauthorized.vue'
import MeetingTypeIndex from '../components/meeting_type/meeting_type_index.vue'
import dashboard from '../components/Index/dashboard_meeting.vue'
import AnnualPlanIndex from '../components/annual_plan/annual_plan_index.vue'
import AnnualPlan from '../components/annual_plan/annual_plan.vue'
import TaskIndex from '../components/task_managment/task_index.vue'
import Test from '../components/Tests.vue'
import Verification_agenda from '../components/verification/verification_agenda.vue'

import MeetingIndex from '../components/meeting_managment/meeting_index.vue'
import Calendar from '../components/calendar/calendar.vue'

Vue.use(Router)
export default new Router({
    mode:'history',
    routes: [
      {
        path: '/Planificacion',
        name: 'Planificacion',
        component: Planificacion,
        beforeEnter(to, from, next) {
          var user = JSON.parse(localStorage.getItem('user'))
          if (user.is_jefe_disciplina) {
            next()
          } else {
            next('/No_autorizado')
          }
        }
      },
      {
        path: '/Planificar',
        name: 'Planificar',
        component: Planificar,
        beforeEnter(to, from, next) {
          var user = JSON.parse(localStorage.getItem('user'))
          if (user.is_jefe_disciplina) {
            next()
          } else {
            next('/No_autorizado')
          }
        }
      },
      {
        path: '/PlanificacionVizualizada',
        name: 'PlanificacionVizualizada',
        component: PlanificacionVizualizada
      },
      {
        path: '/Verificar',
        name: 'Verificar',
        component: Verificar,
        beforeEnter(to, from, next) {
          var planificacion = store.getters.getReglasEspecificas
          var user = JSON.parse(localStorage.getItem('user'))
          if (planificacion!=="" && user.is_jefe_departamento) {
            next()
          } else {
            next('/No_autorizado')
          }
        }
      },
      {
        path: '/',
        name: '',
        component: Verificacion
      },
      {
        path: '/Seguimiento',
        name: 'Seguimiento',
        component: Verificacion
      },
      {
        path: '/No_autorizado',
        name: 'No_autorizado',
        component: Unauthorized
      },
      {
        path: '/PageNotFound',
        name: 'PageNotFound',
        component: PageNotFound
      },
      {
        path: '*',
        redirect: '/PageNotFound'
      }
    ]
})



