        :actionComplete='onActionComplete'  :actionBegin='onActionBegin' :template='gTemplate'>
    <div v-if="loadingList == true" class="sk-fading-circle">
      <div class="sk-circle1 sk-circle"></div>
      <div class="sk-circle2 sk-circle"></div>
      <div class="sk-circle3 sk-circle"></div>
      <div class="sk-circle4 sk-circle"></div>
      <div class="sk-circle5 sk-circle"></div>
      <div class="sk-circle6 sk-circle"></div>
      <div class="sk-circle7 sk-circle"></div>
      <div class="sk-circle8 sk-circle"></div>
      <div class="sk-circle9 sk-circle"></div>
      <div class="sk-circle10 sk-circle"></div>
      <div class="sk-circle11 sk-circle"></div>
      <div class="sk-circle12 sk-circle"></div>
    </div>
      </ejs-listview>
          </div>
          <div class="col-lg-6">
            <div class="row">
        <div class="col-lg-12" style="margin-top: 124px;">
          <div :id='calBlocked'>
           <ejs-button id="primarybtn" class="asset-btn" :isPrimary="true"
                      style="margin-left: -8px;border-radius: 5px;margin-top: -221px;" v-on:click.native="getPrepareMeeting">
            <span class="e-add-icon"></span>
            Crear reunión extraordinaria</ejs-button>
          </div>
          <div class="col-lg-6">
          <!--carga de instancias  -->
          <div v-if="loadingInstances" class="sk-three-bounce" style="margin-top: -54px; margin-left: 49px;">
             <div class="sk-child sk-bounce1"></div>
             <div class="sk-child sk-bounce2"></div>
             <div class="sk-child sk-bounce3"></div>
         </div>
          </div>
          <!--  -->
          <div class="col-lg-6">
          <div style="margin-top: -14px;margin-left: -121px;">
          <span :id="calBlocked" class="ferquency">Frecuencia</span>
          </div>
          </div>
          <hr style="margin-top: 2px;border-color: #dadada; width: 140px;">
           <div class="col-lg-12">
          <ejs-dropdownlist id='intances' :dataSource='intancesData'
           :change='ChangeInstances' :beforeOpen="changeIntancesMeetings" :enabled='instancesEnabled' :placeholder='intancesLabel'
            :popupHeight='heightIntances' :fields='groupIntancesFields'>           
            </ejs-dropdownlist>
            </div>
        </div>
        <div class="col-lg-12">
          <hr style="border-color: #dadada;">
          <div class="col-lg-12 col-md-2 dashboard-card" style="margin-top: 2px;">
            <div :style='styleBlocked'
                 tabindex="0" class="e-card" id="card-request" @click="getPrepareMeeting($event)">
              <div class="e-card-header">
                <div class="e-card-header-caption">
                  <div class="e-card-header-title">Preparar</div>
                  <div class="e-card-sub-title" style="font-size: 17px !important;">reunión</div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-12 col-md-2 dashboard-card" style="margin-top: 8px;">
            <div :style='styleBlocked'
                 tabindex="0" class="e-card" id="card-license" @click="getMakeMeeting($event)">
              <div class="e-card-header">
                <div class="e-card-header-caption">
                  <div class="e-card-header-title">Realizar</div>
                  <div class="e-card-sub-title">reunión</div>
                </div>
              </div>
            </div>
          </div>
        </div>
            </div>
          </div>

        </div>
      </div>
      <hr class="vertical">
      <div class="col-lg-7" >
        <!-- preparar reunion -->
    <PrepareMeeting v-if="prepareMeeting" :meetingSelected="meetingSelected" 
     :meetingTypeSelected="meetingTypeSelected" :isprepareExtra="isprepareExtra" 
     @close="closePrepareMeeting"></PrepareMeeting>
    <!-- Realizar reunion -->
    <MakeMeeting v-if="makeMeeting" :meetingSelected="meetingSelected" 
     :meetingTypeSelected="meetingTypeSelected"   @close="closePrepareMeeting"></MakeMeeting>
      </div>
      </div>

  </div>
</template>
<style>
  #isblocked{
    pointer-events: none;
    color: black;
    opacity: 0.3;
  }
  .ferquency{
    color: #6b6b6b;
    font-size: 15px;
    margin-left: 67px;
    font-family: unset;


  }
  .vertical{
    border: none;
    border-left: 1px solid hsl(218, 29%, 95%);
    height: 73vh;
    border-color: #dadada;
    margin: 13px 0px -540px 0px;

  }
  #calBlocked{
    pointer-events: none;
    color: black;
    opacity: 0.3;
  }
  .dashboard-card {
    padding-left: 10px;
    padding-right: 10px;
  }
  #card-request,
  #card-license {
    height: 80px;
    margin-bottom: 10px;
    text-align: center;
    box-shadow: 0px 2px 8px #7b77885e;
    border: none;
    cursor: pointer;
  }
  #card-request {
    background-color: #B991DA;
  }
  #card-request:hover {
    background-color: #946DB4;
  }
  #card-license {
    background-color: #60D6A2;
  }
  #card-license:hover {
    background-color: #4DAE83;
  }
  .e-card-header-title {
    color: #FFFFFF !important;
    font-size: 26px !important;
    font-family: Roboto;
    font-weight: '1000';
  }
  .e-card-sub-title {
    color: #FFFFFF !important;
    font-family: Roboto;
    font-size: 14px !important;
  }
  /* ListView position alignment */

  #ui-list.e-listview {
    margin: auto;
    max-width: 325px;
    line-height: initial;
    border: 1px solid lightgray;
  }

  /* ListView header alignment */

  #ui-list.e-listview .e-list-header {
    height: 50px
  }

  #ui-list.e-listview .e-list-header .e-text {
    line-height: 18px
  }

  /* ListView template customization */

  #ui-list.e-listview .showUI {
    display: flex;
  }

  #ui-list.e-listview .hideUI {
    display: none;
  }

  #ui-list.e-listview .e-list-item {
    padding: 3px 0;
  }

  #ui-list.e-listview .CD {
    background: rgba(211, 16, 50, 0.51);
  }

  #ui-list.e-listview .CU {
    background: pink;
  }

  #ui-list.e-listview .RD {
    background: lightgreen;
  }

  #ui-list.e-listview .RP {
    background: lightskyblue;
  }

  #ui-list.e-listview .CE {
    background: orange;
  }

  #ui-list.e-listview .RS {
    background: #877ce6;
  }

  /* ListView theme customization */

  .highcontrast #ui-list.e-listview .e-list-item,
  .highcontrast #ui-list.e-listview .e-list-header {
    background: rgb(0, 0, 0);
  }

  .highcontrast #ui-list.e-listview .e-list-item.e-active {
    background: #ffd939;
    color: #000000;
  }
  #date_label {
    display: block;
    width: 248px;
    color: rgba(0, 0, 0, 0.58);
    margin-left: 5px;
  }

  .control_wrapper {
    max-width: 300px;
    margin: 0 auto;
  }

  .e-customStyle span.e-icons.highlight {
    margin-top: -13px;
    display: block;
    margin-left: 4px;
    font-weight: bold;
    font-size: 14px;
  }

  .e-customStyle td span.e-icons.highlight {
    color: #2525fb;
  }

  .e-customStyle .e-other-month span.e-icons.highlight:before {
    content: "";
  }

  .e-customStyle span.e-icons.highlight:before {
    content:"\e790";
    vertical-align: middle;
    margin-right: 3px;
    font-size: 4px;
    position: relative;
    top: -1px;
    font-weight: normal;
  }

  .e-bigger .e-customStyle.e-calendar span.e-icons.highlight:before {
    top: -3px;
  }

  body.highcontrast .e-bigger .e-customStyle.e-calendar span.e-icons.highlight:before {
    top: -2px;
  }

  .e-customStyle .e-selected span.e-icons.highlight:before {
    color: #2861ff;
  }

  body.highcontrast .e-customStyle td.birthday span.e-icons.highlight:before {
    color: #0031ff;
  }

  body.highcontrast .e-customStyle td.farewell span.e-icons.highlight:before {
    color: #001dc7;
  }

  body.highcontrast .e-customStyle td.vacation span.e-icons.highlight:before {
    color: #0013ff;
  }

</style>
<script>
  import Vue from "vue";
  import axios from '../../axios/axios'
  import { ListViewPlugin, Virtualization } from "@syncfusion/ej2-vue-lists";
  import { Browser } from "@syncfusion/ej2-base";
  import { DropDownListPlugin } from "@syncfusion/ej2-vue-dropdowns";
  import {createSpinner, hideSpinner, showSpinner} from "@syncfusion/ej2-popups";
  import { CalendarPlugin } from "@syncfusion/ej2-vue-calendars";
  import { addClass } from "@syncfusion/ej2-base";
  import PrepareMeeting from "./prepare_meeting.vue";
  import MakeMeeting from "./make_meeting.vue";
  import {ToastPlugin} from '@syncfusion/ej2-vue-notifications'
  
  Vue.use(ToastPlugin)
  Vue.use(CalendarPlugin);
  Vue.use(ListViewPlugin);
  Vue.use(DropDownListPlugin);
  let commonData = [];
  let dataSource = {};
  let endTime = undefined;
  let startTime = undefined;
  let liElement = undefined;
  let listObj = undefined;
  export default Vue.extend({
    components:{
      PrepareMeeting,
      MakeMeeting
    },
    data: function() {
      return {  
        // URLs del endpoint
        urlMeetting : '/meeting/meeting/',
        // loading de la lista de tipos de reuniones
        loadingList: false,
//estilo para bloquear la pagina
        isblocked: '',
//aparecer o no las vistas de preparar o realizar reunión
        prepareMeeting: false,
        makeMeeting: false,
//
        loadingInstances: false,
        intancesData: '',
        instancesEnabled: false,
        intancesLabel: 'Seleccione el día',
        heightIntances: '265px',
        groupIntancesFields: {groupBy: 'month', text: 'date_planned_completion', value: 'id_meeting'},
        gTemplate: '<div class="e-list-wrapper e-list-avatar">' +
        '<span  class="e-avatar e-avatar-circle ${icon} ${$imgUrl ? \'\' : \'\' }">' +
        '${icon}</span> <img style="background-color: ${color};" class="e-avatar e-avatar-circle ${$imgUrl ? \'\' : \'\' }" ' +
        'src="${$imgUrl ?  $imgUrl : \' \' }" />' +
        '<span class="e-list-content">${name}</span></div>',
        listData: '',
        meetingTypeSelected:'',
        meetingSelected:'',
        cssClass: 'e-list-template',
        cssClassCal: "e-customStyle",
        header: true,
        title: "Selecione el Tipo de reunión",
        fields: {
          id: "id_meeting_type",
          text: "name"
        },
        heightListMeetingType: 500,
        enableUi: true,
        styleBlocked:'pointer-events: none;color: black;opacity: 0.3;',
         // boton crear reunion extraordinaria
        meetingExtra:'calBlocked',
        calBlocked: 'calBlocked',
        //prepareMeeting
        isprepareExtra: false, 

      };
    },
    mounted(){
      this.getListMeetingTypeDataList();
    },
    methods: {
      changeIntancesMeetings: function(args){
       this.loadingInstances= true
       this.intancesData=[]
       this.$store.dispatch('listMeetingByType',this.meetingTypeSelected.id_meeting_type).then((meetingsInstances) => {
       this.loadingInstances= false
       var intanceList=  this.$store.getters.getlistMeeting
       var auxiliarList= { "meeting": [] }        
       for(var i=0; i< intanceList.length; i++){
         if(new Date(new Intl.DateTimeFormat('en-US').format( new Date(intanceList[i].date_planned_completion)))> new Date() ){
     auxiliarList.meeting.push({'id_meeting':intanceList[i].id_meeting ,
      'date_planned_completion': new Intl.DateTimeFormat('en-GB').format( new Date(intanceList[i].date_planned_completion)),
      'month': this.getMonthIntances(new Date(intanceList[i].date_planned_completion).getMonth())})
       }
       }     
       this.intancesData= auxiliarList.meeting
         }).catch((reason) => {
            this.$root.$children[0].$refs.toastRef.show({
                title: 'Error!',
                content: reason,
                cssClass: 'e-toast-danger',
                icon: 'e-error toast-icons'
              })
         });
      },
      getListMeetingTypeDataList: function(){
       this.loadingList= true;
       this.$store.dispatch('getMeetingTypeListByUser').then((successMessage) => {
       this.listData= this.$store.getters.getMeetingTypeListByUser
       this.loadingList= false;
         }).catch((reason) => {
            this.loadingList= false;
            console.log(this.$root.$children[0])
            this.$root.$children[0].$refs.toastRef.show({
                title: 'Error!',
                content: reason,
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
         });
      },
      selectMeetingTypes: function (args) {
        this.meetingTypeSelected= args.data
        this.meetingExtra=''
        this.instancesEnabled= true;
        this.calBlocked='';
        this.prepareMeeting= false
        this.makeMeeting= false
        this.styleBlocked='pointer-events: none;color: black;opacity: 0.3;';
        document.getElementById("intances").value =''
      },
      closePrepareMeeting: function () {
        this.isblocked= '';
        this.styleBlocked='pointer-events: none;color: black;opacity: 0.3;',
        this.prepareMeeting= false;
        this.makeMeeting= false;
      },

      getMakeMeeting: function () {
        this.isblocked= 'isblocked'
        this.prepareMeeting= false
        this.makeMeeting= true
        alert("Seguro: Faltan "+a+" días para que se realice la reunión")
      },
      getPrepareMeeting: function (args) {
         // reunion ordinaria
        if(args.eventPhase == 3) this.isprepareExtra= false        
       // reunion extraordinaria
       else this.isprepareExtra= true
        this.isblocked= 'isblocked'
        this.prepareMeeting= true
        this.makeMeeting= false
      },
      ChangeInstances: function (args) {
        this.styleBlocked='pointer-events: none;color: black;opacity: 0.3;';
        this.loadingInstances= true
        var jsonBodyRequest = {
               "attr":{
            "id_meeting": args.value
          }
            }
         axios.post(this.urlMeetting , jsonBodyRequest).then(response => {
               this.loadingInstances= false
               this.meetingSelected= response.data[0]
               this.styleBlocked='';
           }).catch(response=>{
               this.loadingInstances= false
               this.styleBlocked=''        
          })
      },
      onActionComplete: function() {
        endTime = new Date();
        document.getElementById("time").innerText =
          endTime.getTime() - startTime.getTime() + " ms";
      },
      onActionBegin: function() {
        startTime = new Date();
        liElement = document.getElementById("ui-list");

        if (Browser.isDevice) {
          liElement.classList.add("ui-mobile");
        }

        createSpinner({
          target: liElement
        });

        listObj = document.getElementById("ui-list").ej2_instances[0];
      },
      onChange: function(e) {
        alert('siiiiiii');

        showSpinner(liElement);
        startTime = new Date();
        listObj.dataSource = dataSource["data" + e.value];
        listObj.dataBind();
        endTime = new Date();
        document.getElementById("time").innerText =
          endTime.getTime() - startTime.getTime() + " ms";
        hideSpinner(liElement);
      },
      specialDate: function(args, name) {
        let span = document.createElement("span");
        span.setAttribute("class", "e-icons highlight");
        args.element.firstElementChild.setAttribute("title", name + "!");
        addClass([args.element], ["e-day", "special", name.toLowerCase()]);
        args.element.setAttribute("data-val", name + "!");
        args.element.setAttribute("title", name + "!");
        args.element.appendChild(span);
      },
      load: function(args) {
        /*Date need to be customized*/
        if (args.date.getDate() === 10) {
          this.specialDate(args, "Consejo_Dirección");
        }
        if (args.date.getDate() === 15) {
          this.specialDate(args, "Consejo_Universitario");
        }
        if (args.date.getDate() === 20) {
          this.specialDate(args, "Reunión_sindicato");
        }
      },
      onValueChange: function(args) {
        let title = "";
        if (args.event) {
          /*Displays selected date in the label*/
          title = args.event.currentTarget.getAttribute("data-val");
          title = title == null ? "" : " ( " + title + " )";
        }
        document.getElementById("date_label").textContent =
          "Selected Value: " + args.value.toLocaleDateString() + title;
      },
      getMonthIntances: function(number){
        var month=''
        switch (number) {
  case 0:
    month= 'Enero';
    break;
  case 1:
    month= 'Febrero';
    break;
  case 2:
    month= 'Marzo';
    break;
  case 3:
    month= 'Abril';
    break;
  case 4:
    month= 'Mayo';
    break;
  case 5:
     month= 'Junio';
     break;
  case 6:
    month= 'Julio';
    break;
  case 7:
    month= 'Agosto';
    break;
  case 8:
    month= 'Septiembre';
    break;
  case 9:
    month= 'Octubre';
    break;
  case 10:
   month= 'Noviembre';
    break;
  case 11:
   month= 'Diciembre';
    break;
 }
 return month;
  },

    },
    provide: {
      listview: [Virtualization]
    }
  });
</script>


 <style type="text/css">
    /*
 *  Usage:
 *
      <div class="sk-three-bounce">
        <div class="sk-child sk-bounce1"></div>
        <div class="sk-child sk-bounce2"></div>
        <div class="sk-child sk-bounce3"></div>
      </div>
 *
 */
.sk-three-bounce {
  margin: 40px auto;
  width: 80px;
  text-align: center; }
  .sk-three-bounce .sk-child {
    width: 8px;
    height: 8px;
    background-color: #333;
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-three-bounce 1.4s ease-in-out 0s infinite both;
            animation: sk-three-bounce 1.4s ease-in-out 0s infinite both; }
  .sk-three-bounce .sk-bounce1 {
    -webkit-animation-delay: -0.32s;
            animation-delay: -0.32s; }
  .sk-three-bounce .sk-bounce2 {
    -webkit-animation-delay: -0.16s;
            animation-delay: -0.16s; }

@-webkit-keyframes sk-three-bounce {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes sk-three-bounce {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

  </style>



