    height: 50px
  }

  #ui-list.e-listview .e-list-header .e-text {
    line-height: 18px
  }

  /* ListView template customization */

  #ui-list.e-listview .showUI {
    display: flex;
  }

  #ui-list.e-listview .hideUI {
    display: none;
  }

  #ui-list.e-listview .e-list-item {
    padding: 3px 0;
  }

  #ui-list.e-listview .CD {
    background: rgba(211, 16, 50, 0.51);
  }

  #ui-list.e-listview .CU {
    background: pink;
  }

  #ui-list.e-listview .RD {
    background: lightgreen;
  }

  #ui-list.e-listview .RP {
    background: lightskyblue;
  }

  #ui-list.e-listview .CE {
    background: orange;
  }

  #ui-list.e-listview .RS {
    background: #877ce6;
  }

  /* ListView theme customization */

  .highcontrast #ui-list.e-listview .e-list-item,
  .highcontrast #ui-list.e-listview .e-list-header {
