              title: 'Error!',
              content: 'La planificación no ha obtenido una solución bajo las reglas anteriormente especificadas. Por favor cambie las reglas',
              cssClass: 'e-toast-danger',
              icon: 'e-error toast-icons'
            })
          }
          else if(response.data[0]==="La ejecución del algoritmo de planificación a fallado. Contacte con su administrador"){
            this.$root.$children[0].$refs.toastRef.show({
              title: 'Error!',
              content: 'La ejecución del algoritmo de planificación a fallado. Contacte con su administrador',
              cssClass: 'e-toast-danger',
              icon: 'e-error toast-icons'
            })
          }
          else{
            let tabObj=document.getElementById('tab_wizard');
            switch (args.target.id) {
              case 'primarybtn3':
