              ControlesPrevia[i].id_person = JSON.parse(ControlesPrevia[i].id_person)
              ControlesPrevia[i].controlado = JSON.parse(ControlesPrevia[i].controlado)
            }
            this.gridData4 = ControlesPrevia
            document.querySelector('#TablaPlanificacionPrevia')['ej2_instances'][0].refresh()

            this.Reglas = JsonPlanificacion
            this.Controles = response.data
