      onGridLoad3: function () {
        if (Browser.isDevice) {
          this.toolbar = [{ text: 'Edit', tooltipText: 'Edit', prefixIcon: 'e-edit', id: 'editTool' }, 'Delete']
          this.$refs.gridObj3.ej2Instances.columns[5].visible = false
        }
      },
      onload: function () {
      },
      onActionBegin: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick(args)
          args.cancel = true
        }
        if (args.requestType === 'delete') {
          this.$refs.gridObj.clearSelection();
        }
      },
      onActionBegin2: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick2(args)
          args.cancel = true
        }
        if (args.requestType === 'delete') {
          this.$refs.gridObj.clearSelection();
        }
      },
      onActionBegin3: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick3(args)
          args.cancel = true
        }
        if (args.requestType === 'delete') {
          this.$refs.gridObj.clearSelection();
        }
      },
      changeNumericControlador: function(args){
        this.CantMaxVariable = args.value
        var grid = this.$refs.gridObj.ej2Instances
        this.$refs.gridObj.clearSelection()
        var jsonTabla = null
        grid.dataSource = []

        //////////////Para eliminar las reglas especificas que q le cambian las cantidades///////////////
        var gridRules = this.$refs.gridObj3.ej2Instances
        var findRules = false
        var gridNuevoRules = []
        for(var i=0;i<gridRules.dataSource.length;i++){
          findRules = false
          for(var j=0;j<this.listExcepcionesControladores.length && !findRules;j++){
            if(this.listExcepcionesControladores[j].person.id_person==gridRules.dataSource[i].profesorcontrolador.id_person){
              findRules = true
              gridNuevoRules.push(gridRules.dataSource[i])
            }
          }
        }
        this.$refs.gridObj3.ej2Instances.dataSource = gridNuevoRules
        //////////////Para eliminar las reglas especificas que q le cambian las cantidades///////////////

        for(var i=0;i<this.Controladores.length;i++){
          var cantAuxiliar = this.findExcepcionAuxiliar(this.Controladores[i])
          if(cantAuxiliar!==""){
            jsonTabla =
              {
                "person": {
                  "id_person": this.Controladores[i].id_person,
                  "name": this.Controladores[i].name
                },
                "cant": cantAuxiliar,
                "id_PC": i+1,
                "catD": this.Controladores[i].teaching_category_ponderada
              }
          }
          else{
            jsonTabla =
              {
                "person": {
                  "id_person": this.Controladores[i].id_person,
                  "name": this.Controladores[i].name
                },
                "cant": this.CantMaxVariable,
                "id_PC": i+1,
                "catD": this.Controladores[i].teaching_category_ponderada
              }
          }
          grid.dataSource.push(jsonTabla)
        }
        this.$refs.gridObj.clearSelection();

        grid.refresh()
      },

      findControladorEnReglaEspecifica: function(row){
        var find = false
        for(var i=0;i<this.gridData3.length && !find;i++){
          if(this.gridData3[i].profesorcontrolador.id_person == row.id_person){
            find = true
          }
        }
        return find
      },

      changeNumericControlado: function(args){
        this.CantVariable = args.value
        var grid = this.$refs.gridObj2.ej2Instances
        this.$refs.gridObj2.clearSelection()
        var jsonTabla = null
        grid.dataSource = []

        //////////////Para eliminar las reglas especificas que q le cambian las cantidades///////////////
        var gridRules = this.$refs.gridObj3.ej2Instances
        var findRules = false
        var gridNuevoRules = []
        for(var i=0;i<gridRules.dataSource.length;i++){
          findRules = false
          for(var j=0;j<this.listExcepcionesControlados.length && !findRules;j++){
            if(this.listExcepcionesControlados[j].person.id_person==gridRules.dataSource[i].profesorcontrolado.id_person){
              findRules = true
              gridNuevoRules.push(gridRules.dataSource[i])
            }
          }
        }
        this.$refs.gridObj3.ej2Instances.dataSource = gridNuevoRules
        //////////////Para eliminar las reglas especificas que q le cambian las cantidades///////////////

        for(var i=0;i<this.Controlados.length;i++){
          var cantAuxiliar = this.findExcepcionAuxiliar2(this.Controlados[i])
          if(cantAuxiliar!==""){
            jsonTabla =
              {
                "person": {
                  "id_person": this.Controlados[i].id_person,
                  "name": this.Controlados[i].name
                },
                "cant": cantAuxiliar,
                "id_PC2": i+1,
                "catD": this.Controlados[i].teaching_category_ponderada
              }
          }
          else if(this.CantVariable>this.Controlados[i].total_hours_plan){
            jsonTabla =
              {
                "person": {
                  "id_person": this.Controlados[i].id_person,
                  "name": this.Controlados[i].name
                },
                "cant": this.Controlados[i].total_hours_plan,
                "id_PC2": i+1,
                "catD": this.Controlados[i].teaching_category_ponderada
              }
          }
          else{
            if(this.CantVariable<2){
              if(this.Controlados[i].teaching_category_ponderada=="Instructor Recién Graduado" ||
                 this.Controlados[i].teaching_category_ponderada=="Instructor" ||
                 this.Controlados[i].teaching_category_ponderada=="Instructor Adjunto" ||
                 this.Controlados[i].teaching_category_ponderada=="Asistente" ||
                 this.Controlados[i].teaching_category_ponderada=="Asistente Adjunto"){
                jsonTabla =
                  {
                    "person": {
                      "id_person": this.Controlados[i].id_person,
                      "name": this.Controlados[i].name
                    },
                    "cant": 2,
                    "id_PC2": i+1,
                    "catD": this.Controlados[i].teaching_category_ponderada
                  }
              }
              else{
                jsonTabla =
                  {
                    "person": {
                      "id_person": this.Controlados[i].id_person,
                      "name": this.Controlados[i].name
                    },
                    "cant": this.CantVariable,
                    "id_PC2": i+1,
                    "catD": this.Controlados[i].teaching_category_ponderada
                  }
              }
            }
            else{
              jsonTabla =
                {
                  "person": {
                    "id_person": this.Controlados[i].id_person,
                    "name": this.Controlados[i].name
                  },
                  "cant": this.CantVariable,
                  "id_PC2": i+1,
                  "catD": this.Controlados[i].teaching_category_ponderada
                }
            }
          }
          grid.dataSource.push(jsonTabla)
        }
        this.$refs.gridObj.clearSelection();
        grid.refresh()
      },
      AddBtnClick: function () {

        if(this.primerBoton){
          this.primerBoton = !this.primerBoton
          var grid = this.$refs.gridObj.ej2Instances
          this.allowSelection = false
          grid.editSettings.allowAdding=false
          grid.editSettings.allowDeleting=false
          grid.editSettings.allowEditing=false
          this.enabledNumeric=true
          document.getElementById('primarybtn').innerHTML="Siguiente"
          grid.refresh()
        }
        else{
          this.primerBoton = !this.primerBoton
          var grid = this.$refs.gridObj.ej2Instances
          this.allowSelection = true
          grid.selectionSettings.type = 'Multiple'
          grid.selectionSettings.mode = 'Row'
          grid.editSettings.allowAdding=true
          grid.editSettings.allowDeleting=true
          grid.editSettings.allowEditing=true
          this.enabledNumeric=false
          document.getElementById('primarybtn').innerHTML="Atrás"
          grid.refresh()
        }
      },
      AddBtnClick2: function () {
        this.dlgShow2 = !this.dlgShow2
        this.isEditing2 = false
      },
      AddBtnClick3: function () {
        this.dlgShow3 = !this.dlgShow3
        this.isEditing3 = false
      },

      //No se para que es (ME PARECE QUE NO HACE NADA)
      editToolClicked: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing = true
          var rows = this.$refs.gridObj.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData = rowObj.data
          console.log(rowObj)
          this.dlgShow = !this.dlgShow
        }
      },
      editToolClicked2: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing2 = true
          var rows = this.$refs.gridObj2.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj2.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj2.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj2.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData2 = rowObj.data
          console.log(rowObj)
          this.dlgShow2 = !this.dlgShow2
        }
      },
      editToolClicked3: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing3 = true
          var rows = this.$refs.gridObj3.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj3.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj3.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj3.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData3 = rowObj.data
          console.log(rowObj)
          this.dlgShow3 = !this.dlgShow3
        }
