      hideDialogConfirmDelete: function () {
        this.$refs.promptDialogConfirmDelete.hide();
      },
      hideDialogConfirmDelete2: function () {
        this.$refs.promptDialogConfirmDelete2.hide();
      },
      DialogConfirmDelete: function () {
        this.$refs.promptDialogConfirmDelete.hide();
        this.isEditing = true
        this.dlgShow = !this.dlgShow
      },
      DialogConfirmDelete2: function () {
        this.$refs.promptDialogConfirmDelete2.hide();
        this.isEditing2 = true
        this.dlgShow2 = !this.dlgShow2
      },

      editBtnClick: function (args) {
        if (this.$refs.gridObj.getSelectedRecords().length >= 1){
          var rows = this.$refs.gridObj.ej2Instances.getSelectedRecords()
          this.loadingEdit = true;
          this.blockedcomponent = 'blockedcomponent'
          if (args.type === 'actionBegin') {
            this.rowData = rows
          }

          var grid = document.querySelector('#TablaControladores')['ej2_instances'][0]
          var gridReglas = document.querySelector('#TablaReglas')['ej2_instances'][0]
          var findControlador = false
          for(var i=0;i<grid.getSelectedRecords().length && !findControlador;i++){
            for(var j=0;j<gridReglas.dataSource.length && !findControlador;j++){
              if(grid.getSelectedRecords()[i].person.id_person == gridReglas.dataSource[j].profesorcontrolador.id_person){
                findControlador = true
              }
            }
          }
          if(findControlador==true){
            this.loadingEdit = false;
            this.blockedcomponent = ''
            this.$refs.promptDialogConfirmDelete.show();
          }
          else{
            this.isEditing = true
            this.dlgShow = !this.dlgShow
            this.loadingEdit = false;
            this.blockedcomponent = ''
          }

        }
        else{
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Advertencia!',
            content: 'Debe seleccionar al menos una excepción para modificar',
            cssClass: 'e-toast-warning',
            icon: 'e-warning toast-icons'
          })
        }

      },
      AnadirExcepcion (value){
        for(var i=0;i<this.rowData.length;i++){
          this.findExcepcion(this.listExcepcionesControladores,this.rowData[i],value)
        }
      },
      AnadirExcep (value){
        for(var i=0;i<this.rowData2.length;i++){
          this.findExcepcion2(this.listExcepcionesControlados,this.rowData2[i],value)
        }
      },
      findExcepcion: function(listExcepcionesControladores,row,value){
        var find = false
        for(var i=0;i<listExcepcionesControladores.length && !find;i++){
          if(listExcepcionesControladores[i].person.id_person == row.person.id_person){
            find = true
            this.listExcepcionesControladores[i].cant = value
          }
        }
        if(find == false){
          this.listExcepcionesControladores.push(row)
        }
      },
      findExcepcion2: function(listExcepcionesControlados,row,value){
        var find = false
        for(var i=0;i<listExcepcionesControlados.length && !find;i++){
          if(listExcepcionesControlados[i].person.id_person == row.person.id_person){
            find = true
            var index = this.FindPosicion(row.person.id_person)
            if(value>this.Controlados[index].total_hours_plan){
              this.listExcepcionesControlados[i].cant = this.Controlados[index].total_hours_plan
            }
            else{
              if(value<2){
                if(this.Controlados[index].teaching_category_ponderada=="Instructor Recién Graduado" ||
                  this.Controlados[index].teaching_category_ponderada=="Instructor" ||
                  this.Controlados[index].teaching_category_ponderada=="Instructor Adjunto" ||
                  this.Controlados[index].teaching_category_ponderada=="Asistente" ||
                  this.Controlados[index].teaching_category_ponderada=="Asistente Adjunto"){
                  this.listExcepcionesControlados[i].cant = 2
                }
                else{
                  this.listExcepcionesControlados[i].cant = value
                }
              }
              else{
                this.listExcepcionesControlados[i].cant = value
              }
            }
          }
        }
        if(find == false){
          this.listExcepcionesControlados.push(row)
        }
      },
      FindPosicion: function(id_person){
        var index = ''
        var find = false
        for(var i=0;i<this.Controlados.length && !find;i++){
          if(this.Controlados[i].id_person==id_person){
            index = i
            find = true
          }
        }
        return index
      },
      findExcepcionAuxiliar: function(row){
        var find = false
        var cant = ''
        for(var i=0;i<this.listExcepcionesControladores.length && !find;i++){
          if(this.listExcepcionesControladores[i].person.id_person == row.id_person){
            find = true
            cant = this.listExcepcionesControladores[i].cant
          }
        }
        return cant
      },
      findExcepcionAuxiliar2: function(row){
        var find = false
        var cant = ''
        for(var i=0;i<this.listExcepcionesControlados.length && !find;i++){
          if(this.listExcepcionesControlados[i].person.id_person == row.id_person){
            find = true
            cant = this.listExcepcionesControlados[i].cant
          }
        }
        return cant
      },
      editBtnClick2: function (args) {
        if (this.$refs.gridObj2.getSelectedRecords().length >= 1){
          var rows = this.$refs.gridObj2.ej2Instances.getSelectedRecords()
          this.loadingEdit = true;
          this.blockedcomponent = 'blockedcomponent'
          if (args.type === 'actionBegin') {
            this.rowData2 = rows
          }

          var grid = document.querySelector('#TablaControlados')['ej2_instances'][0]
          var gridReglas = document.querySelector('#TablaReglas')['ej2_instances'][0]
          var findControlado = false
          for(var i=0;i<grid.getSelectedRecords().length && !findControlado;i++){
            for(var j=0;j<gridReglas.dataSource.length && !findControlado;j++){
              if(grid.getSelectedRecords()[i].person.id_person == gridReglas.dataSource[j].profesorcontrolado.id_person){
                findControlado = true
              }
            }
          }

          if(findControlado==true){
            this.loadingEdit = false;
            this.blockedcomponent = ''
            this.$refs.promptDialogConfirmDelete2.show();
          }
          else{
            this.isEditing2 = true
            this.dlgShow2 = !this.dlgShow2
            this.loadingEdit = false;
            this.blockedcomponent = ''
          }

        }
        else{
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: 'Debe seleccionar al menos una excepción para modificar',
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons'
          })
        }
      },
      editBtnClick3: function (args) {
        if (this.$refs.gridObj3.getSelectedRecords().length === 1){
          this.loadingEdit = true;
          this.blockedcomponent = 'blockedcomponent'
          if (args.type === 'actionBegin') {
            this.rowData3 = args.rowData
          }
//        var target = args.target
//        if (target.classList.contains('e-edit')) {
//          target = target.parentElement
//        }
//        let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(target.parentElement.parentElement.parentElement.getAttribute('data-uid'))
//        this.rowData = rowObj.data
//         let body={
//           "attr":{
//             "id_control": this.rowData['id_control']
//           }
//         }
//         axios.post(this.urlControls+'find_parameters',body).then(response => {
          this.isEditing3 = true
          this.dlgShow3 = !this.dlgShow3
          this.loadingEdit = false;
          this.blockedcomponent = ''
          // })
        }
        else{
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: 'Debe seleccionar una regla para modificar',
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons'
          })
        }
      },
      onDlgClose (){
        this.dlgShow = false
        // var jsonAuxiliar =
        // {
        //   "person": args.person,
        //   "cant": args.cant
        // }
        //
        // this.gridData.push(jsonAuxiliar)
      },
      onDlgClose2 (){
        this.dlgShow2 = false
        // var jsonAuxiliar =
        // {
        //   "person": args.person,
        //   "cant": args.cant
        // }
        //
        // this.gridData.push(jsonAuxiliar)
      },
      onDlgClose3 (){
        this.dlgShow3 = false
        // var jsonAuxiliar =
        // {
        //   "person": args.person,
        //   "cant": args.cant
        // }
        //
        // this.gridData.push(jsonAuxiliar)
      },
      showTooltip: function (args) {
        this.tooltip = new Tooltip({
          beforeOpen: (e) => {
            if (e.target.classList.contains('e-unboundcell') || e.target.lastChild.classList.contains('e-unboundcell')) {
              // e.cancel = true
            }
          },
          content: args.data['Note'].toString() }, args.row)
      }
    },
    provide: {
      grid: [Edit, Group, Filter, PdfExport, ExcelExport, Page, Selection, CommandColumn, Toolbar]
    },
    components: {
      DropDownListComponent,
      AddExcepcion,
      AddExcep,
      AddRules,
      Planificacion
    },
    computed: {
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }

    }
  })
</script>
<style type="text/css">

  .e-error {
    font-size: 12px !important;
    font-weight: 500 !important;
  }

  #toast_type .e-toast-icon.e-icons {
    height: auto;
    font-size: 30px;
  }

  .toast-icons.e-success::before {
    content: "\e701";
  }

  .toast-icons.e-error::before {
    content: "\e700";
  }

  .toast-icons.e-info::before {
    content: "\e704";
  }

  .toast-icons.e-warning::before {
    content: "\e703";
  }


  .vertical{
    border: none;
    border-left: 1px solid hsl(218, 29%, 95%);
    height: 73vh;
    border-color: #dadada;
    margin: 13px 0px -540px 0px;

  }
  /*
*  Usage:
*
    <div class="sk-three-bounce">
      <div class="sk-child sk-bounce1"></div>
      <div class="sk-child sk-bounce2"></div>
      <div class="sk-child sk-bounce3"></div>
    </div>
*
*/
  .sk-three-bounce1 {
    color: #094279;
    margin: 40px auto;
    width: 700px;
    text-align: right; }
  .sk-three-bounce1 .sk-child {
    color: #094279;
    width: 8px;
    height: 8px;
    background-color: #333;
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-three-bounce1 1.4s ease-in-out 0s infinite both;
    animation: sk-three-bounce1 1.4s ease-in-out 0s infinite both; }
  .sk-three-bounce1 .sk-bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s; }
  .sk-three-bounce1 .sk-bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s; }

  @-webkit-keyframes sk-three-bounce1 {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0); }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1); } }

  @keyframes sk-three-bounce1 {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0); }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1); } }

  .input {
    margin: 10px auto;
  }

  .input label {
    display: block;
    color: #333;
    margin-bottom: 6px;
    font-size: 15px;
    font-weight: normal;
  }

  .input input {
    font: inherit;
    width: 100%;
    padding: 6px 12px;
    box-sizing: border-box;
    border: 1px solid #ccc;
  }

  .input input:focus {
    outline: none;
    border: 1px solid #521751;
    background-color: #eee;
  }

</style>

<style>
  @import "@syncfusion/ej2-base/styles/material.css";
  @import "@syncfusion/ej2-inputs/styles/material.css";
</style>



