<template id="view-hardware">
  <div class="container-fluid">
    <div :id="blockedcomponent">
  
      <div id="dialog" style="padding:10px;position:absolute;">
        <add-point :showing="dlgShow" :isEdit="isEditing"
         :meetingSelected="meetingSelected" @listAgendaByMeeting="listAgendaByMeeting" :lengthPoints="lengthPoints"
         :rowData="rowData" @close="onDlgClose" @loadingBlocked="loadingBlocked" :meetingTypeSelected="meetingTypeSelected">
        </add-point>
      </div>
     
      <div class="row top-row">
        <div class="col-xs-12 col-sm-12 col-lg-7 col-md-12" style="margin-top: 6px;padding-left: 28px;">
          <h3> {{headerLabel}}</h3>
          </div>
          <div class="col-xs-12 col-sm-12 col-lg-4 col-md-12" style="margin-top: 6px;padding-left: 28px;">
            <div v-if="!isprepareExtra">
          <ejs-button  id="primarybtn" class="asset-btn styleBtnDelete" 
          v-on:click.native="showDialogConfirmDelete"><span class="e-add-icon"></span>Eliminar</ejs-button>
          <!-- Dialog Confirmar -->
            <ejs-dialog :buttons='promptDlgButtonsConfirm' ref="promptDialogConfirmDelete"
                 isModal='true' :allowDragging='true' v-bind:visible="false" :header='promptHeaderConfirm' 
                  :animationSettings='animationSettings' :showCloseIcon='showCloseIcon'
                  :target='target' :open="dialogOpen" :close="dialogClose" :width='promptWidth'>
        <hr style="margin-top: 0px;">
        <h4 class="row" style="margin-left: 17px; margin-bottom: -20px;">Estás seguro que desea eliminar esta reunión?</h4>
        <br>      
      </ejs-dialog>
      <!-- fin dialog -->
            </div>
          </div>
      </div>
      <hr style="margin-top: 0px">
      <div class="row">
        <div class="col-lg-5">
          <h4>Tipo de Reunión</h4>
        </div>
        <div class="col-lg-7" style="margin-left: -120px">
          <input id="name" name="Name" class="e-input" type="text" disabled>
        </div>
      </div>
      <hr style="margin-right: 119px;border-color: #ded4d4;margin-top: 8px;">

      <div :id="blockedGrid" class="row">
        <div class="col-lg-6">
          <h4>Orden del día</h4>
        </div>
        <div class="col-lg-3" style="margin-left: 150px">
          <ejs-button id="primarybtn" class="asset-btn" :isPrimary="true" v-on:click.native="AddBtnClick"><span
            class="e-add-icon"></span> Añadir punto
          </ejs-button>
        </div>
        <div class="col-xs-12 col-sm-12 col-lg-12 col-md-12" style="margin-top: -17px;">
          <ejs-grid disabled class="r-space" ref="gridObj" :dataSource='gridData'
                    :load="onGridLoad" :actionBegin='onActionBegin' :toolbarClick="editToolClicked"
                    :toolbar="toolbar" :actionComplete='onload' :allowSorting='true' :allowSelection='true'
                    :selectionSettings='selectionSettings' :allowFiltering='true' :allowPaging='true'
                    :allowGrouping='false' allowRowDragAndDrop="true"
                    :pageSettings='pageSettings' :editSettings='editSettings' :filterSettings='filterSettings'
                    :detailTemplate ='details_agenda_template'>
            <e-columns>
              <e-column field='number' headerText='No.' textAlign='Left' width=40   :isPrimaryKey='true'></e-column>
              <e-column field='title' headerText='Punto' :filter='filter' clipMode='EllipsisWithTooltip'
                        width=170></e-column>
              <!-- <e-column field='Responsable' headerText='Responsable' width=100></e-column> -->
              <e-column field='duration' headerText='Duración' :filter='filter' clipMode='EllipsisWithTooltip'
                        width=60></e-column>
              <e-column headerText='Acción' width='70' :commands='commands'></e-column>
            </e-columns>
          </ejs-grid>
        </div>
      </div>

      <hr style="margin-right: 119px;border-color: #ded4d4;margin-top: 8px;">

      <div class="row">
        <div class="col-lg-5">
          <h4 style="font-size: 16px;">Fecha de Planificación</h4>
        </div>
        <div class="col-lg-5" style="margin-left: -100px">
          <ejs-datepicker class="e-input" :min="currentDate" :max="endDate" format="MM-dd-yyyy" :value="valuePlannedDate" id="plannedDate"></ejs-datepicker>
        </div>
      </div>
      <div class="row" style="margin-top: 12px">
        <div class="col-lg-5">
          <h4>Ubicación</h4>
        </div>
        <div class="col-lg-5" style="margin-left: -100px">
          <ejs-dropdownlist id="ubications" name="Ubications" placeholder="Seleccione ubicación"
                            :dataSource="ubicationsList"></ejs-dropdownlist>
        </div>
      </div>
      <div class="row" style="margin-top: 12px">
        <div class="col-lg-5">
          <h4>Invitados</h4>
        </div>
        <div class="col-lg-5" style="margin-left: -100px">
          <ejs-multiselect id="guests" class="e-input" :allowFiltering="true"
                           :dataSource="guestsMeeting" :ignoreAccent="true"
                           :fields='guestMeeting' :query='guestQuery' :change='changeGuets'
                           placeholder="Seleccione invitados"></ejs-multiselect>
                           
        </div>
      </div>

      <div class="row top-row">
        <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6">
          <ejs-button :style="styleBtnSalve" id="primarybtn" class="asset-btn"
                      v-on:click.native="prepareMeeting"><span class="e-add-icon"></span>{{btnPrepareCreate}}
          </ejs-button>
        </div>
        <div class="col-xs-12 col-sm-6 col-lg-2 col-md-6">
          <ejs-button :style="styleBtnCancel" id="primarybtn" class="asset-btn"
                      v-on:click.native="cancelPreparation"><span class="e-add-icon"></span>Cancelar
          </ejs-button>

        </div>
      </div>
    </div>
    <div v-if="changePreparation" class="sk-fading-circle" style="margin-top: -373px;">
      <div class="sk-circle1 sk-circle"></div>
      <div class="sk-circle2 sk-circle"></div>
      <div class="sk-circle3 sk-circle"></div>
      <div class="sk-circle4 sk-circle"></div>
      <div class="sk-circle5 sk-circle"></div>
      <div class="sk-circle6 sk-circle"></div>
      <div class="sk-circle7 sk-circle"></div>
      <div class="sk-circle8 sk-circle"></div>
      <div class="sk-circle9 sk-circle"></div>
      <div class="sk-circle10 sk-circle"></div>
      <div class="sk-circle11 sk-circle"></div>
      <div class="sk-circle12 sk-circle"></div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import axios from '../../axios/axios'
  import {GridPlugin, Edit, Filter, Group, Page, Selection, RowDD, CommandColumn,DetailRow, Toolbar} from '@syncfusion/ej2-vue-grids'
  import {ButtonPlugin} from '@syncfusion/ej2-vue-buttons'
  import {DatePickerPlugin, DateRangePickerPlugin} from '@syncfusion/ej2-vue-calendars'
  import {DropDownListComponent} from '@syncfusion/ej2-vue-dropdowns'
  import AddPoint from '../annual_plan/add_point_plan.vue'
  import {AgendasData} from '../../datasource.js'
  import {TooltipPlugin, Tooltip} from '@syncfusion/ej2-vue-popups'
  import {Predicate} from '@syncfusion/ej2-data'
  import {Browser} from '@syncfusion/ej2-base'
  import {MultiSelectPlugin} from "@syncfusion/ej2-vue-dropdowns";
  import {Query, DataManager, ODataAdaptor} from '@syncfusion/ej2-data';
  import {DialogPlugin} from '@syncfusion/ej2-vue-popups';
  import {ToastPlugin} from '@syncfusion/ej2-vue-notifications'
  import agenda_details from "./agenda_details.vue";


  Vue.use(ToastPlugin)
  Vue.use(DialogPlugin);
  Vue.use(MultiSelectPlugin);
  Vue.use(DateRangePickerPlugin)
  Vue.use(GridPlugin)
  Vue.component(DropDownListComponent)
  Vue.use(DatePickerPlugin)
  Vue.use(ButtonPlugin)
  Vue.use(TooltipPlugin)
  
    var serverDataMeetingType = new DataManager({
    url: 'http://localhost:81/meeting_system/meeting/web/api/users_ldap',
    adaptor: new ODataAdaptor,
    crossDomain: true
  });

  export default Vue.extend({
    data: function () {
      return {
        // URLs
        urlMeetting:"/meeting/meeting/",
        urlAgenda: 'annual_plan/agenda/',
        // informacion extrade la agenda
        lengthPoints:'',
        //fecha de planificacion  
        currentDate:new Intl.DateTimeFormat('en-US').format( new Date()),
        endDate: new Intl.DateTimeFormat('en-US').format( new Date(new Date().getFullYear(),11,28,)),
        valuePlannedDate: '',
        // detalles de los puntos
        details_agenda_template: function () {
          return {template: agenda_details};
      },
        // Header
         headerLabel:"Preparar Reunión",
        blockedcomponent: '',
        blockedGrid:'',
        changePreparation: false,
        btnPrepareCreate:'Preparar',
        // Lista de invitados del LDAP
        guestsMeeting: serverDataMeetingType,
        guestMeeting: {text: 'nombre', value: 'userId'},
        guestQuery: new Query().select(['nombre', 'userId']),
        selectedGuests:[],
        // /////Dialog eliminar       
        animationSettings: {effect: 'Zoom'},
        showCloseIcon: true,
        target: '.control-section',
        promptWidth: '350px',
////////////////////////////////Dialog confirmar plan
        promptDlgButtonsConfirm: [{
          click: this.deleteMeeting,
          buttonModel: {content: 'Confirmar', isPrimary: true}
        }, {click: this.hideDialogConfirmDelete, buttonModel: {content: 'Cancelar'}}],
        promptHeaderConfirm: 'Confirmación',
        // 
        ubicationsList: [
          {id: '1', text: 'Edificio 19'},
          {id: '2', text: 'Decanato'},
          {id: '3', text: 'ViceDecanato'},
          {id: '4', text: 'Pecera 4'},
          {id: '5', text: 'Bunker de Arquitectura'},
          {id: '6', text: 'CITI'}
        ],
        dlgShow: false,
        isEditing: false,
        rowData: null,
        waterMark: 'Select a range',
        gridData: [],
        tooltip: '',
        editSettings: {
          showDeleteConfirmDialog: true,
          allowEditing: true,
          allowAdding: true,
          allowDeleting: true,
          mode: 'Dialog'
        },
        toolbar: undefined,
        editparams: {params: {popupHeight: '300px'}},
        pageSettings: {pageCount: 5, pageSize: 10},
        filterSettings: {type: 'Menu', columns: []},
        filter: {type: 'CheckBox'},
        selectionSettings: {persistSelection: true},
        query: '',
        commands: [{
          type: 'edit',
          buttonOption: {iconCss: 'e-icons e-edit', cssClass: 'e-flat e-noback', click: this.editBtnClick}
        },
          {type: 'Delete', buttonOption: {iconCss: 'e-icons e-delete', cssClass: 'e-flat e-noback'}}],
//        estilos de los botones salvar y guardar
        styleBtnCancel: 'background-color: rgba(220, 216, 216, 0.93) !important;\n' +
        '    border-color: rgb(220, 216, 217) !important;\n' +
        '    color: rgb(60, 57, 57) !important;\n' +
        '    border-radius: 20px;\n' +
        '    height: 33px;\n' +
        '    width: 93px;\n' +
        '    margin-left: 400px;\n' +
        '    margin-top: 15px;',
        styleBtnSalve: '    background-color: rgb(255, 197, 2) !important;\n' +
        '    border-color: rgb(255, 197, 2) !important;\n' +
        '    color: rgb(60, 57, 57) !important;\n' +
        '    border-radius: 20px;\n' +
        '    height: 33px;\n' +
        '    width: 93px;\n' +
        '    margin-left: 405px;\n' +
        '    margin-top: 15px;',
//

      }
    },
    mounted(){
       // this.isprepareExtra
      if(!this.isprepareExtra){
        this.headerLabel= "Preparar reunión"
        var fecha= new Date(new Intl.DateTimeFormat('en-US').format(new Date(this.meetingSelected.date_planned_completion)))
        document.getElementById('name').value = this.meetingTypeSelected.name
        document.getElementById('ubications').value = this.meetingSelected.ubication
        this.valuePlannedDate=fecha;
        // document.getElementById('plannedDate').value = fecha
        this.listAgendaByMeeting()
      }
      else{
        this.blockedGrid='blockedcomponent'
        this.btnPrepareCreate='Crear'
         this.headerLabel= "Crear reunión extraordinaria"
         document.getElementById('name').value = this.meetingTypeSelected.name
         this.valuePlannedDate=this.currentDate
      }
    },
    methods: {
      listAgendaByMeeting: function(){
       this.blockedcomponent = 'blockedcomponent';
       this.changePreparation = true;
       this.$store.dispatch('listPointByMeeting',this.meetingSelected.id_meeting).then((listPoint) => {
       this.gridData= this.$store.getters.getPointListByMeeting
       this.lengthPoints= this.$store.getters.getPointListByMeeting.length
       this.blockedcomponent = '';
       this.changePreparation = false;
         }).catch((reason) => {
            this.blockedcomponent = '';
            this.changePreparation = false;
            console.log(this.$root.$children[0])
            this.$root.$children[0].$refs.toastRef.show({
                title: 'Error!',
                content: reason,
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
         });
      },
      // metodo para eliminar la reunion
      deleteMeeting: function(){
         this.$refs.promptDialogConfirmDelete.hide();
         this.blockedcomponent = 'blockedcomponent';
        this.changePreparation = true;
           if(!this.isprepareExtra){
              axios.post(this.urlMeetting + 'delete/'+this.meetingSelected.id_meeting).then(response => {
             this.$root.$children[0].$refs.toastRef.show({
                  title: 'Éxito!',
                  content: 'La reunión del '+this.meetingSelected.date_planned_completion+' de '+this.meetingTypeSelected.name+' fue eliminada correctamente',
                  cssClass: 'e-toast-success',
                  icon: 'e-success toast-icons',
                  position:  { X: 'Right'}
                })
                 this.$emit('close')
           }).catch(response=>{
                 this.$root.$children[0].$refs.toastRef.show({
                  title: 'Error!',
                  content: 'Existen problemas en el servidor.',
                  cssClass: 'e-toast-danger',
                  icon: 'e-error toast-icons',
                  position:  { X: 'Right'}
                })
               this.$emit('close')

          })
           }
      },
      prepareMeeting: function(){
        this.blockedcomponent = 'blockedcomponent';
        this.changePreparation = true;
        if(this.isprepareExtra){
          this.createMeeting()
        }
        else{
               if(this.validateDataMeeting()){    
               var jsonBodyRequest = {
              "id_meeting_type": this.meetingTypeSelected.id_meeting_type,
              "date_planned_completion": new Date(document.getElementById('plannedDate').value),
              "ubication": document.getElementById('ubications').value,
              // faltan los invitados a la reunion
              }
             axios.post(this.urlMeetting + 'update/'+ this.meetingSelected.id_meeting, jsonBodyRequest).then(response => {
                  this.$root.$children[0].$refs.toastRef.show({
                  title: 'Éxito!',
                  content: this.meetingTypeSelected.name+':'+' La reunión del '+document.getElementById('plannedDate').value+' se ha preparado correctamente',
                  cssClass: 'e-toast-success',
                  icon: 'e-success toast-icons',
                  position:  { X: 'Right'}
                })
                this.tryNotificationsOfPreparation()
                 this.$emit('close')            
            }).catch(response=>{
              this.$root.$children[0].$refs.toastRef.show({
                  title: 'Error!',
                  content: 'Existen problemas en el servidor.',
                  cssClass: 'e-toast-danger',
                  icon: 'e-error toast-icons',
                  position:  { X: 'Right'}
                })
               this.$emit('close')
            })
            }
        }
      },
      // notificar a los usuarios que son responsables de los puntos
      tryNotificationsOfPreparation: function(){
       var agendaBody={
	          "relations":["arrayresponsible_agenda","meeting.meeting_type"],
             "attr": {
							  "id_meeting": this.meetingSelected.id_meeting,
                "observation": "pending"
            }
       }           
        axios.post(this.urlAgenda, agendaBody).then(response => {
          this.changeStatePointAgenda(response.data)
          this.$store.dispatch('addNotifications', response.data)
        })
      },
      // metodo para cambiarle el estado a los puntos de la agenda
      changeStatePointAgenda: function(agendaList){        
       for(var i=0; i< agendaList.length; i++){   
         var agendaPointBody={"observation": "observed"}       
           axios.post(this.urlAgenda + "update/"+ agendaList[i].id_agenda,agendaPointBody).then(response => {          
        })
        }
      },
      // metodo para crear la reunion
      createMeeting: function(){
        if(this.validateDataMeeting()){      
          var jsonBodyRequest = {
              "id_meeting_type": this.meetingTypeSelected.id_meeting_type,
              "date_planned_completion": document.getElementById('plannedDate').value,
              "ubication": document.getElementById('ubications').value,
              // faltan los invitados a la reunion
            }
           axios.post(this.urlMeetting + 'create', jsonBodyRequest).then(response => {
          this.$store.dispatch('addNotifications',{
    "notifications": "Se ha creado una reunión extraordinaria de tipo "+this.meetingTypeSelected.name+" para el "+jsonBodyRequest.date_planned_completion,
	  "type": "createdExtraMeeting",
	  "current_date": new Intl.DateTimeFormat('en-US').format( new Date()),
	  "user": "system",
  	"state": "pending",
		"header": "Reunión extraordinaria",
		"img_src": "assets/calendar_48dp.png"
          })
             this.$root.$children[0].$refs.toastRef.show({
                  title: 'Éxito!',
                  content: 'La reunión del '+document.getElementById('plannedDate').value+' fue creada correctamente',
                  cssClass: 'e-toast-success',
                  icon: 'e-success toast-icons',
                  position:  { X: 'Right'}
                })
                this.$root.$children[0].$refs.toastRef.show({
                  title: 'Información!',
                  content: 'Ya puede preparar o realizar esta reunión. Selecciónala en el listado de frecuencia de su Tipo de Reunión',
                  cssClass: 'e-toast-info',
                  icon: 'e-info toast-icons',
                  position:  { X: 'Right'},
                  timeOut: 50000
                })
                 this.$emit('close')
           }).catch(response=>{
                 this.$root.$children[0].$refs.toastRef.show({
                  title: 'Error!',
                  content: 'Existen problemas en el servidor.',
                  cssClass: 'e-toast-danger',
                  icon: 'e-error toast-icons',
                  position:  { X: 'Right'}
                })
               this.$emit('close')

          })  
        }
      },
      validateDataMeeting: function()
      {
        var validate= false;
        var datePlaned= document.getElementById('plannedDate').value
        var ubication= document.getElementById('ubications').value
        if(datePlaned== '' || ubication== '' ){
           this.$root.$children[0].$refs.toastRef.show({
                  title: 'Advertencia!',
                  content: 'Verifique sus datos por favor',
                  cssClass: 'e-toast-danger',
                  icon: 'e-error toast-icons',
                  position:  { X: 'Right'}
                })
                this.blockedcomponent = '';
                this.changePreparation = false;
        }
        else {validate= true}
        return validate
                
      },
      // metodos del dialog de eliminar la reunion
       hideDialogConfirmDelete: function () {
        this.$refs.promptDialogConfirmDelete.hide();
      },
       showDialogConfirmDelete: function () {
        this.$refs.promptDialogConfirmDelete.show();
      },
       // ////////////////////
        changeGuets: function (args) {
        this.selectedGuests = args.value
      },
      loadingBlocked: function(blocked, loading){
        this.blockedcomponent = 'blockedcomponent';
        this.changePreparation = true;
      },
      salvePreparation: function () {
        this.blockedcomponent = 'blockedcomponent';
        this.changePreparation = true;
        setTimeout(() => {
          this.$emit('close')
        },4000);
      },
      cancelPreparation: function () {
        this.blockedcomponent = 'blockedcomponent';
        this.changePreparation = true;
        this.$emit('close')
      },
      onGridLoad: function () {
        if (Browser.isDevice) {
          this.toolbar = [{text: 'Edit', tooltipText: 'Edit', prefixIcon: 'e-edit', id: 'editTool'}, 'Delete']
          this.$refs.gridObj.ej2Instances.columns[10].visible = false
        }
      },
      onload: function () {
      },
      onActionBegin: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick(args)
          args.cancel = true
        }
        if (args.requestType === 'delete'){        
        this.blockedcomponent = 'blockedcomponent';
        this.changePreparation = true;
        this.$store.dispatch('deleteAgenda',args.data[0].id_agenda).then((message) => {
          this.listAgendaByMeeting();
            this.$root.$children[0].$refs.toastRef.show({
                  title: 'Éxito!',
                  content: "El punto se ha eliminado correctamente",
                  cssClass: 'e-toast-success',
                  icon: 'e-success toast-icons',                  
                })
        this.blockedcomponent = '';
        this.changePreparation = false;
         }).catch((reason) => {            
            this.$root.$children[0].$refs.toastRef.show({
                title: 'Error!',
                content: "No se pudo eliminar el punto. Error en el servior!",
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
        this.blockedcomponent = '';
        this.changePreparation = false;
         });

        }
      },
      AddBtnClick: function () {
        this.dlgShow = !this.dlgShow
        this.isEditing = false
      },
      editToolClicked: function (args) {
        if (args.item.id === 'editTool') {
          this.isEditing = true
          var rows = this.$refs.gridObj.ej2Instances.getSelectedRows()[0]
          if (!rows) {
            this.$refs.gridObj.ej2Instances.editModule.showDialog('EditOperationAlert', this.$refs.gridObj.ej2Instances.editModule.alertDObj)
            return
          }
          let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(rows.getAttribute('data-uid'))
          this.rowData = rowObj.data
          this.dlgShow = !this.dlgShow
        }
      },
      editBtnClick: function (args) {
        this.isEditing = true
        this.dlgShow = !this.dlgShow
        if (args.type === 'actionBegin') {
          this.rowData = args.rowData
          return
        }
        var target = args.target
        if (target.classList.contains('e-edit')) {
          target = target.parentElement
        }
        let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(target.parentElement.parentElement.parentElement.getAttribute('data-uid'))
        this.rowData = rowObj.data
      },
      onDlgClose() {
        this.dlgShow = false
      },
     showTooltip: function (args) {
        this.tooltip = new Tooltip({
          beforeOpen: (e) => {
            if (e.target.classList.contains('e-unboundcell') || e.target.lastChild.classList.contains('e-unboundcell')) {
              // e.cancel = true
            }
          },
          content: args.data['Note'].toString()
        }, args.row)
      },


    },
     computed: {
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }
    },
    provide: {
      grid: [Edit, Group, Filter, Page, Selection, CommandColumn, Toolbar, RowDD, DetailRow]
    },
    components: {
      DropDownListComponent,
      AddPoint
    },
      props: {
      /** Mostrar si es preparar reunion ordinario o extraordinaria */
      isprepareExtra: {
        type: Boolean,
        required: true,
        default: false
      },
      meetingTypeSelected: {
        type: Object,
        required: false,
        default: null
      },
      meetingSelected: {
        type: Object,
        required: false,
        default: null
      },
      
    },
  })
</script>

<style scoped>
   .styleBtnDelete {
    margin-top: -2px;
    margin-left: 131px;
    background-color: #ff2b2bed !important;
    border-color: #f92f2f !important;
    color: #fff8f8 !important;
    border-radius: 20px;
    height: 33px;width: 93px;
   }

  #blockedcomponent {
    pointer-events: none;
    color: black;
    opacity: 0.3;
  }

  .vertical {
    border: none;
    border-left: 1px solid hsl(218, 29%, 95%);
    height: 73vh;
    border-color: #dadada;
    margin: 13px 0px -540px 0px;

  }

  .datepicker-panel {
    float: right;
    margin-right: 20px;
  }

  .datepicker-panel-group {
    display: flex;
  }

  .content-wrapper {
    width: 100%;
    margin: 15px auto;
    min-width: 185px;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }
  }

  .e-add-icon {
    background-repeat: no-repeat;
    /* background-image: url('../assets/Plus.png'); */
  }
</style>
<style>
  .sk-fading-circle {
    margin: 40px auto;
    width: 40px;
    height: 40px;
    position: relative;
  }

  .sk-fading-circle .sk-circle {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  .sk-fading-circle .sk-circle:before {
    content: '';
    display: block;
    margin: 0 auto;
    width: 15%;
    height: 15%;
    background-color: #333;
    border-radius: 100%;
    -webkit-animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
    animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
  }

  .sk-fading-circle .sk-circle2 {
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    transform: rotate(30deg);
  }

  .sk-fading-circle .sk-circle3 {
    -webkit-transform: rotate(60deg);
    -ms-transform: rotate(60deg);
    transform: rotate(60deg);
  }

  .sk-fading-circle .sk-circle4 {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
  }

  .sk-fading-circle .sk-circle5 {
    -webkit-transform: rotate(120deg);
    -ms-transform: rotate(120deg);
    transform: rotate(120deg);
  }

  .sk-fading-circle .sk-circle6 {
    -webkit-transform: rotate(150deg);
    -ms-transform: rotate(150deg);
    transform: rotate(150deg);
  }

  .sk-fading-circle .sk-circle7 {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
  }

  .sk-fading-circle .sk-circle8 {
    -webkit-transform: rotate(210deg);
    -ms-transform: rotate(210deg);
    transform: rotate(210deg);
  }

  .sk-fading-circle .sk-circle9 {
    -webkit-transform: rotate(240deg);
    -ms-transform: rotate(240deg);
    transform: rotate(240deg);
  }

  .sk-fading-circle .sk-circle10 {
    -webkit-transform: rotate(270deg);
    -ms-transform: rotate(270deg);
    transform: rotate(270deg);
  }

  .sk-fading-circle .sk-circle11 {
    -webkit-transform: rotate(300deg);
    -ms-transform: rotate(300deg);
    transform: rotate(300deg);
  }

  .sk-fading-circle .sk-circle12 {
    -webkit-transform: rotate(330deg);
    -ms-transform: rotate(330deg);
    transform: rotate(330deg);
  }

  .sk-fading-circle .sk-circle2:before {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
  }

  .sk-fading-circle .sk-circle3:before {
    -webkit-animation-delay: -1s;
    animation-delay: -1s;
  }

  .sk-fading-circle .sk-circle4:before {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
  }

  .sk-fading-circle .sk-circle5:before {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s;
  }

  .sk-fading-circle .sk-circle6:before {
    -webkit-animation-delay: -0.7s;
    animation-delay: -0.7s;
  }

  .sk-fading-circle .sk-circle7:before {
    -webkit-animation-delay: -0.6s;
    animation-delay: -0.6s;
  }

  .sk-fading-circle .sk-circle8:before {
    -webkit-animation-delay: -0.5s;
    animation-delay: -0.5s;
  }

  .sk-fading-circle .sk-circle9:before {
    -webkit-animation-delay: -0.4s;
    animation-delay: -0.4s;
  }

  .sk-fading-circle .sk-circle10:before {
    -webkit-animation-delay: -0.3s;
    animation-delay: -0.3s;
  }

  .sk-fading-circle .sk-circle11:before {
    -webkit-animation-delay: -0.2s;
    animation-delay: -0.2s;
  }

  .sk-fading-circle .sk-circle12:before {
    -webkit-animation-delay: -0.1s;
    animation-delay: -0.1s;
  }

  @-webkit-keyframes sk-circleFadeDelay {
    0%, 39%, 100% {
      opacity: 0;
    }
    40% {
      opacity: 1;
    }
  }

  @keyframes sk-circleFadeDelay {
    0%, 39%, 100% {
      opacity: 0;
    }
    40% {
      opacity: 1;
    }
  }
</style>



