import {
  meetingData,
} from '../../../datasource.js'
import { DataManager, Query, Predicate } from '@syncfusion/ej2-data'
const state={
  meetingInput: meetingData,
}
const getters={
  meetingStatus: state => {
    return new DataManager(state.meetingInput).executeLocal(new Query().group('Nombre'))
  },
  ConsejoDirData: state => {
    return new DataManager(state.meetingInput).executeLocal(new Query().where(new Predicate('Nombre', 'equal', 'Consejo de dirección')).group('Estado'))
  },
  ConsejoSindData: state => {
    return new DataManager(state.meetingInput).executeLocal(new Query().where(new Predicate('Nombre', 'equal', 'Consejo de Sindicato')).group('Estado'))
  },
  ReunProyData: state => {
    return new DataManager(state.meetingInput).executeLocal(new Query().where(new Predicate('Nombre', 'equal', 'Reunión de proyecto')).group('Estado'))
  },
  ReuniDecanoData: state => {
    return new DataManager(state.meetingInput).executeLocal(new Query().where(new Predicate('Nombre', 'equal', 'Reunión con el Decano')).group('Estado'))
  },
}

export default {
  state,
  getters
}



