import axios from '../../../axios/axios-auth'
import {
  requestData,
  listData,
  employeeData,
  TaskData
} from '../../../datasource.js'
import { DataManager, Query, Predicate } from '@syncfusion/ej2-data'


const state = {
  taskInput: TaskData,
  urlTask: 'task/task/',
  urlMeetingType: 'meeting_type/meeting_type/',
  urlTaskResponsible: 'task/responsible_task/',
  urlPlanificacion: '/controls/web/planificacion/planificacion/',
  urlControl: '/controls/web/planificacion/control/',
  PlanificacionesPendientes: [],
  ControlesRealizadosPendientes: [],
  ControlesRecibidosPendientes: [],
  listTaskByMeeting: [],
  ////// estado de los acuerdos
  observedTask: [],
  developTask: [],
  rejectedTask: [],
  closedTask: [],
  verificationTask: [],
  //
  listTaskUserByState: [],
  listCheckTask: []


}
const getters = { //
  getListCheckTask(state) {
    return state.listCheckTask
  },
  getPlanificacionesPendientes(state) {
    return state.PlanificacionesPendientes
  },
  getControlesRealizadosPendientes(state) {
    return state.ControlesRealizadosPendientes
  },
  getControlesRecibidosPendientes(state) {
    return state.ControlesRecibidosPendientes
  },
  getListTaskUserByState(state) {
    return state.listTaskUserByState
  },
  //estados de los acuerdos
  getObservedTaske(state) {
    return state.observedTask
  },
  getDevelopTask(state) {
    return state.developTask
  },
  getRejectedTask(state) {
    return state.rejectedTask
  },
  getClosedTask(state) {
    return state.closedTask
  },
  getVerificationTask(state) {
    return state.verificationTask
  },
  ////
  pendingTask: state => {
    return new DataManager(state.taskInput).executeLocal(new Query().where(new Predicate('Status', 'equal', 'Pending'))).length
  },
  getUrlTaskResponsible(state) {
    return state.urlTaskResponsible
  },
  getListTaskByMeeting(state) {
    return state.listTaskByMeeting
  },
  getUrlTask(state) {
    return state.urlTask
  },
  getUrlMeetingType(state) {
    return state.urlMeetingType
  },
}
const mutations = {
  setListCheckTask(state, value) {
    state.listCheckTask = value;
  },
  setPlanificacionesPendientes(state, value) {
    state.PlanificacionesPendientes = value;
  },
  setControlesRealizadosPendientes(state, value) {
    state.ControlesRealizadosPendientes = value;
  },
  setControlesRecibidosPendientes(state, value) {
    state.ControlesRecibidosPendientes = value;
  },
  setListTaskUserByState(state, value) {
    state.listTaskUserByState = value;
  },
  // estados de los acuerdos
  setObservedTask(state, value) {
    state.observedTask = value;
  },
  setDevelopTask(state, value) {
    state.developTask = value;
  },
  setRejectedTask(state, value) {
    state.rejectedTask = value;
  },
  setClosedTask(state, value) {
    state.closedTask = value;
  },
  setVerificationTask(state, value) {
    state.verificationTask = value;
  },
  //
  setListTaskByMeeting(state, value) {
    state.listTaskByMeeting = value;
  },
}
const actions = {
  updateTask(context, bodyRequest) {
    return new Promise((resolve, reject) => {
      axios.post(context.getters.getUrlTask + 'update/' + bodyRequest.id_task, bodyRequest.taskEdit).then(response => {
        resolve(Object.assign({}, response.data.model))
      }).catch(response => {
        reject(new Error('No se pudo actualizar el acuerdo. Error en el servior!'))
      })
    })
  },

  listTaskByCheck(context, MeetingType) {
    return new Promise((resolve, reject) => {
      var bodyRequest = {
        "relations": ["arraymeeting.arrayagenda.arraytask.agenda.meeting.meeting_type",
          "arraymeeting.arrayagenda.arraytask.arrayresponsible_task"
        ],
        "attr": {
          "id_meeting_type": MeetingType.id_meeting_type
        }
      }
      context.dispatch('listMembersByUser', MeetingType).then((listTask) => {
        axios.post(context.getters.getUrlMeetingType, bodyRequest).then(response => {
          var taskList = []
          for (let m = 0; m < response.data[0].arraymeeting.length; m++) {
            for (let a = 0; a < response.data[0].arraymeeting[m].arrayagenda.length; a++) {
              for (let t = 0; t < response.data[0].arraymeeting[m].arrayagenda[a].arraytask.length; t++) {
                var task = response.data[0].arraymeeting[m].arrayagenda[a].arraytask[t]
                if (task.state == "pending" || task.state == "observed" || task.state == "developed" || task.state == "rejected")
                  task.state = "Pendiente"
                else if (task.state == "closed")
                  task.state = "Cumplido"
                else if (task.state == "unredeemed")
                  task.state = "Incumplido"
                for (let k = 0; k < task.arrayresponsible_task.length; k++) {
                  for (let n = 0; n < context.getters.getListMembersByUser.length; n++) {
                    if (task.arrayresponsible_task[k].id_user_ldap == context.getters.getListMembersByUser[n].userId) {
                      task.arrayresponsible_task[k].id_user_ldap = context.getters.getListMembersByUser[n].nombre
                      if (k == 0)
                        task.checked = context.getters.getListMembersByUser[n].nombre
                    }
                    if (task.id_checker == context.getters.getListMembersByUser[n].userId)
                      task.id_checker = context.getters.getListMembersByUser[n].nombre
                  }
                }
                if (task.arrayresponsible_task.length > 1)
                  task.checked += ",+" + (task.arrayresponsible_task.length - 1)

                task.compliance_date_planned = new Date(task.compliance_date_planned)
                taskList.push(task)
              }
            }
          }

          context.commit('setListCheckTask', taskList)
          resolve(Object.assign({}, response.data.model))
        })
          .catch(response => {
            reject(new Error('No se pudo cargar la lista de acuerdos. Error en el servior!'))
          })
      })
    })
  },
  listTaskByMeetingAgenda(context, idMeeting) {
    return new Promise((resolve, reject) => {
      var bodyRequest = {
        "relations": ["arraytask.agenda.meeting.meeting_type", "arraytask.arrayresponsible_task"],
        "attr": {
          "id_meeting": idMeeting
        }
      }
      axios.post(context.getters.getUrlAgenda, bodyRequest).then(response => {
        var taskList = []
        for (let i = 0; i < response.data.length; i++)
          for (let j = 0; j < response.data[i].arraytask.length; j++)
            taskList.push(response.data[i].arraytask[j])

        context.commit('setListTaskByMeeting', taskList)
        resolve(Object.assign({}, response.data.model))
      })
        .catch(response => {
          reject(new Error('No se pudo cargar la lista de acuerdos. Error en el servior!'))
        })
    })
  },
  deleteTask(context, idTask) {
    return new Promise((resolve, reject) => {
      axios.post(context.getters.getUrlTask + 'delete_by_id', [idTask]).then(response => {
        resolve(Object.assign({}, response))
      }).catch(response => {
        reject(new Error('No se pudo eliminar el acuerdo. Error en el servior!'))
      })
    })
  },
  stateTaskControlesRealizados(context, request) {
    return new Promise((resolve, reject) => {
        axios.post(context.state.urlControl, request).then(response => {
          // states
          context.commit('setControlesRealizadosPendientes', response.data)
          resolve(Object.assign({}, response.data))

        }).catch(response => {
          reject(new Error('No se pudo obtener la agenda de este usuario. Error en el servior!'))
        })
    })
  },
  stateTaskControlesRecibidos(context, request) {
    return new Promise((resolve, reject) => {
        axios.post(context.state.urlControl, request).then(response => {
          // states
          context.commit('setControlesRecibidosPendientes', response.data)
          resolve(Object.assign({}, response.data))

        }).catch(response => {
          reject(new Error('No se pudo obtener la agenda de este usuario. Error en el servior!'))
        })
    })
  },
  stateTask(context, request) {
    return new Promise((resolve, reject) => {
      if(context.getters.user.is_jefe_departamento==true){
        axios.post(context.state.urlPlanificacion, request).then(response => {
          // states
          context.commit('setPlanificacionesPendientes', response.data)
          resolve(Object.assign({}, response.data))

        }).catch(response => {
          reject(new Error('No se pudo obtener la agenda de este usuario. Error en el servior!'))
        })
      }
      else{
        context.commit('setPlanificacionesPendientes', [])
        resolve(Object.assign({}, context.getters.getPlanificacionesPendientes))
      }
    })
  },
  verificationsTask(context, request) {
    return new Promise((resolve, reject) => {
      axios.post(context.getters.getUrlTask, request).then(response => {
        for (let i = 0; i < response.data.length; i++) {
          // Esto es para ponerle el nombre del verificador al acuerdo
          for (let j = 0; j < context.getters.getUserListLDAP.length; j++)
            if (response.data[i].id_checker == context.getters.getUserListLDAP[j].userId)
              response.data[i].id_checker = context.getters.getUserListLDAP[j].nombre
          // Esto es para ponerle el nombre a los responsables del acuerdo
          for (let k = 0; k < response.data[i].arrayresponsible_task.length; k++) {
            for (let n = 0; n < context.getters.getUserListLDAP.length; n++) {
              if (response.data[i].arrayresponsible_task[k].id_user_ldap == context.getters.getUserListLDAP[n].userId) {
                response.data[i].arrayresponsible_task[k].id_user_ldap = context.getters.getUserListLDAP[n].nombre
              }

            }
          }
          // Esto es para ponerle el nombre a los Participantes del acuerdo
          for (let s = 0; s < response.data[i].arrayparticipant_task.length; s++) {
            for (let t = 0; t < context.getters.getUserListLDAP.length; t++) {
              if (response.data[i].arrayparticipant_task[s].id_user_ldap == context.getters.getUserListLDAP[t].userId) {
                response.data[i].arrayparticipant_task[s].id_user_ldap = context.getters.getUserListLDAP[t].nombre
              }

            }
          }
        }
        context.commit('setVerificationTask', response.data)
        resolve(Object.assign({}, response))
      })
    }).catch(response => {
      reject(new Error('No se pudo obtener los acuerdos de este usuario. Error en el servior!'))
    })
  },

  listTaskByState(context, request) {
    return new Promise((resolve, reject) => {
      let listTask = [];
      var listObser = [];
      var listDev = [];
      var listReject = [];
      var listClosed = []
      context.commit('setListPointUserByState', [])
      axios.post(context.getters.getUrlTaskResponsible, request.body).then(response => {

        for (let i = 0; i < response.data.length; i++) {
          // Esto es para ponerle el nombre del verificador al acuerdo
          for (let j = 0; j < context.getters.getUserListLDAP.length; j++)
            if (response.data[i].task.id_checker == context.getters.getUserListLDAP[j].userId)
              response.data[i].task.id_checker = context.getters.getUserListLDAP[j].nombre
          // Esto es para ponerle el nombre a los responsables del acuerdo
          for (let k = 0; k < response.data[i].task.arrayresponsible_task.length; k++) {
            for (let n = 0; n < context.getters.getUserListLDAP.length; n++) {
              if (response.data[i].task.arrayresponsible_task[k].id_user_ldap == context.getters.getUserListLDAP[n].userId) {
                response.data[i].task.arrayresponsible_task[k].id_user_ldap = context.getters.getUserListLDAP[n].nombre
              }

            }
          }
          // Esto es para ponerle el nombre a los Participantes del acuerdo
          for (let s = 0; s < response.data[i].task.arrayparticipant_task.length; s++) {
            for (let t = 0; t < context.getters.getUserListLDAP.length; t++) {
              if (response.data[i].task.arrayparticipant_task[s].id_user_ldap == context.getters.getUserListLDAP[t].userId) {
                response.data[i].task.arrayparticipant_task[s].id_user_ldap = context.getters.getUserListLDAP[t].nombre
              }

            }
          }
          if (response.data[i].task.state == request.state)
            listTask.push(response.data[i].task)
          if (response.data[i].task.state == 'observed')
            listObser.push(response.data[i]).task
          else if (response.data[i].task.state == 'developed')
            listDev.push(response.data[i]).task
          else if (response.data[i].task.state == 'rejected')
            listReject.push(response.data[i]).task
          else if (response.data[i].task.state == 'closed')
            listClosed.push(response.data[i]).task
        }
        context.commit('setListTaskUserByState', listTask)
        // states
        context.commit('setObservedTask', listObser)
        context.commit('setDevelopTask', listDev)
        context.commit('setRejectedTask', listReject)
        context.commit('setClosedTask', listClosed)

        resolve(Object.assign({}, listTask))
      }).catch(response => {
        reject(new Error('No se pudo obtener los acuerdos de este usuario. Error en el servior!'))
      })
    })
  },


}

export default {
  state,
  mutations,
  actions,
  getters
}



