            this.$store.dispatch('stateTaskControlesRecibidos',jsonControlesRecibir).then((listPoint) => {
              this.data= [
                { id: 'planificacion', text: 'Planificaciones', messages: this.$store.getters.getPlanificacionesPendientes.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos'},
                { id: 'controlesRealizar', text: 'Controles por realizar', messages: this.$store.getters.getControlesRealizadosPendientes.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos'},
                { id: 'controlesRecibir', text: 'Controles por recibir', messages: this.$store.getters.getControlesRecibidosPendientes.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos'}
                // { id: 'closed', text: 'Acuerdos cumplidos', messages: this.$store.getters.getClosedTask.length, badge: 'e-badge e-badge-success', icons: 'primary', type: 'Acuerdos'},
                // { id: 'closed', text: 'Acuerdos incumplidos', messages: this.$store.getters.getRejectedTask.length, badge: 'e-badge e-badge-danger', icons: 'primary', type: 'Acuerdos'},
                // { id: 'observed', text: 'Acuerdos pendientes', messages: this.$store.getters.getObservedTaske.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos/Verificador'},
                // { id: 'developed', text: 'Acuerdos en desarrollo', messages:this.$store.getters.getDevelopTask.length, badge: 'e-badge e-badge-secondary', icons: 'social', type: 'Acuerdos/Verificador' },
                // { id: 'rejected', text: 'Acuerdos rechazados', messages: this.$store.getters.getRejectedTask.length, badge: 'e-badge e-badge-danger', icons: 'promotion', type: 'Acuerdos/Verificador' },
                // { id: 'closed', text: 'Acuerdos cerrados', messages: this.$store.getters.getClosedTask.length, badge: 'e-badge e-badge-success', icons: 'updates', type: 'Acuerdos/Verificador' },
                // { id: 'verification', text: 'Acuerdos para verificar', messages: this.$store.getters.getVerificationTask.length, badge: 'e-badge e-badge-success', icons: 'updates', type: 'Verificacón' },
              ]
              this.loadingListAgenda=false
            }).catch((reason) => {
              this.loadingListAgenda=false
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Error!',
                content: reason,
                cssClass: 'e-toast-danger',
                icon: 'e-error toast-icons'
              })
            });
          }).catch((reason) => {
            this.loadingListAgenda=false
            this.$root.$children[0].$refs.toastRef.show({
              title: 'Error!',
              content: reason,
              cssClass: 'e-toast-danger',
              icon: 'e-error toast-icons'
            })
          });
        }).catch((reason) => {
          this.loadingListAgenda=false
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: reason,
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons'
          })
        });
      },

      showNumberStateBadge: function(){
        this.loadingListAgenda=true
        var jsonBody= {"attr": {"departamento": this.user.departamento, "estado": "Pendiente" }}
        var jsonControlesRealizar = {"attr":{"ci_controlador": this.user.ci, "estado":"Pendiente" }}
        var jsonControlesRecibir = {"attr":{"ci_controlado": this.user.ci, "estado":"Pendiente" }}
        this.$store.dispatch('stateTask',jsonBody).then((listPoint) => {
          this.$store.dispatch('stateTaskControlesRealizados',jsonControlesRealizar).then((listPoint) => {
            this.$store.dispatch('stateTaskControlesRecibidos',jsonControlesRecibir).then((listPoint) => {
              this.data= [
                { id: 'planificacion', text: 'Planificaciones', messages: this.$store.getters.getPlanificacionesPendientes.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos'},
                { id: 'controlesRealizar', text: 'Controles por realizar', messages: this.$store.getters.getControlesRealizadosPendientes.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos'},
                { id: 'controlesRecibir', text: 'Controles por recibir', messages: this.$store.getters.getControlesRecibidosPendientes.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos'}
                // { id: 'closed', text: 'Acuerdos cumplidos', messages: this.$store.getters.getClosedTask.length, badge: 'e-badge e-badge-success', icons: 'primary', type: 'Acuerdos'},
                // { id: 'closed', text: 'Acuerdos incumplidos', messages: this.$store.getters.getRejectedTask.length, badge: 'e-badge e-badge-danger', icons: 'primary', type: 'Acuerdos'},
                // { id: 'observed', text: 'Acuerdos pendientes', messages: this.$store.getters.getObservedTaske.length, badge: 'e-badge e-badge-primary', icons: 'primary', type: 'Acuerdos/Verificador'},
                // { id: 'developed', text: 'Acuerdos en desarrollo', messages:this.$store.getters.getDevelopTask.length, badge: 'e-badge e-badge-secondary', icons: 'social', type: 'Acuerdos/Verificador' },
                // { id: 'rejected', text: 'Acuerdos rechazados', messages: this.$store.getters.getRejectedTask.length, badge: 'e-badge e-badge-danger', icons: 'promotion', type: 'Acuerdos/Verificador' },
                // { id: 'closed', text: 'Acuerdos cerrados', messages: this.$store.getters.getClosedTask.length, badge: 'e-badge e-badge-success', icons: 'updates', type: 'Acuerdos/Verificador' },
                // { id: 'verification', text: 'Acuerdos para verificar', messages: this.$store.getters.getVerificationTask.length, badge: 'e-badge e-badge-success', icons: 'updates', type: 'Verificacón' },
              ]
              this.dataGrid = this.$store.getters.getPlanificacionesPendientes
              for(var i=0;i<this.dataGrid.length;i++){
                this.dataGrid[i].json_planificacion = JSON.parse(this.dataGrid[i].json_planificacion)
                this.dataGrid[i].json_controles_planificados = JSON.parse(this.dataGrid[i].json_controles_planificados)
              }
              this.loadingListAgenda=false
              this.blockedGrid=''
              this.gridData= this.$store.getters.getControlesRealizadosPendientes
              for(var i =0; i<this.gridData.length;i++){
                this.gridData[i].fecha = new Date(this.gridData[i].fecha)
              }
              for(var i=0;i<this.gridData.length;i++){
                this.gridData[i].id_asignatura = JSON.parse(this.gridData[i].id_asignatura)
                this.gridData[i].id_person = JSON.parse(this.gridData[i].id_person)
                this.gridData[i].controlado = JSON.parse(this.gridData[i].controlado)
              }

              this.gridData2= this.$store.getters.getControlesRecibidosPendientes
              for(var i =0; i<this.gridData2.length;i++){
                this.gridData2[i].fecha = new Date(this.gridData2[i].fecha)
              }
              for(var i=0;i<this.gridData2.length;i++){
                this.gridData2[i].id_asignatura = JSON.parse(this.gridData2[i].id_asignatura)
                this.gridData2[i].id_person = JSON.parse(this.gridData2[i].id_person)
                this.gridData2[i].controlado = JSON.parse(this.gridData2[i].controlado)
              }
            }).catch((reason) => {
              this.loadingListAgenda=false
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Error!',
                content: reason,
                cssClass: 'e-toast-danger',
                icon: 'e-error toast-icons'
              })
            });
          }).catch((reason) => {
            this.loadingListAgenda=false
            this.$root.$children[0].$refs.toastRef.show({
              title: 'Error!',
              content: reason,
              cssClass: 'e-toast-danger',
              icon: 'e-error toast-icons'
            })
          });
        }).catch((reason) => {
          this.loadingListAgenda=false
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: reason,
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons'
          })
        });
      },
      selectAgendaType: function (args) {
        if(args != "" && args.data.id == "planificacion"){
          this.stateId= args.data.id;
          this.is_planificacion= true
          this.is_controlRealizado = false
          this.is_controlRecibido = false
        }
        else if(args != "" && (args.data.id == "controlesRealizar")){
          this.stateId= args.data.id;
          this.is_planificacion= false
          this.is_controlRealizado = true
          this.is_controlRecibido = false
        }
        else if(args != "" && (args.data.id == "controlesRecibir")){
          this.stateId= args.data.id;
          this.is_planificacion= false
          this.is_controlRealizado = false
          this.is_controlRecibido = true
        }
      },
      // ////////////////////Cargar los invitados
      onComplete: function(args) {
        let list = document.getElementById('lists').getElementsByClassName('e-list-group-item')[0];
        list.style.display = 'none';
      },
      // metodos del dialog para verificar el acuerdo
      hideDialogCancel: function () {
        this.$refs.promptDialogVerification.hide();
      },
      hideDialogCancelPlanificacion: function () {
        this.$refs.promptDialogVerificationPlanificacion.hide();
      },
      rowSelected: function(args) {
        this.selectedrecordsPlanificacion = this.$refs.gridObjPlanificacion.getSelectedRecords();
      },
      rowSelectedRealizar: function(args) {
        this.selectedrecords = this.$refs.gridObjRealizar.getSelectedRecords();
      },

      showDialogVerificationsDialog: function (args) {
        //let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(args.target.parentElement.parentElement.getAttribute('data-uid'))
        // var row = rowObj.data
        this.$refs.promptDialogVerification.show();
      },
      showDialogVerificationsDialogPlanificacion: function (args) {
        //let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(args.target.parentElement.parentElement.getAttribute('data-uid'))
        // var row = rowObj.data
        this.$refs.promptDialogVerificationPlanificacion.show();
      },

      showDialogVerificationsPlanificacion: function(){
        this.$store.commit('setReglasEspecificas', this.selectedrecordsPlanificacion)
        this.$refs.promptDialogVerificationPlanificacion.hide();
        setTimeout(() => {
          this.$router.replace({ path: '/Verificar' })
        }, 250)
      },

      showDialogVerifications: function (args) {
        //let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(args.target.parentElement.parentElement.getAttribute('data-uid'))
        this.$refs.promptDialogVerification.hide();
        this.blockedGrid='blockedcomponent'
        this.changeGrid= true
        var listControlesActualizar = []
        var jsonControl = null
        for(var i=0;i<this.selectedrecords.length;i++){
          jsonControl = {
            "id_control": this.selectedrecords[i].id_control,
            "estado": "Realizado"
          }
          listControlesActualizar.push(jsonControl)
        }
        var jsonBody = {
          "Control": listControlesActualizar
        }

        var listControlesNuevos = []
        var findControlViejo = false
        for(var i=0;i<this.gridData.length;i++){
          findControlViejo = false
          for(var j=0;j<listControlesActualizar.length;j++){
            if(this.gridData[i].id_control==listControlesActualizar[j].id_control){
              findControlViejo = true
            }
          }
          if(findControlViejo==false){
            listControlesNuevos.push(this.gridData[i])
          }
        }
        this.gridData = listControlesNuevos

        axios.post(this.urlControl + 'update_multiple', jsonBody).then(response => {
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Éxito!',
            content: 'Los controles se han actualizado correctamente',
            cssClass: 'e-toast-success',
            icon: 'e-success toast-icons'
          })
          this.showNumberStateBadge()
          this.blockedGrid='blockedcomponent'
          this.changeGrid= false
          //this.selectAgendaType("")
        }).catch(response=>{
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Error!',
            content: 'Existen problemas en el servidor.',
            cssClass: 'e-toast-danger',
            icon: 'e-error toast-icons'
          })
          this.showNumberStateBadge()
          //this.selectAgendaType("")
          this.blockedGrid=''
          this.changeGrid= false
        })

        // var row = rowObj.data
        //this.$refs.promptDialogVerification.show();
      },
      onGridLoad: function () {
        if (Browser.isDevice) {
          this.toolbar = [{ text: 'Edit', tooltipText: 'Edit', prefixIcon: 'e-edit', id: 'editTool' }, 'Delete']
          this.$refs.gridObj.ej2Instances.columns[10].visible = false
        }
      },
      onload: function () {
      },
      onActionBegin: function (args) {
        if (args.requestType === 'beginEdit') {
          this.editBtnClick(args)
          args.cancel = true
        }
        if (args.requestType === 'delete') {
          this.loadingGrid=true;
          this.blockedcomponent='blockedcomponent'
          var records =  this.$refs.gridObj.getSelectedRecords();
          var listControls=[]
          for (var index = 0; index < records.length; index++) {
            listControls[index]=records[index].id_control
          }
          // Eliminar Tipo de reunión
          axios.post(this.urlControls+'delete_by_id',listControls).then(response => {
            if(records.length == 1){
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Éxito!',
                content: 'El Control se ha eliminado correctamente',
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
              this.$refs.gridObj.clearSelection();
            }
            else{
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Éxito!',
                content: 'Los Controles se han eliminado correctamente',
                cssClass: 'e-toast-success',
                icon: 'e-success toast-icons'
              })
              this.$refs.gridObj.clearSelection();
            }
            this.loadingGrid=false;
            this.blockedcomponent=''
          })
        }
      },
      AddBtnClick: function () {
        console.log(this.$refs.gridObj.ej2Instances)
        this.dlgShow = !this.dlgShow
        this.isEditing = false
      },

      toolbarClick: function (ClickEventArgs) {
        switch (ClickEventArgs.item.text) {
          case 'PDF Export':
            this.$refs.gridObjRealizar.pdfExport();
            break;
          case 'Excel Export':
            this.$refs.gridObjRealizar.excelExport();
            break;
          case 'CSV Export':
            this.$refs.gridObjRealizar.csvExport();
            break;
        }
      },
      toolbarClick2: function (ClickEventArgs) {
        switch (ClickEventArgs.item.text) {
          case 'PDF Export':
            this.$refs.gridObjRecibidos.pdfExport();
            break;
          case 'Excel Export':
            this.$refs.gridObjRecibidos.excelExport();
            break;
          case 'CSV Export':
            this.$refs.gridObjRecibidos.csvExport();
            break;
        }
      },
      toolbarClick3: function (ClickEventArgs) {
        switch (ClickEventArgs.item.text) {
          case 'PDF Export':
            this.$refs.gridObjPlanificacion.pdfExport();
            break;
          case 'Excel Export':
            this.$refs.gridObjPlanificacion.excelExport();
            break;
          case 'CSV Export':
            this.$refs.gridObjPlanificacion.csvExport();
            break;
        }
      },
      editBtnClick: function (args) {
        if (this.$refs.gridObj.getSelectedRecords().length === 1){
          this.loadingEdit = true;
          this.blockedcomponent = 'blockedcomponent'
          if (args.type === 'actionBegin') {
            this.rowData = args.rowData
          }
//        var target = args.target
//        if (target.classList.contains('e-edit')) {
//          target = target.parentElement
//        }
//        let rowObj = this.$refs.gridObj.ej2Instances.getRowObjectFromUID(target.parentElement.parentElement.parentElement.getAttribute('data-uid'))
//        this.rowData = rowObj.data
//         let body={
//           "attr":{
//             "id_control": this.rowData['id_control']
//           }
//         }
//         axios.post(this.urlControls+'find_parameters',body).then(response => {
          this.controlSelected = this.rowData
          this.isEditing = true
          this.dlgShow = !this.dlgShow
          this.loadingEdit = false;
          this.blockedcomponent = ''
          // })
        }
        else{
          this.$root.$children[0].$refs.toastRef.show({
            title: 'Advertencia!',
            content: 'Debe seleccionar solo un control para modificar',
            cssClass: 'e-toast-warning',
            icon: 'e-warning toast-icons'
          })
        }
      },
    },
    computed: {
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }

    },
    provide: {
      grid: [Edit, Group, Filter, PdfExport, ExcelExport, Page, Selection, CommandColumn, Toolbar, RowDD, DetailRow]
    },
  });
</script>

<style type="text/css">
  /*
*  Usage:
*
    <div class="sk-three-bounce">
      <div class="sk-child sk-bounce1"></div>
      <div class="sk-child sk-bounce2"></div>
      <div class="sk-child sk-bounce3"></div>
    </div>
*
*/
  .styleBtnCancel{
    background-color: #2196F3 !important;
    border-color: #2196F3 !important;
    color: #fff !important;
    border-radius: 20px;
    font-size: 14px;
    height: 36px;
    width: 125px;
  }
  .sk-three-bounce {
    margin: 40px auto;
    width: 80px;
    text-align: center; }
  .sk-three-bounce .sk-child {
    width: 8px;
    height: 8px;
    background-color: #333;
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-three-bounce 1.4s ease-in-out 0s infinite both;
    animation: sk-three-bounce 1.4s ease-in-out 0s infinite both; }
  .sk-three-bounce .sk-bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s; }
  .sk-three-bounce .sk-bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s; }

  @-webkit-keyframes sk-three-bounce {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0); }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1); } }

  @keyframes sk-three-bounce {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0); }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1); } }

</style>



