import axios from '../../../axios/axios-auth'

import {
    requestData,
    listData,
    employeeData
} from '../../../datasource.js'
import { DataManager, Query, Predicate } from '@syncfusion/ej2-data'

const state = {
    activityData: [],
    empInput: employeeData,
    //
    requestInput: requestData,
    currentUserID: 2,
    urlNotifications: '/alerts/notifications/',
    urlNotificationsUser: '/alerts/notifications_user/',
    loading: false,
    blockedcomponent: '',
  jsonNotificationsUser:''
    // 
}
const getters = {
    //
    requests: state => {
        return new DataManager(state.requestInput).executeLocal(new Query().where(new Predicate('Status', 'notequal', 'Pending'))).length
    },
    pendingRequests: state => {
        return new DataManager(state.requestInput).executeLocal(new Query().where(new Predicate('Status', 'equal', 'Pending'))).length
    },
    getLoading(state) {
        return state.loading
    },
    getBolckedComponent(state) {
        return state.blockedcomponent
    },
   getJsonNotificationsUser(state) {
        return state.jsonNotificationsUser
    },
    getActivityData(state) {
        return state.activityData
    },
    getUrlNotifications(state) {
        return state.urlNotifications
    },
    getUrlNotificationsUser(state) {
        return state.urlNotificationsUser
    }

}
const mutations = {
    addActivity(state, activity) {
        state.activityData.push(activity)
    },
    setJsonNotificationsUser(state, activity) {
        state.jsonNotificationsUser.push(activity)
    },
    setLoader(state, loading) {
        state.loading = loading
    },
    setBolckedComponent(state, blocked) {
        state.blockedcomponent = blocked
    },
    setListActivities(state, listData) {
        state.activityData = listData
    },

}
const actions = {

    listActivity(context) {
        context.commit('setLoader', true)
        context.commit('setBolckedComponent', 'blockedcomponent')
        axios.post(context.getters.getUrlNotificationsUser, {
            "relations": ["notifications"],
            "attr": { "id_user": context.getters.user.userId }
        }).then(response => {
            var listNotificationsByUser = []
            for (let i = 0; i < response.data.length; i++) {
                listNotificationsByUser.push(response.data[i].notifications)
            }
            context.commit('setListActivities', listNotificationsByUser)
            context.commit('setLoader', false)
            context.commit('setBolckedComponent', '')
        })
    },
    addActivity(context, activity) {
        return new Promise((resolve, reject) => {
            var userExist = context.state.empInput.find((user) => {
                return user.Employee === activity.Employee
            })
            if (!userExist) {
                reject(new Error('No such user found!'))
            } else if (activity.Employee === '') {
                reject(new Error('User name cannot be empty!'))
            } else {
                context.commit('addActivity', activity)
                resolve(Object.assign({}, userExist))
            }
        })
    },
        async addNotifications(context, agendaData) {
           var agendaPromise = [];
           var jsonNotificationsUser = {"Notifications_user": []}
           for (var i = 0; i < agendaData.length; i++) {
             if (agendaData.length > 0) {
               const notificationsResponse = await context.dispatch('notificationsCreateByResponsibles',agendaData[i])

               jsonNotificationsUser.Notifications_user.push({"id_notifications": notificationsResponse.id_notifications,"id_user": agendaData[i].arrayresponsible_agenda[0].id_user_ldap })
               // context.commit('setJsonNotificationsUser', jsonNotificationsUser)
             }
           }
             axios.post(context.getters.getUrlNotificationsUser + 'create',jsonNotificationsUser ).then(response => {
               context.dispatch('listActivity')
         })
          axios.post(context.getters.getUrlNotifications + 'create', {
            "notifications": "La información de la reunión del " + agendaData[0].meeting.date_planned_completion + " ha sido circulada",
            "type": "system",
            "current_date": new Intl.DateTimeFormat('en-US').format(new Date()),
            "user": "system",
            "state": "pennding",
            "header": agendaData[0].meeting.meeting_type.name,
            "img_src": "assets/calendar_48dp.png"
          }).then(response => {

            context.dispatch('listActivity')
          })
    },
          notificationsCreateByResponsibles(context, agenda_Data_In_i){
            return new Promise((resolve, reject) => {
                 axios.post(context.getters.getUrlNotifications + 'create', {
                    "notifications": "Usted es responsable del punto #" + agenda_Data_In_i.number + " de la reunión: " + agenda_Data_In_i.meeting.meeting_type.name,
                    "type": "createdPointFromMeeting",
                    "current_date": new Intl.DateTimeFormat('en-US').format(new Date()),
                    "user": "system",
                    "state": "pending",
                    "header": "Agenda del " + agenda_Data_In_i.meeting.meeting_type.name,
                    "img_src": "assets/agenda.png"
                  }).then(response => {
                    resolve(Object.assign({}, response.data.model))
                  })
                    .catch(response => {
                    reject(new Error('Error en el servior!'))
                  })

            })
          },




}

export default {
    state,
    mutations,
    actions,
    getters
}



