      VerificarPlanificacion: function(args){
        //////////////////////////////////////////////////////////////////
        var dateNow = new Date()
        var idSemestre = null
        if(dateNow.getMonth()>=8){
          idSemestre = 3
        }
        else{
          idSemestre = 2
        }

        var JsonPlanificacion = {
