            "excepciones": []
          },
          "cantControlesPorMes": 1,
          //Aqui estoy pasando el semestre al berro pq tengo la fecha de la maquina mal y estoy probando
          "semestre": idSemestre,
          "reglasespecificas": [
            // {
            //   "profesorcontrolador": 3110,
            //   "profesorcontrolado": 4210,
            //   "cantcontroles": 2,
            //   "mes": null
            // },
            // {
            //   "profesorcontrolador": 4200,
            //   "profesorcontrolado": 4686,
            //   "cantcontroles": 2,
            //   "mes": null
            // },
            // {
            //   "profesorcontrolador": 4195,
            //   "profesorcontrolado": 4210,
            //   "cantcontroles": null,
            //   "mes": 3
            // },
            // {
            //   "profesorcontrolador": 4194,
            //   "profesorcontrolado": 4210,
            //   "cantcontroles": null,
            //   "mes": null
            // }
          ]
        }

        for(var i = 0;i<this.listExcepcionesControladores.length;i++){
          var prueba = {
            "id_person": this.listExcepcionesControladores[i].person.id_person,
            "cant": this.listExcepcionesControladores[i].cant
          }
          JsonPlanificacion.cantControlesARealizar.excepciones.push(prueba)
        }

        for(var i = 0;i<this.listExcepcionesControlados.length;i++){
          var prueba2 = {
            "id_person": this.listExcepcionesControlados[i].person.id_person,
            "cant": this.listExcepcionesControlados[i].cant
          }
          JsonPlanificacion.controlesPorProfesor.excepciones.push(prueba2)
        }

        for(var i = 0;i<this.$refs.gridObj3.ej2Instances.dataSource.length;i++){
          var prueba3 = {
            "profesorcontrolador": this.$refs.gridObj3.ej2Instances.dataSource[i].profesorcontrolador.id_person,
            "profesorcontrolado": this.$refs.gridObj3.ej2Instances.dataSource[i].profesorcontrolado.id_person,
            "cantcontroles": this.$refs.gridObj3.ej2Instances.dataSource[i].cantcontroles,
            "mes": ""
          }
          if(this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Septiembre" || this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Enero")
            prueba3.mes = 1
          else if(this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Octubre" || this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Febrero")
            prueba3.mes = 2
          else if(this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Noviembre" || this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Marzo")
            prueba3.mes = 3
          else if(this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Diciembre" || this.$refs.gridObj3.ej2Instances.dataSource[i].mes=="Abril")
            prueba3.mes = 4

          JsonPlanificacion.reglasespecificas.push(prueba3)
        }
        //////////////////////////////////////////////////////////////////
        var PlanificacionOriginal = this.$store.getters.getReglasEspecificas
        var ReglasEspecificasOriginal = PlanificacionOriginal[0].json_planificacion
        var ControlesPlanificadosOriginal = PlanificacionOriginal[0].json_controles_planificados
        var sonIguales = true

        ////////////////////////
        if(ReglasEspecificasOriginal.cantControlesARealizar.cant!=JsonPlanificacion.cantControlesARealizar.cant){
          sonIguales = false
        }
        if(ReglasEspecificasOriginal.controlesPorProfesor.cant!=JsonPlanificacion.controlesPorProfesor.cant){
          sonIguales = false
        }
        ////////////////////////
        var findA = false
        for(var i=0;i<ReglasEspecificasOriginal.cantControlesARealizar.excepciones.length && sonIguales;i++){
          findA = false
          for(var j=0;j<JsonPlanificacion.cantControlesARealizar.excepciones.length && !findA;j++){
            if(ReglasEspecificasOriginal.cantControlesARealizar.excepciones[i].id_person==JsonPlanificacion.cantControlesARealizar.excepciones[j].id_person){
              if(ReglasEspecificasOriginal.cantControlesARealizar.excepciones[i].cant==JsonPlanificacion.cantControlesARealizar.excepciones[j].cant){
                findA = true
              }
            }
          }
          if(findA == false){
            sonIguales = false
          }
        }
        /////////////////////////
        findA = false
        for(var i=0;i<ReglasEspecificasOriginal.controlesPorProfesor.excepciones.length && sonIguales;i++){
          findA = false
          for(var j=0;j<JsonPlanificacion.controlesPorProfesor.excepciones.length && !findA;j++){
            if(ReglasEspecificasOriginal.controlesPorProfesor.excepciones[i].id_person==JsonPlanificacion.controlesPorProfesor.excepciones[j].id_person){
              if(ReglasEspecificasOriginal.controlesPorProfesor.excepciones[i].cant==JsonPlanificacion.controlesPorProfesor.excepciones[j].cant){
                findA = true
              }
            }
          }
          if(findA == false){
            sonIguales = false
          }
        }
        ////////////////////////
        findA = false
        for(var i=0;i<ReglasEspecificasOriginal.reglasespecificas.length && sonIguales;i++){
          findA = false
          for(var j=0;j<JsonPlanificacion.reglasespecificas.length && !findA;j++){
            if(ReglasEspecificasOriginal.reglasespecificas[i].profesorcontrolador==JsonPlanificacion.reglasespecificas[j].profesorcontrolador &&
               ReglasEspecificasOriginal.reglasespecificas[i].profesorcontrolado==JsonPlanificacion.reglasespecificas[j].profesorcontrolado &&
               ReglasEspecificasOriginal.reglasespecificas[i].cantcontroles==JsonPlanificacion.reglasespecificas[j].cantcontroles &&
               ReglasEspecificasOriginal.reglasespecificas[i].mes==JsonPlanificacion.reglasespecificas[j].mes){
              findA = true
            }
          }
          if(findA == false){
            sonIguales = false
          }
        }
        ////////////////////////Aqui se repite la comparacion pero de un lado a otro
