  /*
*  Usage:
*
    <div class="sk-three-bounce">
      <div class="sk-child sk-bounce1"></div>
      <div class="sk-child sk-bounce2"></div>
      <div class="sk-child sk-bounce3"></div>
    </div>
*
*/
  .sk-three-bounce2 {
    color: #094279;
    margin: 40px auto;
    width: 705px;
    text-align: right; }
  .sk-three-bounce2 .sk-child {
    color: #094279;
    width: 8px;
    height: 8px;
    background-color: #333;
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-three-bounce2 1.4s ease-in-out 0s infinite both;
    animation: sk-three-bounce2 1.4s ease-in-out 0s infinite both; }
  .sk-three-bounce2 .sk-bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s; }
  .sk-three-bounce2 .sk-bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s; }

  @-webkit-keyframes sk-three-bounce2 {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0); }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1); } }

  @keyframes sk-three-bounce2 {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0); }
    40% {
      -webkit-transform: scale(1);
      transform: scale(1); } }

  .input {
