<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-8">
    <div class="row top-row">
      <div class="col-lg-3 col-md-3 dashboard-card">
        <div tabindex="0" class="e-card" id="card-task-pennding" @click="requestClick($event)">
          <div class="e-card-header">
            <div class="e-card-header-caption">
              <div class="e-card-header-title">{{this.$store.state.store_agreement.taskInput.length}}</div>
              <div class="e-card-sub-title">Acuerdos pendientes</div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-3 col-md-3 dashboard-card">
        <div tabindex="0" class="e-card" id="card-rejected" @click="licenseClick($event)">
          <div class="e-card-header">
            <div class="e-card-header-caption">
              <div class="e-card-header-title">{{this.$store.state.store_notification.activityData.length}}</div>
              <div class="e-card-sub-title">Acuerdos rechazados</div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-3 col-md-3 dashboard-card">
        <div tabindex="0" class="e-card" id="card-complete" @click="licenseClick($event)">
          <div class="e-card-header">
            <div class="e-card-header-caption">
              <div class="e-card-header-title">{{this.$store.state.store_notification.activityData.length+3}}</div>
              <div class="e-card-sub-title">Acuerdos cumplidos</div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-3 col-md-3 dashboard-card">
        <div tabindex="0" class="e-card" id="card-checked" @click="licenseClick($event)">
          <div class="e-card-header">
            <div class="e-card-header-caption">
              <div class="e-card-header-title">{{this.$store.state.store_notification.activityData.length+1}}</div>
              <div class="e-card-sub-title">Acuerdos verificados</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- Charts de información -->
    <div class="row">
      <!-- <div class="col-lg-12 "> -->
      <div class="col-lg-12">
        <div class="row">
          <div class="col-lg-6 chart-area">
            <div id='chart01'>
              <div class='innerChart'>
                <meeting-calendar></meeting-calendar>
              </div>
            </div>
          </div>
          <div class="empty-col"></div>
          <div class="col-lg-6 chart-area">
            <div id='chart02'>
              <div class='innerChart'>
                <meeting-category-chart id='chart2'></meeting-category-chart>
              </div>
            </div>
          </div>
        </div>
        <div class="row col-lg-8 empty-row">
        </div>
        <div class="row">
          <div class="col-lg-6 chart-area">
            <div id='chart03'>
              <div class='innerChart'>
                <task-status-chart id='chart3'></task-status-chart>
              </div>
            </div>
          </div>
          <div class="empty-col"></div>
          <div class="col-lg-6 chart-area">
            <div id='chart04'>
              <div class='innerChart'>
                <information-chart id='chart4'></information-chart>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
      </div>
      <div class="col-lg-4 list-surface">
        <div> 
          <div class="list-area">
            <ejs-listview id='listview_template' ref='list_template' 
            :fields='fields' :dataSource='this.$store.state.store_notification.activityData' 
            :showHeader='header' :headerTitle='listTitle' :actionComplete='onComplete'
             :template='ltemplate'></ejs-listview>
          </div>
        </div>
        <!--loader-->
        <div v-if="this.$store.getters.getLoading" class="sk-fading-circle" style="margin-top: -477px;">
                   <div class="sk-circle1 sk-circle"></div>
                   <div class="sk-circle2 sk-circle"></div>
                   <div class="sk-circle3 sk-circle"></div>
                   <div class="sk-circle4 sk-circle"></div>
                   <div class="sk-circle5 sk-circle"></div>
                   <div class="sk-circle6 sk-circle"></div>
                   <div class="sk-circle7 sk-circle"></div>
                   <div class="sk-circle8 sk-circle"></div>
                   <div class="sk-circle9 sk-circle"></div>
                   <div class="sk-circle10 sk-circle"></div>
                   <div class="sk-circle11 sk-circle"></div>
                   <div class="sk-circle12 sk-circle"></div>
         </div>
        <!---->
      </div>
      <!-- </div> -->
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import { ListViewPlugin } from '@syncfusion/ej2-vue-lists'
  import ActivityTemplate from '../notifications/ActivityTemplate.vue'
  import MeetingCategoryChart from '../Index/meeting_category_chart.vue'
  import MeetingCalendar from './meeting_calendar.vue'
  import TaskStatusChart from '../Index/task_status_chart.vue'
  import InformationChart from '../Index/information_chart.vue'

  Vue.use(ListViewPlugin)
  export default Vue.extend({
    data: function () {
      return {
        header: true,
        // eslint-disable-next-line
        ltemplate: function () {
          return {template: ActivityTemplate}
        },
        listTitle: 'Actividades Recientes',
        fields: { 'id': 'Employee' }
      }
    },
    mounted(){
      this.$store.dispatch("listActivity");
      if(this.$router.currentRoute.path==='/'){
          this.$router.replace({ path: '/Inicio' })
      }
    },
    methods: {
      requestClick: function (event) {
        this.$router.push({ path: '/Acuerdos' })
        this.$root.$children[0].$refs.sidebarListObj.selectItem({id: '05'})
      },
      licenseClick: function (event) {
        this.$router.push({ path: '/PrepararReunion' })
        this.$root.$children[0].$refs.sidebarListObj.selectItem({id: '04'})
      },
      onComplete: function () {
      }
    },
    components: {
      MeetingCategoryChart,
      MeetingCalendar,
      TaskStatusChart,
      InformationChart
    }
  })
</script>

<style>
  .dashboard-card {
    padding-left: 10px;
    padding-right: 10px;
  }
  #card-task-pennding,
  #card-rejected,
  #card-complete,
  #card-checked{
    height: 80px;
    margin-bottom: 10px;
    text-align: center;
    box-shadow: 0px 2px 8px #7b77885e;
    border: none;
    cursor: pointer;
  }
  #card-task-pennding {
    background-color: #B991DA;
  }
  #card-task-pennding:hover {
    background-color: #946DB4;
  }
  #card-rejected {
    background-color: rgb(179, 115,58, 1);
  }
  #card-rejected:hover {
    background-color: rgb(170, 70, 75);
  }
  #card-complete {
    background-color: #60D6A2;
  }
  #card-complete:hover {
    background-color: #4DAE83;
  }
   #card-checked {
    background-color: rgb(130, 104, 171);
  }
  #card-checked:hover {
    background-color: rgb(95, 96, 133);
  }
  
  .e-card-header-title {
    color: #FFFFFF !important;
    font-size: 26px !important;
    font-family: Roboto;
    font-weight: '1000';
  }
  .e-card-sub-title {
    color: #FFFFFF !important;
    font-family: Roboto;
    font-size: 14px !important;
  }
  .chart-area {
    height: 296px;
    padding: 10px 10px;
  }
  #chart01,
  #chart02,
  #chart03,
  #chart04 {
    height: 296px;
    border-radius: 4px;
    box-shadow: 0px 2px 8px #7b77885e;
    background: #fff;
  }
  #chart001,
  #chart002,
  #chart003,
  #chart004 {
    height: 250px !important;
  }
  .list-area {
    border-radius: 4px;
    box-shadow: 0px 2px 8px #7b77885e;
    height: 612px;
    overflow: auto;
    background: #fff;
    margin-top: 10px;
  }
  .row.col-lg-8.empty-row {
    padding: 10px;
  }

  @media screen and (min-width: 1366px){
    .chart-area {
      height: 296px;
      padding: 10px 10px;
    }
    #chart01,
    #chart02,
    #chart03,
    #chart04 {
      height: 296px;
      border-radius: 4px;
      box-shadow: 0px 2px 8px #7b77885e;
      background: #fff;
    }
    #chart001,
    #chart002,
    #chart003,
    #chart004 {
      height: 250px !important;
    }
    .list-area {
      border-radius: 4px;
      box-shadow: 0px 2px 8px #7b77885e;
      height: 612px;
      overflow: auto;
      background: #fff;
      margin-top: 10px;
    }
  }
  @media screen and (min-width: 1600px){
    .chart-area {
      height: 350px;
      padding: 10px 10px;
    }
    #chart01,
    #chart02,
    #chart03,
    #chart04 {
      height: 350px;
      border-radius: 4px;
      box-shadow: 0px 2px 8px #7b77885e;
      background: #fff;
    }
    #chart001,
    #chart002,
    #chart003,
    #chart004 {
      height: 300px !important;
    }
    .list-area {
      border-radius: 4px;
      box-shadow: 0px 2px 8px #7b77885e;
      height: 720px;
      overflow: auto;
      background: #fff;
      margin-top: 10px;
    }
  }
  @media screen and (min-width: 1900px){
    .chart-area {
      height: 370px;
      padding: 10px 10px;
    }
    #chart01,
    #chart02,
    #chart03,
    #chart04 {
      height: 370px;
      border-radius: 4px;
      box-shadow: 0px 2px 8px #7b77885e;
      background: #fff;
    }
    #chart001,
    #chart002,
    #chart003,
    #chart004 {
      height: 320px !important;
    }
    .list-area {
      border-radius: 4px;
      box-shadow: 0px 2px 8px #7b77885e;
      height: 760px;
      overflow: auto;
      background: #fff;
      margin-top: 10px;
    }
  }

  @media screen and (max-width: 450px){
    .chart-area{
      margin-bottom: 20px;
    }

    .empty-row{
      padding: 0px !important;
    }
  }

  #chart-title {
    font-size: 14px;
    font-family: Helvetica, 'Segoe UI', Tahoma, Geneva, Verdana, Arial;
    color: #323C47;
    padding-top: 16px;
    padding-left: 10px;
    margin: 0 0 5px !important;
  }

  .list-surface {
    padding-left: 10px;
    padding-right: 10px;
  }
  #listview_template.e-listview .e-list-item {
    padding: 10px;
    /* height: 65px; */
    line-height: 15px !important;
  }
  #listview_template.e-listview .e-list-header {
    margin-bottom: 15px;
    color: #323C47;
    font-family: helvetica;
    font-weight: 600 !important;
    height: 50px !important;
  }
  #listview_template.e-listview .e-list-item {
    height: auto !important;
  }
</style>



