<template>
    <table class="detailtable" width="100%">
        <colgroup>
            <col width="0%">
            <col width="50%">
            <col width="50%">
        </colgroup>
        <tbody>
            <tr>
                <td rowspan="4" style="text-align: center;">
                    <!-- <img class='photo' :src="image" :alt="data.id" /> -->
                </td>
                 <td>
                    <span style="font-weight: 500;">Punto número: </span> {{data.agenda.number}}
                </td>    
                 
                    <td>
                    <span style="font-weight: 500;">Facilitador: </span> {{data.agenda.id_facilitator}}
                </td>                                          
            </tr>
            <tr>  
                 <td>
                    <span style="font-weight: 500;">Titulo del punto: </span> {{data.agenda.title}}
                </td>   
                <td>
                    <span style="font-weight: 500;">Invitados: </span><div style="margin-left: 62px; margin-top: -17px;">
                         <div v-for="item in data.agenda.arrayagenda_guest"> {{item.id_user_ldap+',' }}</div></div>
                   </td>          
                          
            </tr>
            <tr>
                 <td>
                    <span style="font-weight: 500;">Duración </span> {{data.agenda.duration}}
                </td>
                              
                       
            </tr>           
        </tbody>
    </table>
</template>
 
<script>

import { Internationalization } from '@syncfusion/ej2-base';

let instance = new Internationalization();

export default {
  data () {
    return {
        data: {},
    }
  },
  created(){
   this.userResponsible=this.user.nombre
  },
  methods: {
    format: function(value){
        return instance.formatDate(value, { skeleton: 'yMd', type: 'date' });
    }
  },
  computed: {
    image: function() {
      return './assets/Avatar_Male.svg';
    },    
  }
}
</script>



