<template>
  <div id="appId" class="#app">
    <Login v-if="!auth"></Login>
    <Site v-if="auth"></Site>
    <!--Messagges-->
    <div class="e-sample-resize-container">
      <ejs-toast ref='toastRef' id='toast_type' position='right' timeOut=3000 showCloseButton=true showProgressBar=true>
      </ejs-toast>
    </div>
  </div>
</template>

<script>

  import Vue from 'vue'
  import Site from './components/Home/App_Home.vue'
  import Login from './components/Home/Login.vue'

  export default Vue.extend({ 
    data: function () {
      return {
      }
    },
    computed: {
      auth() {
        return this.$store.getters.isAuthenticated
      },
    },
    components: {
      Site,
      Login
    },
    created(){
      this.$store.dispatch('tryAutoLogin')
      }
  })
</script>
<style>
  @font-face {
    font-family: 'Toast_icons';
    src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype');
    font-weight: normal;
    font-style: normal;
  }
  #toast_type .toast-icons.e-success::before {
    content: "\e701";
  }
  .toast-icons {
    font-family: 'Toast_icons' !important;
    font-size: 55px;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
</style>




