<template>
  <div>
    <p id="chart-title">Estado de los acuerdos por Tipo de reunión</p>
    <ejs-chart style='display:block' align='center' id='chart002' :primaryXAxis='primaryXAxis' :primaryYAxis='primaryYAxis'
               :chartArea='chartArea' :tooltip='tooltip'>
      <e-series-collection>
        <e-series :dataSource='this.$store.getters.ConsejoDirData' :query='laptopQuery' type='Column' xName='key' yName='count' name='Consejo de dirección' :marker='marker' fill='#A6DC7E'> </e-series>
        <e-series :dataSource='this.$store.getters.ConsejoSindData' :query='monitorQuery' type='Column' xName='key' yName='count' name='Consejo de sindicato' :marker='marker' fill='#6EB5D0'> </e-series>
        <e-series :dataSource='this.$store.getters.ReunProyData' :query='tabletQuery' type='Column' xName='key' yName='count' name='Reunión de proyecto' :marker='marker' fill='#7E7EDC'> </e-series>
        <e-series :dataSource='this.$store.getters.ReuniDecanoData' :query='miscQuery' type='Column' xName='key' yName='count' name='Reunion con el Decano' :marker='marker' fill='#ffc502'> </e-series>
      </e-series-collection>
    </ejs-chart>
  </div>
</template>
<script>
  import Vue from 'vue'
  import { ChartPlugin, ColumnSeries, Category, DataLabel, Tooltip, Legend } from '@syncfusion/ej2-vue-charts'
  import { Query } from '@syncfusion/ej2-data'

  Vue.use(ChartPlugin)
  export default Vue.extend({
    data: function () {
      return {
        laptopQuery: new Query().take(this.$store.getters.ConsejoDirData.length),
        monitorQuery: new Query().take(this.$store.getters.ConsejoSindData.length),
        tabletQuery: new Query().take(this.$store.getters.ReunProyData.length),
        miscQuery: new Query().take(this.$store.getters.ReuniDecanoData.length),
        // Initializing Primary X Axis
        primaryXAxis: {
          valueType: 'Category', interval: 0, majorGridLines: { width: 0 }
        },
        chartArea: { border: { width: 0 } },

        // Initializing Primary Y Axis
        primaryYAxis:
          {
            minimum: 0,
            maximum: 8,
            interval: 10,
            majorGridLines: { width: 0 },
            majorTickLines: { width: 0 },
            lineStyle: { width: 0 },
            labelStyle: { color: 'transparent' }
          },
        theme: 'Fabric',
        width: '100%',
        marker:
          { dataLabel:
            { visible: true, position: 'Top', font: { fontWeight: '500' }
            }
          },
        tooltip: {
          enable: true
        },
        title: 'Statistics by Hardware Category',
        enableSmartLabels: true
      }
    },
    provide: {
      chart: [ColumnSeries, Legend, DataLabel, Category, Tooltip]
    }
  })
</script>
<style scoped> 

</style>



