import axios from '../../../axios/axios-auth'
import { DataManager, Query, Predicate } from '@syncfusion/ej2-data'

const state = {
  profesoresFacultad:[],
  profesoresFacultadCompleto:[],
  ProfesoresFacultadSemestre:[],
  reglasEspecificas:"",
  controlsList: [],
  ListaControlesPrevia: [],
  urlControls: '/controls/web/planificacion/control/',
  urlPlanificacion: '/controls/web/planificacion/planificacion/',
  urlProfesores: '/pandora/web/planificacion/person/',
  urlAsigantedGroups: '/pandora/web/planificacion/asignated_groups/',

}
const getters = {
  getControlList(state) {
    return state.controlsList
  },
  getListaControlesPrevia(state) {
    return state.ListaControlesPrevia
  },
  getReglasEspecificas(state){
    return state.reglasEspecificas
  },
  getProfesoresFacultad(state){
    return state.profesoresFacultad
  },
  getProfesoresFacultadCompleto(state){
    return state.profesoresFacultadCompleto
  },
  getProfesoresFacultadSemestre(state){
    return state.ProfesoresFacultadSemestre
  }

}
const mutations = {
  setControlList(state, controlsList) {
    state.controlsList = controlsList
  },
  setListaControlesPrevia(state, ListaControlesPrevia) {
    state.ListaControlesPrevia = ListaControlesPrevia
  },
  setReglasEspecificas(state, reglasEspecificas) {
    state.reglasEspecificas = reglasEspecificas
  },
  setProfesoresFacultad(state, profesoresFacultad){
    state.profesoresFacultad = profesoresFacultad
  },
  setProfesoresFacultadCompleto(state, profesoresFacultadCompleto){
    state.profesoresFacultadCompleto = profesoresFacultadCompleto
  },
  setProfesoresFacultadSemestre(state, ProfesoresFacultadSemestre){
    state.ProfesoresFacultadSemestre = ProfesoresFacultadSemestre
  }
}
const actions = {
  getControlList(context) {
    return new Promise((resolve, reject) => {
      var jsonBody = null
      if(context.getters.user.is_jefe_departamento == true){
        jsonBody = {
          "attr":{
            "estado":"Pendiente",
            "departamento": context.getters.user.departamento
          }
        }
      }
      else{
        jsonBody = {
          "attr":{
            "estado":"Pendiente",
            "departamento": context.getters.user.departamento,
            "disciplina": context.getters.user.disciplina
          }
        }
      }
      axios.post(context.state.urlControls,jsonBody).then(response => {
          context.commit('setControlList', response.data)
          resolve(Object.assign({}, response.data))

      }).catch(response => {
        reject(new Error('Error del servior!'))
      })
    })
  },

  getReglasEspecificas(context) {
    return new Promise((resolve, reject) => {
      var jsonBody = {
        "attr": {
          "departamento": "Matematica",
          "estado": "Pendiente"
        }
      }
      axios.post(context.state.urlPlanificacion + 'find_parameters' ,jsonBody).then(response => {
        context.commit('setReglasEspecificas', response.data.items[0])
        resolve(Object.assign({}, response.data.items[0]))

      }).catch(response => {
        reject(new Error('Error del servior!'))
      })
    })
  },

  getProfesoresFacultad(context,yuni){
    return new Promise((resolve, reject) => {
      var jsonBody = {
        "pagination":false,
        "attr": {
          "faculty": "Ingeniería Informática"
        }
      }
      axios.post(context.state.urlProfesores + 'find_parameters' ,jsonBody).then(response => {
        context.commit('setProfesoresFacultad', response.data.items)
        resolve(Object.assign({}, response.data.items))

      }).catch(response => {
        reject(new Error('Error del servior!'))
      })
    })
  },

  getProfesoresFacultadCompleto(context){
    return new Promise((resolve, reject) => {
      // var jsonBody = {
      //   "relations": "asignaturaPlan",
      //   "pagination":false,
      //   "attr": {
      //     "faculty": "Ingeniería Informática",
      //   }
      // }
      var dateNow = new Date()
      var idSemestre = null
      if(dateNow.getMonth()>=8){
        idSemestre = 3
      }
      else{
        idSemestre = 2
      }
      var jsonBody = {
        "course": "2013-2014",//Aqui coger el ano actual de cuando se vaya a realizar la planificacion
        "idsemester": idSemestre,//Aqui poner idSemestre
        "faculty": "Ingeniería Informática",//Cambiar pa Facultad Ingenieria Informatica
        "department": context.getters.user.departamento,//"FACULTAD DE INGENIERIA  MECANICA. CENTRO DE ESTUDIOS DE TECNOLOGIA Y ENERGIAS RENIVABLES",
        "disciplina": context.getters.user.disciplina//"Postgrado"
      }
      axios.post(context.state.urlAsigantedGroups,jsonBody).then(response => {
        context.commit('setProfesoresFacultadCompleto', response.data.ProfesoresDepartamento)
        // var jsonProfesoresSemestre = []
        // for(var i=0;i<response.data.items.length;i++){
        //   if(response.data.items[i].id_asignatura !=null && response.data.items[i].id_asignatura !=""){
        //     jsonProfesoresSemestre.push(response.data.items[i])
        //   }
        // }
        context.commit('setProfesoresFacultadSemestre', response.data.ProfesoresSemestre)
        resolve(Object.assign({}, response.data))

      }).catch(response => {
        reject(new Error('Error del servior!'))
      })
    })
  },


  getProfesoresFacultadCompletoParaVerificador(context,disciplina){
    return new Promise((resolve, reject) => {
      // var jsonBody = {
      //   "relations": "asignaturaPlan",
      //   "pagination":false,
      //   "attr": {
      //     "faculty": "Ingeniería Informática",
      //   }
      // }
      var dateNow = new Date()
      var idSemestre = null
      if(dateNow.getMonth()>=8){
        idSemestre = 3
      }
      else{
        idSemestre = 2
      }
      var jsonBody = {
        "course": "2013-2014",//Aqui coger el ano actual de cuando se vaya a realizar la planificacion
        "idsemester": idSemestre,//Aqui poner idSemestre
        "faculty": "Ingeniería Informática",//Cambiar pa Facultad Ingenieria Informatica
        "department": context.getters.user.departamento,//"FACULTAD DE INGENIERIA  MECANICA. CENTRO DE ESTUDIOS DE TECNOLOGIA Y ENERGIAS RENIVABLES",
        "disciplina": disciplina//"Postgrado"
      }
      axios.post(context.state.urlAsigantedGroups,jsonBody).then(response => {
        context.commit('setProfesoresFacultadCompleto', response.data.ProfesoresDepartamento)
        // var jsonProfesoresSemestre = []
        // for(var i=0;i<response.data.items.length;i++){
        //   if(response.data.items[i].id_asignatura !=null && response.data.items[i].id_asignatura !=""){
        //     jsonProfesoresSemestre.push(response.data.items[i])
        //   }
        // }
        context.commit('setProfesoresFacultadSemestre', response.data.ProfesoresSemestre)
        resolve(Object.assign({}, response.data))

      }).catch(response => {
        reject(new Error('Error del servior!'))
      })
    })
  }

}

export default {
  state,
  mutations,
  actions,
  getters
}



