import axios from '../../../axios/axios-auth'
import router from '../../../router/index'

const state = {
    login: false,
    idToken: null,
    userIidGroupsd: null,
    user: null,
    loader: false
}
const getters = {
    isAuthenticated(state) {
        return state.user !== null
    },
    user(state) {
        state.idToken = state.user.idToken;
        state.userId = state.user.userId;
        return state.user
    },
    getLoader(state) {
        return state.loader
    },
}
const mutations = {
    auth(state, islogin) {
        state.login = islogin;
    },
    authUser(state, userData) {
        state.idToken = userData.token;
        state.userId = userData.userId;
    },
    storeUser(state, user) {
        state.user = user
        console.log('En store state logueado', user)
    },
    isloader(state, isloader) {
        state.loader = isloader

    },
    clearAuthData(state) {
        state.user = null;
        state.idToken = null;
        state.userId = null;
    },

}
const actions = {
    loginSystem(context, authData) {
        return new Promise((resolve, reject) => {
            context.commit('isloader', true)
            axios.post('controls/web/api/login', authData)
                .then(response => {
                    if (response.data.length !== 0) {
                        context.commit('storeUser', response.data)
                        const now = new Date()
                        const expirationDate = new Date(now.getTime() + response.data.expiresIn * 1000)
                        localStorage.setItem('token', response.data.idToken)
                        localStorage.setItem('user', JSON.stringify(response.data))
                        localStorage.setItem('userId', response.data.userId)
                        localStorage.setItem('expirationDate', expirationDate)
                        console.log(response.data.expiresIn)
                        context.dispatch('setLogoutTimer', response.data.expiresIn * 1000)
                        context.commit('isloader', false)
                        router.replace('/Seguimiento')
                        context.commit('auth', true)
                        resolve(Object.assign({}, response.data))

                    } else {
                        reject(new Error('Verifique sus datos por favor!'))
                        context.commit('isloader', false)
                        context.commit('auth', false)
                    }
                })
                .catch(response => {
                    context.commit('isloader', false)
                })
        })
    },
    setLogoutTimer(context, expirationTime) {
        setTimeout(() => {
            context.commit('clearAuthData')
          context.dispatch("logout", false);
          document.getElementById("sidebar-menu").remove();
          document.getElementById("appId").setAttribute("class", "#app");
          document.getElementById("appId").setAttribute("style" , "");
          document.getElementById("yyy").remove();
          document.getElementById("yy").innerHTML(yy);
            router.replace('/')
        }, expirationTime)
    },
    tryAutoLogin(context) {
      return new Promise((resolve, reject) => {
        const token = localStorage.getItem('token')
        if (!token) {
          return reject(new Error('El usuario está deslogueado'))
        }
        const expirationDate = localStorage.getItem('expirationDate')
        const now = new Date()
        if (now >= expirationDate) {
          return reject(new Error('El usuario está deslogueado'))
        }
        const userId = localStorage.getItem('userId')
        const user = JSON.parse(localStorage.getItem('user'))
        resolve(Object.assign({}, user))
        context.commit('authUser', { token: token, userId: userId })
        context.commit('storeUser', user)
      })
    },
    logout(context) {
        context.commit('clearAuthData')
        localStorage.removeItem('expirationDate')
        localStorage.removeItem('token')
        localStorage.removeItem('userId')
        localStorage.removeItem('user')
        router.replace('/')
    }

}

export default {
    state,
    mutations,
    actions,
    getters
}



