            for(var i=0;i<ListaControlados.length && !findControlado;i++){
              if(ListaControlados[i].id_person == this.selectedControlado){
                Controlado = ListaControlados[i]
                findControlado = true
              }
            }

            var findControlMes = false
            var gridMes = null
            for(var i=0;i<gridSinTuplaSeleccionada.dataSource.length;i++){
              gridMes = new Date(gridSinTuplaSeleccionada.dataSource[i].fecha)
              if(gridMes.getMonth()==mes && Controlado.id_person==gridSinTuplaSeleccionada.dataSource[i].controlado.id_person){
                findControlMes = true
              }
            }

            if(findControlMes == true){
              this.$root.$children[0].$refs.toastRef.show({
                title: 'Advertencia!',
                content: 'Ya existe un control asignado al profesor seleccionado en ese mes',
                cssClass: 'e-toast-warning',
                icon: 'e-warning toast-icons'
              })
              //this.frmObj.reset()
              return
            }

            var jsonControlesPrevia = {
              "id_asignatura" : Controlado.id_asignatura,
              "id_person" : Controlador,
              "controlado" : Controlado,
              "fecha" : date
            }

            var index = null
            for(var i=0;i<grid.getSelectedRecords().length;i++){
              for(var j=0;j<grid.dataSource.length;j++){
                if(grid.getSelectedRecords()[i].id_person.id_person == grid.dataSource[j].id_person.id_person &&
                   grid.getSelectedRecords()[i].controlado.id_person ==  grid.dataSource[j].controlado.id_person &&
                   grid.getSelectedRecords()[i].id_asignatura.id_asignatura == grid.dataSource[j].id_asignatura.id_asignatura &&
                   grid.getSelectedRecords()[i].fecha == grid.dataSource[j].fecha){
                  index = j
                }
              }
            }

            grid.dataSource[index].id_asignatura = jsonControlesPrevia.id_asignatura
            grid.dataSource[index].id_person = jsonControlesPrevia.id_person
            grid.dataSource[index].controlado = jsonControlesPrevia.controlado
            grid.dataSource[index].fecha = jsonControlesPrevia.fecha
            grid.refresh()

            this.selectedControlador=''
            this.selectedControlado=''
            this.disabledCant=false
            this.disableTodo=false
            this.primeraVez=true
            this.frmObj.reset()
            this.blockedForm = ''
            this.loadingCreate = false
            this.$emit('close')
            this.frmObj.reset()
          }
        }
      }
    },
    props: {
      /** Whether the dialog is currently showing */
      showing: {
        type: Boolean,
        required: true,
        default: false
      },
      isEdit: {
        type: Boolean,
        required: true,
        default: false
      },
      rowData: {
        type: Object,
        required: false,
        default: null
      },
      Controladores:{
        type: [],
        required: false,
        default:[]
      },
      Controlados:{
        type: [],
        required: false,
        default:[]
      },

    },
    computed: {
      isShowing() {
        return this.showing
      },
      user() {
        if (this.$store.getters.user)
          return this.$store.getters.user
        return false
      }

    }
  })
</script>

<style scoped>
  .content-wrapper {
    width: 100%;
    margin: 0 auto;
    min-width: 185px;
  }

  .content-wrapper div.row {
    padding: 5px 0px;
  }

  .control-label {
    padding: 24px 0px 0px 0px;
    font-size: 12px;
    opacity: 0.54;
  }

  @media only screen and (max-width: 480px) {
    .content-wrapper {
      width: 92%;
    }

    .e-input-section .col-xs-6,
    .e-input-section .col-xs-4,
    .e-input-section .col-xs-12 {
      padding: 10px 5px;
      width: 100%;
    }

    .content-wrapper div.row {
      padding: 0px;
    }

    #dialog3 {
      top: 10px;
      /* height: 100%; */
    }
  }

  .upload-box {
    padding-top: 15px;
  }
</style>



