import Vue from 'vue'
import Vuex from 'vuex'
import login from './modules/login/store_login'
import store_planificacion from './modules/planificacion/store_planificacion'
import store_verification from './modules/planificacion/store_verification'
import store_dashboard from './modules/dashboard/store_dashboard'
import store_agreement from './modules/agreement/store_agreement'
import store_annual_plan from './modules/annual_plan/store_annual_plan'
import store_notification from './modules/notification/store_notification'
import store_meeting_type from './modules/meeting_type/store_meeting_type'
import store_meeting from './modules/managment_meeting/store_meeting'


Vue.use(Vuex)
export default new Vuex.Store({
    state: {},
    getters: {},
    mutations: {},
    actions: {},
    modules: {
        login,
        store_planificacion,
        store_verification,
        store_dashboard,
        store_agreement,
        store_annual_plan,
        store_notification,
        store_meeting_type,
        store_meeting
    }
})



