<template>
  <div class="row">
    <div class="col-md-12" style="text-align: center">
      <div>
        <h1 style="color: rgb(49, 123, 183); font-size: 175px;">
          403
        </h1>
      </div>
      <div>
        <h3>No Autorizado!.</h3>
        <p>
          Usted no posee los permisos necesarios para visualizar este contenido<br/>
          <a style="color: rgb(49, 123, 183)" v-on:click='goBack'>
            Ir a inicio </a>
        </p>
      </div>
    </div>
  </div>
</template>

<style>
.default-sample .control_wrapper {
    max-width: 450px;
    min-width: 245px;
    margin: auto;
}
#defaultfileupload .e-upload.e-control {
    position: relative;
    margin: 15px 0;
}

</style>
<script>
import Vue from "vue";
import { UploaderPlugin } from '@syncfusion/ej2-vue-inputs';
import { CheckBoxPlugin } from "@syncfusion/ej2-vue-buttons";

Vue.use(UploaderPlugin);
Vue.use(CheckBoxPlugin);



export default Vue.extend({
    data: function(){
        return {
          dropElement: '.control-fluid',
        }
    },
    methods:{      
        goBack:function(args){
         this.$router.push({ path: '/Seguimiento' })
         this.$root.$children[0].$children[0].$refs.sidebarListObj.selectItem({id: '31'})
        },   
    }
});
</script>



