import axios from '../../../axios/axios-auth'

const state = {
    participantsList: [],
    urlusersLDAP: "api/users_ldap",
    urlMeeting: "meeting/meeting",
    listMeeting: [],
    listMembersByUser: []
}
const getters = {
    getUrlUsersLDAP(state) {
        return state.urlusersLDAP
    },
    getUrlMeeting(state) {
        return state.urlMeeting
    },
    getUserListLDAP(state) {
        return state.participantsList
    },
    getlistMeeting(state) {
        return state.listMeeting
    },
    getListMembersByUser(state) {
        return state.listMembersByUser
    },
}
const mutations = {
    setUserLDAPList(state, participantsList) {
        state.participantsList = participantsList
    },
    setlistMeeting(state, meetingsInstances) {
        state.listMeeting = meetingsInstances
    },
    setListlistMembersByUser(state, listMembersByUser) {
        state.listMembersByUser = listMembersByUser
    },


}
const actions = {
    userListLDAP(context) {
        return new Promise((resolve, reject) => {
            axios.get(context.getters.getUrlUsersLDAP).then(response => {
                context.commit('setUserLDAPList', response.data)
                resolve(Object.assign({}, response.data))
            }).catch(response => {
                reject(new Error('No se pudieron cargar los participantes. Error en el servior!'))
            })
        })
    },
    listMeetingByType(context, idMeetingType) {
        return new Promise((resolve, reject) => {
            var jsonBody = {
                "attr": {
                    "id_meeting_type": idMeetingType
                }
            }
            axios.post(context.getters.getUrlMeeting, jsonBody).then(response => {
                context.commit('setlistMeeting', response.data)
                resolve(Object.assign({}, response.data))
            }).catch(response => {
                reject(new Error('No se pudieron cargar las reuniones. Error en el servior!'))
            })
        })
    },
    listMembersByUser(context) {
        return new Promise((resolve, reject) => {
            var user = context.getters.user
            var membersList = []
            for (var i = 0; i < user.grupos.length; i++)
                for (var j = 0; j < user.grupos[i].Miembros.length; j++)
                    membersList.push(user.grupos[i].Miembros[j])
            context.commit('setListlistMembersByUser', membersList)
            resolve(Object.assign({}, membersList))
        }).catch(response => {
            reject(new Error('No se pudieron cargar los usuarios. Error en el servior!'))
        })

    },

}

export default {
    state,
    mutations,
    actions,
    getters
}



